<template>
  <div class="main-setting-menu">
    <HeaderMenu
      :title="$t('mainSettings.title')"
      :routeBackName="routeBackName"
    />
    <ul class="list-main-setting list-menu">
      <li class="row-in-setting item">
        <RowItem
          @click="goToLanguageSetting"
          :title="$t('mainSettings.languageName')"
          :HasIconItem="false"
          :HasIconArrow="true"
          :HasIconDisplay="false"
          :HasIconCheckbox="false"
        />
        <router-view />
      </li>
      <li class="item">
        <RowItem
          @click="goToDisplaySetting"
          :title="$t('mainSettings.displaySttingName')"
          :HasIconItem="false"
          :HasIconArrow="true"
          :HasIconDisplay="false"
          :HasIconCheckbox="false"
        />
        <router-view />
      </li>
      <li class="item">
        <RowItem
          @click="goToMareiMekomotSetting"
          :title="$t('mainSettings.Hyperlinks')"
          :HasIconItem="false"
          :HasIconArrow="true"
          :HasIconDisplay="false"
          :HasIconCheckbox="false"
        />
        <router-view />
      </li>
      <li class="item" v-hide.online>
        <RowItem
          v-disable
          @click="goToBackupSetting"
          :title="$t('mainSettings.backupSettingName')"
          :HasIconItem="false"
          :HasIconArrow="true"
          :HasIconDisplay="false"
          :HasIconCheckbox="false"
        />
        <router-view />
      </li>
      <!-- displayCheckbox -->
      <!-- <li class="item">
        <RowItem
          v-disable
          @click="setViewPlacesTo"
          :title="$t('mainSettings.viewPlacesRegularlygName')"
          :HasIconItem="false"
          :HasIconArrow="false"
          :HasIconDisplay="false"
          :HasIconCheckbox="true"
          :displayCheckbox="getviewPlaces"
          type="checkbox"
        />
      </li> -->
      <li class="row-in-setting item" v-hide.online>
        <RowItem
          @click="factoryReset"
          :title="$t('mainSettings.resetData')"
          :HasIconArrow="false"
          :HasIconDisplay="false"
          :HasIconItem="false"
          :HasIconCheckbox="false"
        >
          <!-- <BaseIcon class="" width="25" nameIcon="keyboard" pathIcon="k" /> -->
        </RowItem>
        <router-view />
      </li>
    </ul>
  </div>
</template>

<script>
import menuService from "../../../../services/menu";
import LanguageSetting from "./LanguageSetting.vue";
import { mapGetters } from "vuex";
import config from "@/config/general.js";
import { Axios } from "@/services/_axios";
import shutdown from "@/services/shutdown";
import init from "@/config/init.js";
import * as advLocalStorage from "localforage";
export default {
  components: {
    LanguageSetting,
  },
  data() {
    return {
      routeBackName: "main",
    };
  },
  computed: {
    //getviewPlaces
    ...mapGetters("userSetting", ["getviewPlaces"]),
  },
  methods: {
    goToLanguageSetting() {
      // this.$router.push({ name: "language-setting" });
      menuService.OpenLanguageMenu();
    },
    //goToDisplaySetting
    goToDisplaySetting() {
      // this.$router.push({ name: "display-setting" });
      menuService.OpenDisplayMenu();
    },
    goToMareiMekomotSetting() {
      // this.$router.push({ name: "display-setting" });
      menuService.OpenMmMenu();
    },
    goToBackupSetting() {
      // this.$router.push({ name: "backup-setting" });
      menuService.openBackup();
    },
    //setViewPlacesRegularly
    setViewPlacesTo() {
      this.userSettings.setSettings(
        "mm",
        !this.$store.state.userSetting.viewPlacesRegularly
      );
      this.$store.dispatch(
        "userSetting/setViewPlacesRegularly",
        !this.$store.state.userSetting.viewPlacesRegularly,
        {
          root: true,
        }
      );
    },
    async factoryReset() {
      if (config.serverConnection !== "offline") return;
      const url = init.getServer() + init.UsersDB.userReset;

      const ask_msg = this.$t("mainSettings.askFactoryData");
      let payload = {
        title: this.$t("mainSettings.resetData"),
        content: ask_msg,
        btns: [this.$t("general.yes"), this.$t("general.no")],
      };
      let act = await this.$msg(payload);
      if (act != this.$t("general.yes")) {
        return;
      }
      //send request to reset all user data
      try {
        await Axios.delete(url);

        //reset all localstorages

        localStorage.clear();
        await advLocalStorage.clear();

        const success_msg = this.$t("actionBook.resetSuccessful");
        payload = {
          title: "",
          content: success_msg,
          btns: [this.$t("general.confirm")],
        };
        await this.$msg(payload);
        //restart app
        await shutdown.restart();
      } catch {
        const errmsg = this.$t("actionBook.resetError");
        payload = {
          title: this.$t("general.error"),
          content: errmsg,
          btns: [this.$t("general.confirm")],
        };
        await this.$msg(payload);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.main-setting-menu {
  width: -webkit-fill-available;
  z-index: 3;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.list-main-setting {
  display: flex;
  flex-direction: column;
  // margin: 0;
  // padding: 0;
  list-style-type: none;
}
.row-in-setting {
  cursor: pointer;
}
.red {
  color: red;
}
</style>
