<script>
import menuService from "@/services/menu";
import { doHardReload } from "@/services/bookData";
import { mapGetters, mapActions } from "vuex";
import { Axios } from "@/services/_axios";
export default {
  //historyLists/Hitory
  components: {},
  data() {
    return {
      routeBackName: "menu-options",
      historyIcon: "fa fa-history",
      genderlessIcon: "fa fa-genderless",
      customPigeonsIcon: "fa fa-bullseye",
      desktopIcon: "fa fa-television",
      enableBackupIcon: "fa fa-floppy-o",
      refreshListIcon: "fa fa-refresh",
      changeBookInfoIcon: "fa fa-book",
      showFileExplore: false,
    };
  },
  computed: {
    //getviewPlaces

    ...mapGetters("userSetting", ["getviewPlaces"]),
  },
  methods: {
    ...mapActions("encyclopedia", ["setclosePopup", "setVisible"]),
    ...mapActions("createBook", ["showCreate"]),
    ...mapActions("systemKeys", ["setShowKeys", "setCloseKeys"]),
    // Encyclopedia
    /*  openEncyclopPopup() {
      this.setVisible();
      menuService.closeMenu();
    }, */
    closeMenu() {
      menuService.closeMenu();
    },
    openMainKeys() {
      this.setShowKeys();
      menuService.closeMenu();
    },
    openCreateBook() {
      this.showCreate();
      menuService.closeMenu();
      // this.$router.push({ name: "home" });
    },
    //history
    /*     goToHistory() {
      // this.$router.push({ name: "history" });
      menuService.openHistoryMenu();
    }, */
    //personal-extensions
    goToPersonalExtensions() {
      // this.$router.push({ name: "personal-extensions" });
      menuService.openPersonalExtenMenu();
    },
    goToHiddenBook() {
      menuService.openHiddenBooksMenu();
    },
    //desktop-settings
    goToDesktop() {
      // this.$router.push({ name: "desktop-settings" });
      menuService.openDesktopsMenu();
    },
    enableBackup() {
      menuService.openBackup();
    },
    refreshList() {
      doHardReload();
      menuService.closeMenu();
    },
    goToChangeBookInfo() {
      // this.$router.push({ name: "books-info-settings" });
      menuService.openBooksInfoMenu();
    },
    goToActiveStore() {
      this.$store
        .dispatch("folders/setOpenTypePopup", "activeStore")
        .then(() => {
          this.$store
            .dispatch("folders/setListType", "activeStore")
            .then(() => {
              this.$store.dispatch("folders/togglePopupFolder", true);
            });
        });
      // this.$router.push({ name: "home" });
    },
    customSort() {
      this.$store.dispatch("customSort/showCustomSort");
      menuService.closeMenu();
    },
    ffff() {},
    openCalDialog() {
      this.showFileExplore = true;
    },
    async updateCal(file) {
      this.showFileExplore = false;

      if (!file.endsWith("zip") || !file.includes("update_cal_")) {
        const notification = {
          type: "error",
          message: this.$t("mainTools.fileError"),

          timeout: 2000,
          setting: {
            grid: "icon",
            nameIcon: "icon-save-win",
            position: "center",
          },
        };
        this.$notify(notification);
      } else {
        try {
          let data = await Axios.post("api/general/calender", {
            data: file,
          });
          const notification = {
            type: "success",
            message: this.$t("mainTools.succeedUpdateBoard"),
            timeout: 2000,
          };
          this.$notify(notification);
        } catch {
          const notification = {
            type: "error",
            message: this.$t("mainTools.fileError"),
            timeout: 2000,
          };
          this.$notify(notification);
        }
      }
      menuService.closeMenu();
      // }
    },
    showImportOldVersData() {
      this.$store.dispatch("importOldVersData/showWin", true);

      menuService.closeMenu();
    },
  },
};
</script>

