import { getAppInfo } from "@/services/offline/license.js";
import errorsMgr from "@/helper/errorCodes";
import store from "../store/store";
export default function licenseCheck({ to, from, next }) {
  //check license
  if (globalThis.SERVER_MODE === "offline")
    return getAppInfo().then((res) => {
      //res = 2;
      if (typeof res === "number") {
        let routeData = {
          name: "lic_error",
          params: { err: errorsMgr.getErrorDesc()[res - 1], code: res },
        };
        store.state.fatalError = true;
        return Promise.resolve({ error: 0, next: routeData });
      } else {
        store.dispatch("offline/setLicence", res);
        return Promise.resolve({ error: 0, next });
      }
    });
  else return Promise.resolve({ error: 0, next });
}
