<template>
  <div
    class="FilterCombo no-select"
    @click="toggleCombo"
    :class="{ FilterComboFocus: showMenu }"
    v-click-outside="closeCombo"
  >
    <div class="selected-text text-col">{{ selected }}</div>
    <div class="arrow-down-col" :class="{ open: showMenu }">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="7.449"
        height="4.717"
        viewBox="0 0 7.449 4.717"
      >
        <g
          id="icon-arrow-combo"
          data-name="חץ לקומבחו"
          transform="translate(0.565 0.064)"
        >
          <path
            id="Path_23"
            data-name="Path 23"
            d="M127.4,52.591l2.654,2.885,2.675-2.907"
            transform="translate(-126.902 -51.573)"
            fill="none"
            stroke="#7b7b7b"
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-miterlimit="10"
            stroke-width="1.5"
          />
        </g>
      </svg>
    </div>
    <div
      :style="listStyle"
      style="overflow:auto"
      :class="{
        'open-menu': showMenu && !grid,
        'open-menu-grid': showMenu && grid,
        'option-items': !grid,
        'option-items-grid': grid,
      }"
      @click.stop="closeCombo"
    >
      <div
        v-for="(item, index) in items"
        :key="index"
        :class="{ 'combo-item': !grid, 'combo-item-grid': grid }"
        @click="triggerInput(item, index)"
      >
        <span :class="{ 'label-option': grid }">{{ item.label }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ComboboxInput",
  props: {
    grid: {
      default: false,
    },
    items: {},
    LabelKey: 0,
    value: {
      default: "",
    },
    labelStart: {
      default: 0,
    },
    listStyle: {
      default: "max-height:200px",
    },
  },
  data() {
    return {
      showMenu: false,
      start: this.labelStart,
    };
  },
  computed: {
    selected() {
      // if (typeof this.value[this.LabelKey] === "undefined")
      //   return this.items[this.labelStart].label;

      if (!this.items.length) return "";
      return this.items[this.start].label;
    },
  },
  watch: {
    labelStart: function() {
      this.start = this.labelStart;
    },
  },
  methods: {
    openCombo() {
      this.showMenu = true;
    },
    toggleCombo() {
      if (this.showMenu) this.showMenu = false;
      else this.showMenu = true;
    },
    closeCombo() {
      this.showMenu = false;
    },
    triggerInput(data, index) {
      this.start = index; //data.key;
      this.$emit("input", data);
    },
  },
};
</script>

<style scoped>
.ontop {
  position: relative;
  z-index: 3;
}
</style>
