<template>
  <div
    @click.stop="click"
    class="item-book row-menu"
    :class="{ selected: book.id == bookSelectedId }"
  >
    <span v-show="book.id == bookSelectedId" class="font-icon check-icon"
      >A</span
    >

    <div class="container-text-item">
      <nameBook :name="getName" />
      <nameAuthor :name="book.mainAuthorName" />
    </div>
    <div
      class="next"
      @click="$emit('change')"
      v-tooltip="{
        content: this.$t('changeBookInfo.selectBook'),
        placement: 'right',
      }"
    >
      <baseIcon
        width="9.451"
        height="7.516"
        pathIcon="icons"
        nameIcon="arrow-advanced-search-black"
      />
    </div>
  </div>
</template>

<script>
import { getBookName } from "@/services/bookData.js";
import nameBook from "@/components/listBookArea/listContentContainer/nameBook.vue";
import nameAuthor from "@/components/listBookArea/listContentContainer/nameAuthor.vue";

import { mapActions, mapGetters } from "vuex";

export default {
  props: {
    book: Object,
    isCheck: Boolean,
    bookSelectedId: Number,
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters("books", ["getBookbyId"]),
    getName() {
      return getBookName(this.book.name, this.book.volume, true);
    },
  },
  components: {
    nameBook,
    nameAuthor,
  },
  methods: {
    ...mapActions("tabsManager", ["setBook"]),
    ...mapActions("booklistComp", ["addListBooks"]),

    click() {
      this.$emit("click");
    },
  },
};
</script>

