      transform: rotate(45deg);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 9;
    }
  }
  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    margin-top: -1px;
    margin-left: 1px;
  }
}
.item-book:hover,
.item-book.selected,
