import config from "@/config/general";

export default function(Vue) {
  Vue.mixin({
    computed: {
      appMode() {
        let vm = this;
        return {
          online() {
            return globalThis.SERVER_MODE === "online";
          },
          offline() {
            return globalThis.SERVER_MODE === "offline";
          },
        };
      },
    },
  });
}
