import { loginUser } from "@/services/login.js";
import { setAuthHeader, clearAuthHeader } from "@/services/_axios.js";

import { settingsToObject } from "@/services/userSettingsData";
import { getSocket } from "@/main_app";
import VueInst from "@/main_app.js";
import onlineStationsHelper from "@/services/onlineStations";

export const namespaced = true;
export const state = {
  userId: 0,
  clientId: 0,
  stickerId: 0,
  username: "",
  fullname: "",
  permissions: [],
  token: "",
  userType: "",
  settings: {},
  showLogin: false,
  licenseInfo: [],
};
export const mutations = {
  SET_USER_DATA(state, { userData, token, settings }) {
    let protectionCode = 0;
    if (userData.permissions) {
      userData.permissions.forEach((p) => {
        protectionCode = protectionCode | p.protectionCode;
      });
    }
    if (userData.error) {
      VueInst.$notify({
        type: "error",
        message: "כל העמדות תפוסות. נסה שוב מאוחר יותר", // VueInst.$t("hiddenbooks.nohiddenbooks"),
        timeout: 6000,
      });
    }
    //start online timer
    if (
      VueInst.appMode.online() &&
      !userData.error &&
      state.userType !== "GUEST"
    )
      onlineStationsHelper.startOnlineTimer();

    const socket = getSocket();
    console.log("set user socket");

    socket.emit("setUserSocket", token);

    state.token = token;
    state.userId = userData.id;
    state.clientId = userData.clientId;
    state.stickerId = userData.stickerId;
    state.username = userData.username;
    state.fullname = userData.firstName + " " + userData.lastName;
    state.permissions = protectionCode;
    state.userType = userData.userType || "";
    if (settings) state.settings = settingsToObject(settings);
  },
  SET_USER_SETTINGS(state, settings) {
    state.settings = settings;
  },
  CLEAR_USER_DATA(state) {
    state.clientId = 0;
    state.username = "";
    state.permissions = [];
    state.token = "";
    state.stickerId = 0;
    state.settings = {};
    state.userType = "";
  },
};
export const actions = {
  setUser({ dispatch }, data) {
    dispatch("setUserData", data);
    localStorage.setItem("user", JSON.stringify(data));
    setAuthHeader(data);
    return Promise.resolve();
  },
  setUserData({ commit, state, dispatch }, { userData, token }) {
    commit("SET_USER_DATA", { userData, token });

    //if license is chabad set activestore to chabad
    if (state.permissions === 8) {
      dispatch("folders/setChabadActiveStore", null, { root: true });
    }
  },
  setUserSettings({ commit }, data) {
    commit("SET_USER_SETTINGS", data);
  },
  login({ commit, dispatch }, user) {
    return loginUser(user, false)
      .then(({ data }) => {
        dispatch("setUserData", data);
        localStorage.setItem("user", JSON.stringify(data));
        setAuthHeader(data);
        return Promise.resolve(data);
      })
      .catch((error) => {
        //console.error(error);
        return Promise.reject(error);
      });
  },
  loginFreeUser({ commit, dispatch }) {
    return loginUser({ username: "", password: "" }, true)
      .then(({ data }) => {
        dispatch("setUserData", data);
        localStorage.setItem("user", JSON.stringify(data));
        setAuthHeader(data);
        return Promise.resolve();
      })
      .catch((error) => {
        console.error(error);
        return Promise.reject(error);
      });
  },
  logout({ dispatch }, data) {
    dispatch("setUserData", data);
    localStorage.setItem("user", JSON.stringify(data));
    setAuthHeader(data);
    return Promise.resolve();
  },
};
export const getters = {
  getUserName(state) {
    return state.fullname;
  },
  getUserId(state) {
    return state.userId;
  },
  getUserToken(state) {
    return state.token;
  },
};
