<template>
  <div
    v-cdrag.parent
    v-cdrag.self
    :style="{ width: width + 2 + 'px', height: height + 2 + 'px' }"
    class="magnify-container"
  >
    <canvas ref="image" :width="width" :height="height" />
  </div>
</template>
<script>
import { getImageObject } from "@/services/bookData.js";
export default {
  props: {
    magRatio: {
      default: 2,
    },
    imageRatio: {
      default: 1.0,
    },
    width: {
      default: 200,
    },
    height: {
      default: 200,
    },
    bookId: {
      default: 0,
    },
    page: { default: 0 },
    magnifyPosition: { x: 0, y: 0 },
    marginLeft: { default: 0 },
  },
  data() {
    return {
      fullSizeImg: null,
    };
  },
  watch: {
    magnifyPosition(val) {
      //calc image part to magnify

      if (!this.fullSizeImg) return;
      this.drawMagnify();
    },
    page(val) {
      this.drawMagnify();
    },
  },
  computed: {
    partWidth() {
      return this.width / parseFloat(this.magRatio);
    },
    partHeight() {
      return this.height / parseFloat(this.magRatio);
    },
    lMargin() {
      return (this.width - this.partWidth) / 2;
    },
    tMargin() {
      return (this.height - this.partHeight) / 2;
    },
  },
  methods: {
    async drawMagnify() {
      if (!this.$refs.image) return;
      if (this.bookId > 0 && this.page !== 0) {
        //get image in origin size
        this.fullSizeImg = await getImageObject(this.bookId, this.page);
        if (!this.fullSizeImg) return;
        const ctx = this.$refs.image.getContext("2d");

        const rat = parseFloat(this.imageRatio);
        ctx.drawImage(
          this.fullSizeImg.picture,
          (this.magnifyPosition.x +
            this.lMargin -
            parseFloat(this.marginLeft) -
            10) *
            rat,
          (this.magnifyPosition.y + this.tMargin - 20) * rat,
          this.partWidth * rat,
          this.partHeight * rat,
          0,
          0,
          this.width,
          this.height
        );
      }
    },
  },
  async mounted() {
    this.drawMagnify();
  },
  destroyed() {
    this.fullSizeImg = null;
  },
};
</script>

<style lang="scss" scoped>
.magnify-container {
  position: absolute;
  top: 40px;
  left: 10px;
  z-index: 9999;
  cursor: zoom-in;
  border: black 1px solid;
}
</style>
