<script>
import { printResultsList, printPreview } from "@/services/bookLists.js";
import iconRefreshList from "@/components/listBookArea/headerBookListContainer/iconsUpdateList/iconRefreshList.vue";
import iconsUpdateContainer from "@/components/listBookArea/headerBookListContainer/iconsUpdateList/iconsUpdateListContainer.vue";
import titleList from "@/components/listBookArea/headerBookListContainer/titleList.vue";
import iconsFolderContainer from "@/components/listBookArea/headerBookListContainer/iconFolderList/iconFolderListContainer.vue";

// import actionsItemContainer from "@/components/listBookArea/headerBookListContainer/iconsUpdateList/actionsCheckbookContainer.vue";
import * as history from "@/services/history.js";
import { treeBooks } from "@/store/modules/books";
import { mapActions, mapGetters, mapState } from "vuex";

export default {
  props: {
    showRefresh: {
      default: true,
    },
  },
  components: {
    // actionsItemContainer,
    iconsUpdateContainer,
    titleList,
    iconRefreshList,
    iconsFolderContainer,
  },
  sockets: {
    connect: function() {},
    createPdf: function(res) {
      this.$store.state.progressWindow.show = false;
      this.$store.state.progressWindow.bottomMessage = "";
      printPreview(res);
    },
  },
  data() {
    return {
      labelKey: 0,
      inSearch: false,
      labelStart: 0,
      action: true,
    };
  },
  computed: {
    ...mapGetters("bookList", [
      "getCheckedBooks",
      "getBookListHistory",
      "getCurrentListType",
      "getFavoriteCheckBooksCount",
    ]),
    // getDataOfCurrentList
    ...mapGetters("booklistComp", [
      "hasCheckAll",
      "hasCheckedTop",
      "getLengthListCheck",
    ]),
    ...mapState("bookList", ["historyUsersActions", "textSearch", "pinChecks"]),
    ...mapState("userFavoriteBooks", ["favoriteChange"]),

    ...mapGetters("bookList", [
      "getTextSearchInput",
      "getBooksCount",
      "getChecksCount",
      "getDoNotFocus",
    ]),
    systemMaagarim() {
      return (
        this.currentListData.type == "SYSTEM_LIST" &&
        this.currentListData.categoryTypeId === 4
      );
    },
    showSettings() {
      return this.currentListData.type !== "RECENT_BOOKS";
    },
    showSdarotOption() {
      return this.systemMaagarim || this.isInSearch || this.allBooks;
    },
    grayPin() {
      return this.currentListData.pin != 1 || this.isInSearch;
    },
    enableHide() {
      return this.userSettings.settings.enableHidden != "0";
    },
    label() {
      return this.$t("freeSearchResults.sortBy");
    },

    getFavoritesTooltip() {
      let checked = this.getFavoriteCheckBooksCount;
      return checked == 1
        ? this.$t("toolbox.removefavorites")
        : this.$t("toolbox.favorites");
    },
    getFavoritesIcon() {
      let checked = this.getFavoriteCheckBooksCount;
      return checked == 1 ? "0" : "P";
    },
    hidePin() {
      return (
        this.currentListData.type == "ALL" && this.getTextSearchInput == ""
      );
      //    (this.currentListData.type != "SEARCH" && this.getTextSearchInput != "")
      // );
    },
    allBooks() {
      if (this.currentListData.type == "ALL" && this.textSearch == "") {
        return true;
      }
      return false;
    },
    catgList() {
      return [
        { label: this.$t("changeBookInfo.bookName"), key: 0, name: "book" },
        { label: this.$t("changeBookInfo.authorName"), key: 1, name: "author" },
      ];
    },
    checkMinus() {
      let booksCount = this.getBooksCount;
      let checksCount = this.getChecksCount;
      return checksCount > 0 && checksCount < booksCount;
      //if (Length != this.getLengthListCheckFree) return true;
      //return false;
    },
    showActions() {
      return this.getChecksCount > 0;
    },
    nameSearch() {
      if (this.$store.state.lastTxtSearch === undefined) {
        // console.dir(this.$store.state.lastTxtSearch);
        return this.$t("searchIn.allBooks");
      }
      return $store.state.lastTxtSearch;
    },
    showOrder() {
      if (this.getTextSearchInput && this.getTextSearchInput.length > 0)
        return false;
      if (
        this.currentListData.type == "ALL" ||
        this.currentListData.type == "SYSTEM_LIST" ||
        this.currentListData.type == "CUSTOM_LIST" ||
        this.currentListData.type == "HIDDEN_LIST" ||
        this.currentListData.type == "SAVED_LIST" ||
        this.currentListData.type == "RECENT_BOOKS" ||
        this.currentListData.type == "SYSTEM_MULTIPLE_LISTS"
      )
        return true;
      return false;
    },
    showSearch() {
      if (this.getTextSearchInput && this.getTextSearchInput.length > 0)
        return false;
      if (
        [
          "RECENT_BOOKS",
          "SYSTEM_LIST",
          "CUSTOM_LIST",
          "SAVED_LIST",
          "SYSTEM_MULTIPLE_LISTS",
        ].includes(this.currentListData.type)
      )
        return true;
      return false;
    },

    pinToolTip() {
      return this.currentListData.pin != 1 || this.isInSearch
        ? this.$t("tooltip.pin")
        : this.$t("tooltip.unpin");
    },
    currentListData() {
      let currentList = this.$store.getters["bookList/getDataOfCurrentList"]
        .data;
      return currentList;
    },
    openSdarot() {
      if (!this.userSettings.settings.open_sdarot) return false;
      if (this.userSettings.settings.open_sdarot == "1") return true;
      return false;
    },
    sdarotBookMode() {
      return this.userSettings.settings.sdarot_mode === "book";
      if (!this.userSettings.settings.mouse_wheel) return false;
      if (this.userSettings.settings.mouse_wheel == "scroll") return true;
      return false;
    },
    mouseWheelScroll() {
      return this.userSettings.settings.mouse_wheel === "scroll";
      if (!this.userSettings.settings.mouse_wheel) return false;
      if (this.userSettings.settings.mouse_wheel == "scroll") return true;
      return false;
    },
    inSearchInput() {
      if (
        (this.currentListData.type == "ALL" &&
          this.getTextSearchInput.trim() == "") ||
        this.currentListData.type == "SYSTEM_LIST" ||
        this.currentListData.type == "SAVED_LIST" ||
        this.currentListData.type == "SYSTEM_MULTIPLE_LISTS" ||
        this.currentListData.type == "RECENT_BOOKS" ||
        this.currentListData.type == "CUSTOM_LIST"
      ) {
        return false;
      }
      return true;
    },
    showBookVolumes() {
      let listType = this.getCurrentListType;
      return (
        ["SEARCH", "ALL", "AUTHOR_LIST"].includes(listType) ||
        this.yesodBook ||
        this.systemMaagarim
      );
    },
    yesodBook() {
      return (
        this.currentListData.type == "CUSTOM_LIST" &&
        this.currentListData.content == "ספרי יסוד"
      );
    },
    systemMaagarim() {
      return (
        this.currentListData.type == "SYSTEM_LIST" &&
        this.currentListData.categoryTypeId === 4
      );
    },
    hiddenBooksList() {
      return this.currentListData.type === "HIDDEN_LIST";
    },
    isInSearch() {
      return (
        (this.currentListData.type !== "SEARCH" &&
          this.getTextSearchInput !== "") ||
        (this.currentListData.type === "SEARCH" &&
          this.getTextSearchInput !== this.currentListData.content)
      );
    },
  },

  methods: {
    ...mapActions("userFavoriteBooks", [
      "addFavoriteBook",
      "removeFavoriteBook",
    ]),
    ...mapActions("bookList", [
      "removeBooks",
      "saveFsList",
      "checkAllBooks",
      "revertChecks",
      "goToHistory",
      "setDoNotFocus",
    ]),
    ...mapActions("booklistComp", ["checkAllListBooks"]),
    openHistory(item) {
      this.goToHistory({ id: item.index });
      // this.clicked();
      // this.$store.state.bookList.initBook = 143448;
    },
    doExportList(type) {
      printResultsList(type, !this.showBookVolumes, "book");
    },

    setSettingOpenSdarot(val) {
      this.userSettings.setSettings("open_sdarot", val);
      this.$store.dispatch("bookList/toggleShowVols", { root: true });
    },
    setSettingMouseWheel(val) {
      this.userSettings.setSettings("mouse_wheel", val);
    },
    setSettingSdarotMode(val) {
      this.userSettings.setSettings("sdarot_mode", val);
    },
    pinCurrentList(pin) {
      //if it is a search list that is not yet in history
      if (this.isInSearch) {
        let payload = {
          type: "SEARCH",
          content: "",
          pin: true,
        };
        this.$store.dispatch("bookList/addUserActionToHistory", payload);
        return;
      }
      if (pin == undefined) pin = this.currentListData.pin == 1 ? false : true;
      if (
        pin &&
        this.getBookListHistory.filter((i) => i.pin == 1).length >= 5
      ) {
        let msg = this.$t("listBooks.dontPinMoreFiveList");
        const notification = {
          type: "error",
          message: msg,
          timeout: 2000,
        };
        this.$notify(notification);
        return;
      }
      let currentList = this.currentListData;
      let id = currentList.historyId ? currentList.historyId : currentList.id;
      if (id == undefined) return;

      history.setSearchHistoryPin({ id, pin }).then(() => {
        this.$store.dispatch("bookList/setPin", { id, pin }, { root: true });
      });
    },
    pinList(id, pin = true) {
      if (
        pin &&
        this.getBookListHistory.filter((i) => i.pin == 1).length >= 5
      ) {
        let msg = this.$t("listBooks.dontPinMoreFiveList");
        const notification = {
          type: "error",
          message: msg,
          timeout: 2000,
        };
        this.$notify(notification);
        return;
      }
      history.setSearchHistoryPin({ id, pin }).then(() => {
        this.$store.dispatch("bookList/setPin", { id, pin }, { root: true });
      });
    },
    async removeHiddenBooks() {
      let books = this.getCheckedBooks;
      let msg;
      msg =
        books.length == 1
          ? this.$t("hiddenbooks.sureCancelHiddenBooks")
          : this.$t("hiddenbooks.sureCancelHiddenBook");
      let payload = {
        title: this.$t("general.remove2"),
        content: msg,
        btns: [this.$t("general.yes"), this.$t("general.no")],
      };
      let act = await this.$msg(payload);
      if (act != this.$t("general.yes")) {
        return;
      }

      let alreadyHidden = this.userSettings.settings.hiddenBooks
        ? this.userSettings.settings.hiddenBooks
        : [];
      if (typeof alreadyHidden == "string")
        alreadyHidden = JSON.parse(alreadyHidden);
      //remove selected books
      let hiddenBooks = alreadyHidden.filter((b) => !books.includes(b));
      try {
        await this.userSettings.setSettings(
          "hiddenBooks",
          JSON.stringify(hiddenBooks)
        );

        this.$store.dispatch("books/unsetHiddenBooks", books);

        msg =
          books.length == 1
            ? this.$t("hiddenbooks.succeedCancel")
            : this.$t("hiddenbooks.succeedCancel");
        const notification = {
          type: "success",
          message: msg,
          timeout: 2000,
        };
        this.$notify(notification);
      } catch {
        const notification = {
          type: "error",
          message: "error occured",
          timeout: 2000,
        };
        this.$notify(notification);
      }
    },
    async addToHiddenBooks() {
      let payload = {
        title: this.$t("general.remove2"),
        content: this.$t("hideBookQuestion"),
        btns: [this.$t("general.yes"), this.$t("general.no")],
      };
      let act = await this.$msg(payload);
      if (act != this.$t("general.yes")) {
        return;
      }
      let books = this.getCheckedBooks;
      let alreadyHidden = this.userSettings.settings.hiddenBooks
        ? this.userSettings.settings.hiddenBooks
        : [];
      if (typeof alreadyHidden == "string")
        alreadyHidden = JSON.parse(alreadyHidden);
      //remove dups
      let hiddenBooks = new Set([...books, ...alreadyHidden]);
      hiddenBooks = Array.from(hiddenBooks);
      try {
        await this.userSettings.setSettings(
          "hiddenBooks",
          JSON.stringify(hiddenBooks)
        );

        this.$store.dispatch("books/setHiddenBooks", hiddenBooks);
        const notification = {
          type: "success",
          message: this.$t("hideBookSuccess"),

          timeout: 2000,
        };
        this.$notify(notification);
      } catch {
        const notification = {
          type: "error",
          message: "error occured",
          timeout: 2000,
        };
        this.$notify(notification);
      }
    },
    addQuickBtn() {
      this.$store.dispatch("popupOnceComp/setSlotShow", "add-quick-books");
      this.$store.dispatch("popupOnceComp/toggleVisible");
    },
    async doRemoveBooks() {
      let content = this.$t("selectBook.removeBookQ");
      if (this.getChecksCount > 1) content = this.$t("selectBook.removeBooksQ");
      let payload = {
        title: this.$t("selectBook.deleteBookInList"),
        content: content,

        btns: [this.$t("general.yes"), this.$t("general.no")],
      };
      let act = await this.$msg(payload);
      if (act == this.$t("general.no") || act == "") return;
      if (
        this.currentListData.type == "SAVED_LIST" ||
        this.currentListData.type == "RECENT_BOOKS"
      ) {
        let folders = {
          id: this.currentListData.listId || this.currentListData.id,
          books: this.getCheckedBooks,
        };
        this.$store.dispatch("folders/userFolderListDeleteBooks", folders);
        if (
          this.currentListData.content === "favorites" ||
          this.currentListData.content === "מועדפים"
        ) {
          //remove from favorites books module
          this.$store.dispatch("userFavoriteBooks/delFavoriteBooks", {
            books: this.getCheckedBooks,
          });
        }
        if (this.currentListData.type == "RECENT_BOOKS") {
          //remove from recent books module
          this.$store.dispatch(
            "userRecentBooks/removeRecentBooks",
            this.getCheckedBooks
          );
        }
      }
      content = this.$t("selectBook.removeSelectBook");
      const notification = {
        type: "success",
        message: content,
        timeout: 2000,
        setting: {
          grid: "progress",
          nameIcon: "icon-garbage-color",

          position: "center",
        },
      };
      this.$notify(notification);
      this.$store.dispatch("quickButtons/setQuickButtonsData", null, {
        root: true,
      });
    },
    revert() {
      this.revertChecks();
    },
    closeAction() {
      this.action = false;
    },
    toggleAction() {
      this.action = !this.action;
    },
    txtSearchIn(e) {
      // setBooksListSearchTo

      this.$store.dispatch("bookList/setDoNotFocus", true);
      this.$store.dispatch("bookList/setBooksListSearchTo", e.target.value);
    },
    checkAll($event, check) {
      if (check == undefined) check = $event.target.checked;
      this.$emit("checkall", check);
    },
    searchInList() {
      if (!this.inSearch) {
        this.inSearch = true;
        setTimeout(() => {
          this.$refs.inputSearch.focus();
        }, 10);
      } else {
        this.inSearch = false;
        this.$refs.inputSearch.value = "";

        this.$store.dispatch("bookList/setBooksListSearchTo", "");
      }
      return;
    },

    // filterByCatg($event) {
    //   this.label = $event.label;
    //   this.labelKey = $event.labelKey;
    //   this.userSettings.setSettings("sortBookList", $event.name);
    //   this.$store.dispatch("booklistComp/setSortByList", $event.name);
    // },
    filterByCatg(sort) {
      this.userSettings.setSettings("sortBookList", sort);
      this.$store.dispatch("booklistComp/setSortByList", sort);
    },
    saveSelect() {
      this.$store.dispatch("folders/setOpenTypePopup", "export").then(() => {
        this.$store.dispatch("folders/setListType", "book").then(() => {
          this.$store.dispatch("folders/togglePopupFolder", true);
        });
      });
    },
    addListFavorites() {
      let books = this.getCheckedBooks;
      this.getFavoriteCheckBooksCount == 1
        ? this.removeFavoriteBook({ books })
        : this.addFavoriteBook({ books });
    },
  },
  watch: {
    hasCheckedTop: function(val) {
      if (!val) {
        this.action = true;
      }
    },
  },
};
</script>

<style lang="scss">
.inner-list {
  max-height: 250px;
  overflow: auto;
}
.icons-check {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  // margin-left: 7px;
  height: 100%;
  align-items: center;
}
.icons-header {
  max-width: calc(100% - 42px);
  &.show-more {
    max-width: calc(100% - 118px);
  }
}
.icons-header-left {
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  margin-right: 7px;
  padding-right: 5px;
}
.action-list-inner .check-larg input:checked + .checkmark {
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.29);
  border: 1.5px solid #7b7b7b;
}
.action-list-inner {
  .arrow-combo-bottom {
    height: 23px;
    width: 14px;
    position: relative;
    right: -4px;
    transition: 0.3s;
    border-radius: 3px;
    &:hover {
      background: #00000014;
    }
    svg {
      position: absolute;
      top: 51%;
      left: 51%;
      transform: translateX(-50%) translateY(-50%);
    }
  }
}
.icon-pin {
  font-size: 28px;
}
.list-content {
  .underline-separate {
    margin: 6px 0 12px;
  }
  .title {
    color: #7f7f7f;
    font-size: 13px;
    font-weight: bold;
    padding: 0px 20px 0 0;
  }
  .item.checked {
    color: var(--custom-color1);
    height: 36px;
  }
  .item.item-check {
    font-size: 14px;
    height: 36px !important;
    line-height: 36px !important;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }

  .item.item-check .row-menu {
    padding: 0 30px 0 20px;
  }
  .item.item-check .font-icon {
    margin-right: -24px;
    right: 13px;
    margin-left: 10px;
    height: 36px;
    line-height: 36px;
    color: var(--custom-color1);
    font-size: 14px;
  }
}
.pinned .item:last-child {
  border-bottom: 1px solid #e4e4e4;
}
.div-input-search {
  position: absolute;
  top: 0;
  width: calc(100% - 50px);
  left: 2px;
  background-color: var(--bg-color3);
  text-align: right;
  height: 100%;
  border: 0;
  border-bottom: 1px solid;
  direction: rtl;
  color: var(--text-color1);
}

.favorites-icon {
  font-size: 28px;
}
.close-search {
  position: absolute;
  z-index: 1;
  left: 2px;
  top: 0;
}

.icon-pin-gray {
  color: rgba(0, 0, 0, 0.3) !important;
}
</style>
