<template>
  <div
    @dblclick.exact="openInWinOrMax"
    @click.ctrl="openBookInRealWin"
    tabindex="0"
    ref="container"
    class="container"
    @keydown="doKeyPress"
    @keyup="doKeyUp"
    :id="'the-book-container' + tabId"
    @contextmenu="setCurrentPage"
  >
    <BaseContextMenu>
      <div class="menu-item" @click="print">
        {{ $t("bookPrint.btnTxt") }}
      </div>
      <div class="menu-item" @click="startPrintFrom = currentClickedPage">
        {{ $t("bookPrint.startFromHere") }}
      </div>
      <div
        class="menu-item"
        :class="{ disable: !showEnd }"
        @click="printToHere"
      >
        {{ $t("bookPrint.endHere") }}
      </div>
      <div class="menu-item" @click="setOcr">
        {{ $t("toolbox.ocr") }}
      </div>
      <div class="menu-item" @click="addextens('comment')" v-kiosk>
        {{ $t("general.add") }} {{ $t("comments.comment") }}
      </div>
      <div class="menu-item" @click="addextens('key')" v-kiosk>
        {{ $t("general.add") }} {{ $t("keys.key") }}
      </div>
      <div class="menu-item" @click="addNewTziun" v-kiosk>
        {{ $t("tsiyunim.addTsiyunHeader") }}
      </div>
      <div class="menu-item" @click="openBookInWin">
        {{ $t("toolbox.openBook") }}
      </div>
    </BaseContextMenu>
    <pagesScroller
      @addexten="$emit('addexten')"
      v-if="list.length > 0"
      :items="list"
      :tabId="tabId"
      :raw="imageContent"
      scrollToPosition="0"
      :refresh="renderPage"
      :currScroll="currScroll"
      :scrollToItem="scrollToItem"
      :offset="offset"
      @toggleocr="toggleOCR"
      @inc-page="incPage"
      :mm="mm"
      :drawFs="drawFs"
      :isscrolling="scrolling"
      @login="$emit('login')"
    ></pagesScroller>
    <!--     <magnifyImg
      @dragging="setMagnifyData"
      v-if="showMagnify"
      :bookId="book"
      :page="magnifyPage"
      :magnifyPosition="magnifyPosition"
      :refresh="refresh"
    /> -->
  </div>
</template>

<script>
import pageItem from "@/components/book/pageItem.vue";
import pagesScroller from "@/components/book/pagesScroller.vue";
import { goToPage as go, getImageObject } from "@/services/bookData.js";
import { startPrint } from "@/services/print.js";

import { mapActions, mapGetters } from "vuex";
import winManager from "@/services/book_win";
import magnifyMix from "@/mixing/magnifier";
import magnifyImg from "./magnifyImg.vue";

export default {
  components: {
    pageItem,
    pagesScroller,
    //  magnifyImg,
  },
  mixins: [magnifyMix],
  props: {
    list: Array,
    tabId: String,
    drawData: Object,
    mm: Boolean,
    drawFs: Boolean,
  },
  data() {
    return {
      thepage: 0,
      imageContent: null,
      searchTimer: null,
      lastKey: 0,
      scrollToItem: 0,
      offset: 0,
      renderPage: false,
      scrolling: false,
      currScroll: 0,
      startPrintFrom: -1,
      endPrint: -1,
      currentClickedPage: -1,
    };
  },
  watch: {
    resize: function(val, oldVal) {
      this.renderPage = !this.renderPage;
    },
    list: function(val, oldVal) {
      // this.doResize();
      // this.scrollToPage(this.scrollTo);
    },
    fullScreen: function(val, oldVal) {
      this.doResize(this.$store.state.tabsManager.currScroll);
    },
    scrollTo: function(page, oldVal) {
      this.scrollToPage(page);
    },
    splitterChange: function(val, oldVal) {
      this.doResize();
    },
    book: async function(val, oldVal) {
      this.imageContent = null;
    },
  },
  computed: {
    ...mapGetters("tabsManager", ["isWindow"]),
    showEnd() {
      return this.startPrintFrom != -1;
    },
    getTabData() {
      return this.$store.getters["tabs/" + this.tabId + "/getTabData"];
    },
    ...mapGetters("tabsManager", ["getSearchPage"]),
    pageHeight() {
      return this.$store.state.tabs[this.tabId].pageHeight;
    },
    getTsiyunim() {
      return this.getTabData.tsiyunim || [];
      return this.$store.getters[`tabs/${this.tabId}/getTsiyunim`];
    },
    tsiyunimType1() {
      try {
        return this.getTsiyunim.filter((t) => t.type == 1);
      } catch (ex) {
        return [];
      }
    },
    tsiyunimType2() {
      try {
        return this.getTsiyunim.filter((t) => t.type == 2);
      } catch (ex) {
        return [];
      }
    },
    tsiyunimSorted() {
      return [...this.tsiyunimType2, ...this.tsiyunimType1];
    },
    getUserTsiyuns() {
      return this.getTabData.usertsiyuns || [];

      return this.$store.getters[`tabs/${this.tabId}/getUserTsiyuns`];
    },
    getPages() {
      //return this.$store.getters[`tabs/${this.tabId}/getPages`];
      return this.getTabData.pages || [];
    },
    book() {
      return this.getTabData.book;
    },
    getBookFit() {
      return this.$store.getters[`tabs/${this.tabId}/getBookFit`];
    },
    resize() {
      return this.$store.state.tabs[this.tabId].resize;
    },
    page() {
      if (this.tabId == "") return 0;
      let page = this.$store.state.tabs[this.tabId];

      if (page != undefined) page = page.page;
      else page = 1;
      return page;
    },
    scrollTo() {
      return this.$store.state.tabs[this.tabId].scrollTo;
    },
    fullScreen() {
      return this.$store.state["tabsManager"].fullScreen;
    },
    splitterChange() {
      return this.$store.state["tabsManager"].splitterChange;
    },
  },
  methods: {
    ...mapActions("userRecentBooks", ["addRecentBook"]),
    ...mapActions("tabsManager", [
      "setPageHeight",
      "setCurrentScrollTo",
      "setResizeToggle",
      "initCropper",
      "setSearchPage",
      "addToSearchPage",
    ]),
    ...mapActions("bookPopups", ["setOCREnable"]),
    ...mapActions("popupOnceComp", [
      "toggleVisible",
      "setSlotShow",
      "setTabId",
    ]),
    ...mapActions("bookPersonalExtensions", [
      "setAddEtensType",
      "closeExtens",
      "startCreateExtentionMode",
    ]),
    setCurrentPage(e) {
      try {
        this.currentClickedPage = parseInt(e.target.getAttribute("page"));
      } catch {
        this.currentClickedPage0;
      }
    },
    printToHere() {
      if (!this.showEnd) return;
      if (startPrint(this.tabId))
        this.$emit("print", {
          show: true,
          from: this.startPrintFrom,
          to: this.currentClickedPage,
        });
      this.startPrintFrom = -1;
    },
    zoom(inc) {
      let currZoom = this.$store.getters["tabs/" + this.tabId + "/getZoom"];
      if (inc) currZoom += 10;
      else currZoom -= 10;

      if (currZoom < 40) currZoom = 40;
      if (currZoom > 250) currZoom = 250;
      this.$store
        .dispatch(`tabs/${this.tabId}/setBookFit`, "width", {
          root: true,
        })
        .then(() => {
          this.$store.dispatch(`tabs/${this.tabId}/setZoom`, currZoom, {
            root: true,
          });
          setTimeout(this.triggerResize(), 0);
        });
    },
    triggerResize() {
      this.$store.dispatch(`tabs/${this.tabId}/triggerResize`, null, {
        root: true,
      });
    },
    doKeyUp(event) {
      let numlock = event.getModifierState("NumLock");
      switch (event.code) {
        case "ShiftLeft":
        case "ShiftRight":
          this.$store.state.shiftPress = false;
          break;
        case "ArrowUp":
        case "PageUp":
        case "Numpad8":
        case "Numpad9":
          if ((event.code == "Numpad8" || event.code == "Numpad9") && numlock)
            break;

          clearInterval(globalThis.scrollInterval);
          clearInterval(globalThis.clickBookInterval);
          globalThis.scrollInterval = null;
          globalThis.clickBookInterval = null;

          break;
        case "ArrowDown":
        case "PageDown":
        case "Numpad3":
        case "Numpad2":
          if ((event.code == "Numpad2" || event.code == "Numpad3") && numlock)
            break;

          clearInterval(globalThis.scrollInterval);
          clearInterval(globalThis.clickBookInterval);

          globalThis.scrollInterval = null;
          globalThis.clickBookInterval = null;

          break;
      }
    },
    doKeyPress(event) {
      const NumLockEnabled = event.getModifierState
        ? event.getModifierState("NumLock")
        : false;

      switch (event.code) {
        case "Equal":
          event.stopPropagation();
          event.preventDefault();
          if (!event.shiftKey) break;
          this.zoom(true);
          break;
        case "NumpadAdd":
          event.stopPropagation();
          event.preventDefault();
          this.zoom(true);
          break;
        case "Minus":
          event.stopPropagation();
          event.preventDefault();
          if (!event.shiftKey) break;
          this.zoom(false);
          break;
        case "NumpadSubtract":
          event.stopPropagation();
          event.preventDefault();
          this.zoom(false);
          break;
        case "KeyP":
          if (event.ctrlKey) {
            if (startPrint(this.tabId)) this.$emit("print", { show: true });
            this.$nextTick(() => {
              document.getElementById("printToPrinterBtn").click();
            });
          } else this.searchPage(event);

          break;
        /*  case "KeyN":
          // if (event.ctrlKey) {
          this.toggleMagnifier();
          //   }

          break; */

        case "Space":
          this.searchPage(event);
          event.stopPropagation();
          event.preventDefault();
          break;
        case "Numpad4":
        case "ArrowLeft":
          if (event.code == "Numpad4" && NumLockEnabled) {
            this.searchPage(event);
            break;
          }
          this.incPage(1);
          break;
        case "Numpad6":
        case "ArrowRight":
          if (event.code == "Numpad6" && NumLockEnabled) {
            this.searchPage(event);
            break;
          }
          this.incPage(-1);
          break;
        case "PageUp":
        case "Numpad9":
          if (event.code == "Numpad9" && NumLockEnabled) {
            this.searchPage(event);
            break;
          }
          // event.stopPropagation();
          // event.preventDefault();
          // this.incPage(-10);
          break;
        case "PageDown":
        case "Numpad3":
          if (event.code == "Numpad3" && NumLockEnabled) {
            this.searchPage(event);
            break;
          }
          // event.stopPropagation();
          // event.preventDefault();
          // this.incPage(10);
          break;
        default:
          this.searchPage(event);
      }
    },
    openBookInRealWin() {
      const bookId = this.getTabData.book;
      const page = this.getTabData.page;
      const w = 100;
      const h = 100;
      const fs = this.book?.fs;
      const fsName = fs ? fs.fs : 0;
      const end = fs ? fs.end : 0;
      const start = fs ? fs.start : 0;
      const tabId = Math.floor(Math.random() * 1000000);
      const params = ["height=" + screen.height, "width=" + screen.width].join(
        ","
      );
      window.open(
        globalThis.APP_URL +
          `#/book/${bookId}/p/${page}/t/${tabId}/fs/${fsName}/start/${start}/end/${end}/c`,
        "_blank" + tabId,
        params
      );
    },
    openBookInWin() {
      winManager.createWinBook(
        this.getTabData.book,
        this.getTabData.page,
        null,
        null,
        null,
        null,
        this.getTabData.drawData?.fs
      );
    },
    openInWinOrMax() {
      if (!this.isWindow(this.tabId)) this.openBookInWin();
    },
    toggleOCR(show) {
      this.$emit("toggleocr", show);
    },
    searchPage(event) {
      //TODO search only letters and numbers
      this.lastKey = new Date().getTime();
      this.addToSearchPage(event.key);
      clearTimeout(this.searchTimer);
      this.searchTimer = setTimeout(this.doPageSearch, 1000);
    },
    doPageSearch() {
      try {
        let found;
        let searchtxt = this.getSearchPage;
        this.setSearchPage("");
        //this.resetPageSearch();
        //check if it a valid page number
        if (
          !isNaN(searchtxt) &
          (parseInt(searchtxt) <= this.getPages.length) &
          (parseInt(searchtxt) > 0)
        )
          this.$store.state.tabs[this.tabId].scrollTo = parseInt(searchtxt);
        //search in page names
        else {
          found = this.getPages.filter((page, i) =>
            page.pagedata[0].letter.includes(searchtxt)
          );

          if (found.length > 0)
            this.$store.state.tabs[this.tabId].scrollTo = found[0].position;
          //search in tsiyunim
          else {
            found = this.tsiyunimSorted.filter((tsiyun, i) =>
              tsiyun.title.includes(searchtxt)
            );

            if (found.length > 0)
              this.$store.state.tabs[this.tabId].scrollTo = found[0].page;
            //   search in user tsiyunim
            else {
              found = this.getUserTsiyuns.filter((tsiyun, i) =>
                tsiyun.title.includes(searchtxt)
              );

              if (found.length > 0)
                this.$store.state.tabs[this.tabId].scrollTo = found[0].page;
            }
          }
        }

        // this.searchTimer = setTimeout(this.resetPageSearch, 1);
      } catch (ex) {
        //console.error(ex);
      }
    },
    resetPageSearch() {
      if (new Date().getTime() - this.lastKey > 1000) this.setSearchPage("");
    },
    incPage(num) {
      this.scrolling = true;
      this.$nextTick(() => {
        let goTo = this.page + num;
        if (goTo < 1) goTo = 1;
        if (goTo > this.list.length) goTo = this.list.length;
        go(goTo, this.tabId);
      });
    },

    doResize(currScroll) {
      this.renderPage = !this.renderPage;
      this.currScroll = currScroll || 0;
      return;
    },
    scrollToPage(page) {
      page.page != undefined
        ? (this.scrollToItem = page.page)
        : (this.scrollToItem = page);
      page.offset != undefined
        ? (this.offset = page.offset)
        : (this.offset = 0);
    },
    setOcr() {
      this.setOCREnable(true);
    },
    addNewTziun() {
      this.setTabId(this.tabId);
      this.setSlotShow("add-tsiyun");
      this.toggleVisible();
    },
    addextens(type) {
      this.closeExtens(); //reset all before start

      this.setAddEtensType(type);

      this.startCreateExtentionMode();
    },
    print() {
      if (startPrint(this.tabId) === true) this.$emit("print", { show: true });
    },
  },
  mounted() {
    // ;
    this.doResize();

    this.$nextTick(function() {
      window.addEventListener("resize", this.doResize);
    });
  },

  async created() {
    this.scrollToItem = this.page;
  },
};
</script>

<style scoped>
.container {
  height: 100%;
  outline: 0;
  /* margin: auto; */
}
.scroller {
  height: 100%;
}
.disable {
  cursor: "default" !important;
  color: "gray";
}
</style>
