class Node {
  constructor(key, data) {
    this.key = key;
    this.height = 1;
    this.left = null;
    this.right = null;
    this.data = data;
  }
}

export default class AVLTree {
  constructor() {
    this.root = null;
    this.length = 0;
  }
  height(N) {
    if (N == null) return 0;

    return N.height;
  }
  max(a, b) {
    return a > b ? a : b;
  }
  rightRotate(y) {
    let x = y.left;
    let T2 = x.right;

    // Perform rotation
    x.right = y;
    y.left = T2;

    // Update heights
    y.height = this.max(this.height(y.left), this.height(y.right)) + 1;
    x.height = this.max(this.height(x.left), this.height(x.right)) + 1;

    // Return new root
    return x;
  }
  leftRotate(x) {
    let y = x.right;
    let T2 = y.left;

    // Perform rotation
    y.left = x;
    x.right = T2;

    // Update heights
    x.height = this.max(this.height(x.left), this.height(x.right)) + 1;
    y.height = this.max(this.height(y.left), this.height(y.right)) + 1;

    // Return new root
    return y;
  }
  getBalance(N) {
    if (N == null) return 0;

    return this.height(N.left) - this.height(N.right);
  }
  has(node, key) {
    let item = this.get(node, key);
    if (item) return true;
    return false;
  }
  get(node, key) {
    // Base Cases: root is null or key is present at root
    if (node == null) return undefined;
    else if (node.key == key) return node.data;
    // Key is greater than root's key
    else if (node.key < key) return this.get(node.right, key);

    // Key is smaller than root's key
    return this.get(node.left, key);
  }
  set(node, key, data) {
    this.insert(node, key, data);
  }
  insert(node, key, data) {
    /* 1. Perform the normal BST insertion */
    if (node == null) {
      this.length++;
      return new Node(key, data);
    }

    if (key < node.key) node.left = this.insert(node.left, key, data);
    else if (key > node.key) node.right = this.insert(node.right, key, data);
    // Duplicate keys not allowed
    else return node;

    /* 2. Update height of this ancestor node */
    node.height = 1 + this.max(this.height(node.left), this.height(node.right));

    /* 3. Get the balance factor of this ancestor  
                node to check whether this node became  
                unbalanced */
    let balance = this.getBalance(node);

    // If this node becomes unbalanced, then there
    // are 4 cases Left Left Case
    if (balance > 1 && key < node.left.key) return this.rightRotate(node);

    // Right Right Case
    if (balance < -1 && key > node.right.key) return this.leftRotate(node);

    // Left Right Case
    if (balance > 1 && key > node.left.key) {
      node.left = this.leftRotate(node.left);
      return this.rightRotate(node);
    }

    // Right Left Case
    if (balance < -1 && key < node.right.key) {
      node.right = this.rightRotate(node.right);
      return this.leftRotate(node);
    }

    /* return the (unchanged) node pointer */
    return node;
  }
  minValueNode(node) {
    let current = node;

    /* loop down to find the leftmost leaf */
    while (current.left != null) current = current.left;

    return current;
  }
  delete(root, key) {
    // STEP 1: PERFORM STANDARD BST DELETE
    let node = root;
    if (root == null) return root;

    // If the key to be deleted is smaller than the
    // root's key, then it lies in left subtree
    if (key < node.key) node.left = this.delete(node.left, key);
    // If the key to be deleted is greater than the
    // root's key, then it lies in right subtree
    else if (key > node.key) node.right = this.delete(node.right, key);
    // if key is same as root's key, then This is
    // the node to be deleted
    else {
      // node with only one child or no child
      if (root.left == null || root.right == null) {
        let temp = root.left ? root.left : root.right;

        // No child case
        if (temp == null) {
          temp = root;
          root = null;
        } // One child case
        else root = temp; // Copy the contents of
        // the non-empty child
        //free(temp);
      } else {
        // node with two children: Get the inorder
        // successor (smallest in the right subtree)
        let temp = this.minValueNode(root.right);

        // Copy the inorder successor's data to this node
        root.key = temp.key;

        // Delete the inorder successor
        root.right = this.delete(root.right, temp.key);
      }
    }

    // If the tree had only one node then return
    if (root == null) return root;

    // STEP 2: UPDATE HEIGHT OF THE CURRENT NODE
    /* 2. Update height of this ancestor node */
    node.height = 1 + this.max(this.height(node.left), this.height(node.right));

    /* 3. Get the balance factor of this ancestor  
               node to check whether this node became  
               unbalanced */
    let balance = this.getBalance(node);

    // If this node becomes unbalanced, then there
    // are 4 cases Left Left Case
    if (balance > 1 && key < node.left.key) return this.rightRotate(node);

    // Right Right Case
    if (balance < -1 && key > node.right.key) return this.leftRotate(node);

    // Left Right Case
    if (balance > 1 && key > node.left.key) {
      node.left = this.leftRotate(node.left);
      return this.rightRotate(node);
    }

    // Right Left Case
    if (balance < -1 && key < node.right.key) {
      node.right = this.rightRotate(node.right);
      return this.leftRotate(node);
    }

    /* return the (unchanged) node pointer */
    return node;
  }
}

