const stringMap = new Map();
let stringBuffer = new ArrayBuffer(20971520); //20mb
let viewStringBuf = new DataView(stringBuffer);
let fileBuffer = new ArrayBuffer(30971520);
let viewfileBuffer = new DataView(fileBuffer);
let bookTableBuffer = new ArrayBuffer(1600000);
let viewBookTable = new DataView(bookTableBuffer);
let curStringBufferPos = 0;
let currentStringLenPos = 0;
let curfileBufferPos = 0;
let curBookTablePos = 0;
const encoder = new TextEncoder();
function setNumber(num, size) {
  num = Number(num);
  if (size == 1) viewfileBuffer.setInt8(curfileBufferPos, num);
  else if (size == 2)
    addNum = viewfileBuffer.setInt16(curfileBufferPos, num, true);
  else addNum = viewfileBuffer.setInt32(curfileBufferPos, num, true);
  curfileBufferPos += size;
}

function getNumber(dview, size, address) {
  let addr = Number(address);
  let num;
  /**@type {DataView} */
  let dv = dview;
  if (size == 1) num = dv.getInt8(addr);
  else if (size == 2) num = dv.getInt16(addr, true);
  else num = dv.getInt32(addr, true);
  return num;
}
function setString(str) {
  let alreadyHadStr = stringMap.get(str);
  let stringSize;
  let stringStartAt;
  if (alreadyHadStr) {
    stringSize = alreadyHadStr.stringSize;
    stringStartAt = alreadyHadStr.currentStringLenPos;
  } else {
    stringSize = str.length;
    stringStartAt = currentStringLenPos;
  }

  //write the location of string (start at)
  viewfileBuffer.setInt32(curfileBufferPos, stringStartAt, true);
  //update the pointer in bigfile
  curfileBufferPos += 4;
  //write string len
  viewfileBuffer.setInt16(curfileBufferPos, stringSize, true);
  curfileBufferPos += 2;
  //we done here because we already had all the info
  if (alreadyHadStr) return;
  //add the string to string buffer
  const buffStr = encoder.encode(str);
  for (let i = 0; i < buffStr.length; i++)
    viewStringBuf.setInt8(curStringBufferPos + i, buffStr[i]);

  //add the new info to map
  stringMap.set(str, { stringSize, stringStartAt });

  //update the location of string buff
  curStringBufferPos += buffStr.length;
  currentStringLenPos += stringSize;

  allStrings += str;
}

function checkIsEmpty(item, col) {
  if (item[col] == [] || item[col] == "" || item[col] == null) return true;
  return false;
}

function getTheFlag(obj, flags, order) {
  let bookFlag = 0;
  order.forEach((keyName, keyPos) => {
    const keyInfo = flags.filter((key) => key.name == keyName)[0];
    if (checkIsEmpty(obj, keyName)) {
      //set flag to 0
      bookFlag = clearBit(bookFlag, keyInfo.bit);
    } else {
      //set flag to 1
      bookFlag = setBit(bookFlag, keyInfo.bit);
    }
  });
  return bookFlag;
}
function setFlag(flag) {
  viewfileBuffer.setInt32(curfileBufferPos, flag, true);
  curfileBufferPos += 4;
}

function updateBookTableLocation(itemId, location) {
  viewBookTable.setInt32(curBookTablePos, Number(itemId), true);
  curBookTablePos += 4;
  viewBookTable.setInt32(curBookTablePos, Number(location), true);
  curBookTablePos += 4;
}
function getString(vbuff, location) {
  let stringStartAt = getNumber(vbuff, 4, location);
  // location += 4;
  // let stringLen = getNumber(vbuff, 2, location);
  // location += 2;
  // let val = stringStartAt << 8;
  // val = val | stringLen;

  return stringStartAt;
}
function compressData(
  lst,
  flags = BookFlagInformation,
  order = bookFlagOrder,
  idKey = "id"
) {
  lst.forEach((item) => {
    let lastPos = 0;
    //first lets set the flag
    let flag = getTheFlag(item, flags, order);
    setFlag(flag);

    //now let build the obj data
    //lets go through the keys and build them
    order.forEach((keyName) => {
      let keyInfo = flags.find((k) => k.name == keyName);
      //should we ignore this key
      if (keyInfo.skip) return;
      //is the flag set
      let isFlagSet = getBit(flag, keyInfo.bit);
      if (!isFlagSet) return;
      let keyValue = item[keyName];
      if (keyInfo.type == "Number") setNumber(keyValue, keyInfo.countBytes);
      else if (keyInfo.type == "String") setString(keyValue);
      else if (keyInfo.type == "Object") {
        //this is moredata field
        let num = 0;
        num = keyValue.isText ? 1 : 0;
        num = num << 0xf;

        num = keyValue.isSidra ? 1 : 0;
        num = num << 0xf;

        num = keyValue.dor;
        num = num << 0xf;
        setNumber(num, 2);
      } else {
        //let set the count before all
        setNumber(keyValue.length, 2);
        //this is arr lets check how to build
        let innerKeysOrder = keyInfo.bookFlagOrder;
        keyValue.forEach((innerItem) => {
          innerKeysOrder.forEach((innerKey) => {
            let kInfo = keyInfo.child.find((a) => a.name == innerKey);
            if (kInfo.skip) return;
            if (kInfo.type == "Number")
              setNumber(innerItem[innerKey], kInfo.countBytes);
            else if (kInfo.type == "String") setString(innerItem[innerKey]);
          });
        });
      }
    });
    //update the table
    updateBookTableLocation(item[idKey], lastPos);
    //set to last pos
    lastPos = curfileBufferPos;
  });
  //let slice all
  let data = fileBuffer.slice(0, curfileBufferPos);
  let table = bookTableBuffer.slice(0, curBookTablePos);
  let strings = stringBuffer.slice(0, curStringBufferPos);
  return {
    data,
    table,
    strings,
    tableSize: curBookTablePos,
    stringsSize: curStringBufferPos,
    dataSize: curfileBufferPos,
  };
}

function decompressItem(
  buff,
  addr = 0,
  flags = BookFlagInformation,
  order = bookFlagOrder
) {
  let vbuff = new DataView(buff);
  let location = addr;
  let obj = { _bookinfo: 1 };
  //first let gets the flag
  let bookFlag = vbuff.getInt32(location, true);
  location += 4;
  obj._flag = bookFlag;
  order.forEach((keyName) => {
    let keyInfo = flags.find((k) => k.name == keyName);
    if (keyInfo.skip) return;
    let isSet = getBit(bookFlag, keyInfo.bit);
    //hmmm
    if (!isSet) {
      return;
    }
    if (keyInfo.type == "Number") {
      let num = getNumber(vbuff, keyInfo.countBytes, location);
      location += keyInfo.countBytes;
      obj[keyName] = num;
    } else if (
      keyInfo.type == "String" ||
      (keyInfo.type == "ArrayOfObject" && keyInfo.justOne)
    ) {
      let val = getString(vbuff, location);
      location += 4;
      obj[keyName] = val;
    } else if (keyInfo.type == "Object") {
      //this is more data
      const moreData = getNumber(vbuff, 2, location);
      location += 2;

      obj["moredata"] = moreData;
    } else if (keyInfo.type == "ArrayOfObject") {
      //get count
      let count = getNumber(vbuff, 2, location);
      obj[keyName + "_length"] = count;
      location += 2;
      if (count == 0) {
        obj[keyName + "_length"] = 0;
        return;
      }
      if (keyInfo.justOne) {
        let val = getString(vbuff, location);
        location += 4;
        obj[keyName] = val;
      } else {
        //we must have spread
        //let arr = [];
        let childs = keyInfo.child;

        for (let i = 0; i < count; i++) {
          childs.forEach((child) => {
            if (child.skip) return;
            if (child.type == "Number") {
              let num = getNumber(vbuff, child.countBytes, location);
              obj[keyName + "_" + i + "_" + child.name] = num;
              location += child.countBytes;
            } else {
              //must be string
              let str = getString(vbuff, location);
              location += 4;
              obj[keyName + "_" + i + "_" + child.name] = str;
            }
          });
        }
      }
    }
  });
  return obj;
}
function clearBit(number, bitPosition) {
  const mask = ~(1 << bitPosition);
  return number & mask;
}

function setBit(number, bitPosition) {
  return number | (1 << bitPosition);
}

function getBit(number, bitPosition) {
  return (number & (1 << bitPosition)) === 0 ? 0 : 1;
}

function toArrayBuffer(buf) {
  var view = new Uint8Array(buf);
  return view.buffer;
}

function compressArr(arr) {
  let ret = compressData(data);
  let header = Buffer.alloc(12);
  header.writeInt32LE(ret.dataSize);
  header.writeInt32LE(ret.tableSize, 4);
  header.writeInt32LE(ret.stringsSize, 8);
  let data1 = Buffer.from(ret.data);
  let strings1 = Buffer.from(ret.strings);
  let table1 = Buffer.from(ret.table);
  let dataFile = Buffer.concat([header, data1, table1, strings1]);
  return dataFile;
}

export default { decompressItem, getBit };
