import { mapActions } from "vuex";
import { signInUser } from "@/services/login";
export default {
  methods: {
    ...mapActions("user", ["login", "setUserData", "setUser"]),
    async loginOffline() {
      //auto connect user
      await this.login({
        username: "זמני",
        password: "1234",
      });
    },
    async loginOnline() {
      let user = localStorage.getItem("user");
      if (user) {
        //if user exists - verify it
        user = JSON.parse(user);
        user = await signInUser({
          token: user.token,
        });
      } else user = await signInUser();

      if (user) {
        this.setUser(user);
        this.$router.push(this.to);
      } else {
        this.$router.replace({
          name: "lic_error",
          params: { err: this.$t("errors.userError"), code: 200 },
        });
      }
    },
  },
};
