<template>
  <div class="book-details-container">
    <!-- <div class="header drag">
      <span>{{ $t("bookDetails.header") }}</span>
      <div class="darg-circle"></div>
      <div
        class="close-popup otz-icon"
        @click="closeWindow"
        style="position: absolute; left: 0;"
      >
        F
      </div>
    </div> -->

    <div class="content" :class="{ 'hide-shaar': hideShaar }">
      <!-- <spinnerCircle /> -->
      <div ref="imgContainer" v-if="!hideShaar" class="right">
        <img ref="shaar" @error="hideShaar = true" @load="resizeImage" />
      </div>
      <div class="left">
        <h3>{{ $t("bookDetails.bookName") }}</h3>
        <p>{{ bookName }}</p>

        <h3>{{ $t("bookDetails.bookAuthor") }}</h3>

        <p>{{ bookAuthor }}</p>
        <h3 v-if="bookPlaces != ''">{{ $t("bookDetails.bookPlace") }}</h3>
        <p>
          {{ bookPlaces }}
        </p>
        <h3 v-if="bookYears != ''">{{ $t("bookDetails.bookYear") }}</h3>
        <p>{{ bookYears }}</p>
        <h3 v-if="subjects != ''">{{ $t("bookDetails.subjects") }}</h3>
        <p>{{ subjects }}</p>
        <h3 v-show="getBookDetails.addnames_length > 0">
          {{ $t("bookDetails.addNames") }}
        </h3>
        <div v-show="getBookDetails.addnames_length > 0">
          <li v-for="(item, index) in bookAddNames" :key="index">
            {{ item }}
          </li>
        </div>
        <h3 v-show="bookAddAuthors.length > 0">
          {{ $t("bookDetails.addAuthors") }}
        </h3>
        <div v-show="bookAddAuthors.length > 0">
          <li v-for="(item, index) in bookAddAuthors" :key="index">
            {{ item }}
          </li>
        </div>
        <h3>{{ $t("bookDetails.bookId") }}</h3>
        <p>{{ getBookDetails.id }}</p>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import init from "@/config/init.js";
import * as bookData from "@/services/bookData.js";
import imgCode from "@/utils/imgUrlEnc";
import { Axios } from "@/services/_axios";
export default {
  props: ["bookid", "triggerResize"],
  data() {
    return { subjects: "", book: this.bookid, hideShaar: false };
  },
  watch: {
    bookid: {
      immediate: true,
      handler(val, oldVal) {
        this.getImage();
        this.getSubjects();
      },
    },
    triggerResize: {
      handler(val, oldVal) {
        this.resizeImage();
      },
    },
  },
  mounted() {
    this.getImage();
  },
  computed: {
    ...mapGetters("books", ["getBookbyId"]),
    bookName() {
      return bookData.getBookName(
        this.getBookDetails.name,
        this.getBookDetails.volume,
        true
      );
    },
    getBookDetails() {
      let bk = this.getBookbyId(this.bookid);

      return bk || {};
    },
    idd() {
      return this.bookid;
    },
    x() {
      let wWindow = window.winSizes.innerWidth;
      // let left = wWindow / 2 - this.w / 2;
      let left = wWindow / 2 - 210;
      if (left < 0) return 0;
      return left;
    },
    y() {
      let hWindow = window.winSizes.innerHeight;
      // let top = hWindow / 2 - this.h / 2;
      let top = hWindow / 2 - 296;
      if (top < 0) return 0;
      return top;
    },
    bookAuthor() {
      let author = this.getBookDetails.mainAuthorName;

      // author = author.filter((au) => au.authorTypeId == 1);

      return author;
    },

    bookAddNames() {
      let len = this.getBookDetails.addnames_length;
      let ret = [];
      for (let i = 0; i < len; i++) {
        const name = this.getBookDetails[`addnames_${i}_name`];
        ret.push(name);
      }
      // let authors = author.filter((au) => au.authorTypeId == 2);

      return ret;
    },
    bookAddAuthors() {
      let authlen = this.getBookDetails.authors_length;
      let ret = [];
      for (let i = 0; i < authlen; i++) {
        if (this.getBookDetails[`authors_${i}_authorTypeId`] == 1) continue;
        const name = this.getBookDetails[`authors_${i}_name`];
        ret.push(name);
      }
      // let authors = author.filter((au) => au.authorTypeId == 2);

      return ret;
    },
    bookPlaces() {
      return this.getBookDetails.places;
    },
    bookYears() {
      let yearFrom = this.getBookDetails.fromyear;
      let yearTo = this.getBookDetails.toyear;
      return yearFrom == yearTo ? yearTo : yearFrom + " - " + yearTo;
    },

    getShaar() {
      let url =
        init.getServer() +
        init.BooksDB.bookImages +
        init.BooksDB.byPos +
        "/" +
        this.bookid +
        "/0" +
        "?c=" +
        imgCode(this.bookid, 0);
      return url;
    },
  },
  methods: {
    ...mapActions("bookPopups", ["closeBookDetails"]),
    resizeImage() {
      let imgRatio =
        this.$refs.shaar.naturalHeight / this.$refs.shaar.naturalWidth;
      let contHeight = this.$refs.imgContainer.clientHeight;
      let contWidth = this.$refs.imgContainer.clientWidth;
      let contRatio = contHeight / contWidth;

      if (imgRatio < contRatio) {
        this.$refs.shaar.style.width = "100%";
        this.$refs.shaar.style.height = "auto";
      } else {
        this.$refs.shaar.style.height = "100%";
        this.$refs.shaar.style.width = "auto";
      }
    },
    async getImage() {
      let bookId = this.bookid;
      let pageId = 1;

      try {
        if (this.otzarBook.type(bookId) == "system") {
          let pageName = await Axios.get(
            `/api/books/pageName/${bookId}/${pageId}`
          );
          pageId = pageName.data[0].name;
        }

        let imageData;
        try {
          imageData = await bookData.getImageObject(bookId, pageId);
        } catch (ex) {
          // alert(ex);
        }
        // if (this.otzarBook.type(this.bookId) != "system") {
        //   //we deal with pdf
        //   imageData = await bookData.getPdfPageAsImage(imageData.pdf, 1);
        // }

        let isCnv =
          imageData instanceof HTMLCanvasElement ||
          (typeof OffscreenCanvas !== "undefined" &&
            imageData instanceof OffscreenCanvas);
        if (!isCnv) {
          //TODO draw image on canvas for online
          imageData = imageData.picture;
        }

        const picDom = this.$refs.shaar;
        if (
          typeof OffscreenCanvas !== "undefined" &&
          imageData instanceof OffscreenCanvas
        ) {
          const picdata = await imageData.convertToBlob();
          loadBlobToImg(picdata);
        } else {
          imageData.toBlob((picdata) => loadBlobToImg(picdata));
        }

        // picDom.width = w;
        //  picDom.height = h;
        function loadBlobToImg(blob) {
          let reader = new FileReader();
          reader.readAsDataURL(blob);
          reader.addEventListener("loadend", () => {
            let contents = reader.result;
            picDom.src = contents;
          });
        }
      } catch (ex) {
        this.hideShaar = true;
      }
    },
    closeWindow() {
      this.$emit("book-details", false);
      this.closeBookDetails();
    },

    async getSubjects() {
      // let x = this.bookid;
      let subjects = await bookData.getBookSubjects([this.bookid]);
      if (subjects.data.length == 0) return "";

      let allSubjects = subjects.data[0].categories;
      let mainSubjects = allSubjects.filter((cat) => cat.level == 1);
      let subStr = mainSubjects.map((sub) => sub.title);
      subStr = subStr.join(", ");
      this.subjects = subStr;

      //return x;
    },
  },
};
</script>
<style lang="scss" scoped>
$border: #d4d4d4;
$custom-color: gray;
.content.hide-shaar {
  padding: 15px 25px;
}
.book-details::selection {
  background-color: $custom-color !important;
  color: #000 !important;
}
.book-details-container {
  .right {
    // float: right;
    // width: 250px;
    height: 100%;
    flex-grow: 1;
    width: 30%;
    overflow: hidden;
    border-left: $border 1px solid;
    display: flex;
    padding: 10px;
    img {
      padding: 10px;
      max-height: 100%;
      margin: 0 auto;
      display: block;

      max-width: 100%;
      margin: auto;
      margin-top: 0;
      border: 1px solid #d1d1d1;
    }
  }
  .left {
    max-width: 300px;
    width: 50%;
    padding: 10px;
    flex-grow: 1;
    overflow: auto;
    user-select: text;
    cursor: text;
  }
  position: relative;
  height: 100%;
  .header {
    // width: 100%;
    // height: 56px;
    // line-height: 56px;
    // padding: 0 20px;
    // background-color: #f1f1f1;
    // color: #000;
    // position: relative;
  }
  .header span {
    // padding-top: 20px;
    // padding-right: 20px;
    font-size: 14px;
    color: #000;
    font-weight: 500;
  }
  .icon-del {
    position: absolute;
    top: 50%;
    left: 20px;
    font-size: 9px;
    line-height: 20px;
    transform: translateY(-50%);
    width: 20.3px;
    height: 20.3px;
  }
  .content {
    display: flex;
    height: 100%;
    position: relative;
  }
  h3 {
    margin-bottom: 0;
    font-size: 11px;
    font-weight: bold;
    color: #aaa;
    line-height: 15px;
  }
  p {
    margin-top: 0;
    margin-bottom: 0;
  }
}
</style>
