      >
        <g
          transform="translate(0.000000,23.000000) scale(0.100000,-0.100000)"
          fill="#000000"
          stroke="none"
        >
          <path
            d="M10 136 c0 -46 3 -92 6 -100 5 -14 23 -16 108 -15 55 0 108 5 117 10
12 6 22 2 34 -11 10 -11 25 -20 33 -20 12 0 9 7 -10 28 -21 22 -24 31 -16 49
