    <!-- <BaseIcon
      v-else
      class="icon-header"
      width="24"
      height="16"
      nameIcon="icon-folder-search-list-list"
      pathIcon="icons-folder"
      @click="importList"
    /> -->
  </div>
</template>

<script>
// import { mapGetters, mapActions, mapState } from "vuex";
export default {
  props: {
    listType: {
      type: String,
      default: "book",
    },
  },
  data() {
    return {
      nameIcon: "s",
      state: "open",
      showPopup: false,
    };
  },
  computed: {
    tooltip() {
      return this.listType === "book"
        ? this.$t("folders.openFolder")
        : this.$t("folders.openFreeList");
    },
  },
  methods: {
    importList() {
      this.$store.dispatch("folders/setOpenTypePopup", "import");
      this.$store.dispatch("folders/setListType", this.listType);
      this.$store.dispatch("folders/togglePopupFolder", true);
    },
  },
};
</script>

<style lang="scss" scoped>
.icon {
