import store from "@/store/store";
import init from "@/config/init.js";

export default function(Vue) {
  Vue.directive("version", {
    inserted: function(el, binds) {
      if (init.serverConnection == "online") return;
      // const curVersoin = store.getters["offline/version"];
      // const curVersoin = 20; //store.getters["offline/version"];
      //TEMP
      // localStorage.setItem("otzar_ver", 20);
      let otzar_ver = localStorage.getItem("otzar_ver");
      const curVersoin = otzar_ver || store.getters["offline/version"];

      if (binds.modifiers.less) {
        if (curVersoin > binds.value) el.remove();
      } else if (binds.modifiers.up) {
        if (curVersoin < binds.value) el.remove();
      } else {
        if (curVersoin != binds.value) el.remove();
      }
    },
  });
}
