import {
  postUserSetting,
  addSettingListner,
} from "@/services/userSettingsData.js";

export default function(Vue) {
  Vue.mixin({
    computed: {
      userSettings() {
        let vm = this;
        const settingsObj = {
          get settings() {
            return vm.$store.state.user.settings;
          },
          async setSettings(key, value) {
            return postUserSetting({ key, value });
          },
          delSettings(key) {},
          addListner(key, fn) {
            addSettingListner(key, fn);
          },
        };
        window.DEV_SETTINGS = settingsObj;
        return settingsObj;
      },
    },
  });
}

export async function setSettings(key, value) {
  return postUserSetting({ key, value });
}
