<template>
  <div class="input input-icon-search w-100">
    <input
      ref="input"
      type="text"
      :placeholder="placeholder"
      :value="textSearch"
      @input="keyupInput($event)"
      @blur="handleBlur($event)"
      @keydown.enter="search"
      class="w-100"
      v-focus="nofocus"
    />
    <div
      v-if="fill"
      class="icon-cancel otz-icon no-bg clear-input"
      @click="clear"
    >
      F
    </div>
    <div class="icon-general icon-search" @click="search">d</div>
  </div>
</template>

<script>
export default {
  props: ["placeholder", "text", "nofocus"],
  data() {
    return {
      textSearch: "",
      // textValue: ""
    };
  },
  computed: {
    fill() {
      //
      if (this.textSearch != "") return true;
      return false;
    },
  },
  methods: {
    mounted: function() {
      this.$nextTick(function() {
        // Code that will run only after the
        // entire view has been rendered
        this.$refs.input.focus();
        this.$refs.input.select();
      });
    },
    keyupInput(e) {
      this.textSearch = e.target.value;
      this.$emit("input", e);
    },
    handleBlur(e) {
      this.$emit("blur", e);
    },
    clear() {
      this.textSearch = "";
      this.$refs.input.focus();
      this.$emit("clear");
    },
    search() {
      this.$emit("search");
    },
  },
};
</script>

<style lang="scss" scoped>
$bg-color1: #fff;
.icon-cancel {
  cursor: pointer;
  width: 20px;
  border-radius: 50%;
  height: 20px;
  text-align: center;
  transition: 0.3s;
  font-size: 8px;
  position: absolute;
  top: 2.5px;
  left: 30px;
  &:hover {
    background-color: #00000014;
  }
}
.input-icon-search.input {
  margin: 0;
  position: relative;
  .icon-general {
    position: absolute;
    height: 25px;
    line-height: 25px;
    top: 0;

    &.icon-search {
      left: 13px;
      color: var(--custom-color1);
      transition: 0.3s;
      border-radius: 50%;
      width: 25px;
      left: 11px;
      cursor: pointer;
      &:hover {
        background-color: #d1d1d1;
      }
      &.active {
        background-color: #a7a7a7;
      }
    }
  }

  input {
    display: block;
    padding: 0 15px 0 40px;
    border: 1px solid var(--custom-color1);
    background-color: $bg-color1;
    height: 25px;
    font-size: 13px;
    font-weight: bold;
    color: #000;
    line-height: 25px;
    transition: 0.3s;
    margin: 0;
    &:focus {
      border-color: var(--custom-color1);
    }
    &::placeholder {
      font-size: 13px !important;
      font-weight: 300 !important;
      color: var(--text-color2) !important;
    }
  }
}
</style>
