<template>
  <div>
    <div class="flex-row">
      <div class="combo-vol">
        <bookVolumes
          :tabId="tabId"
          v-if="tabData(tabId).volumes.length > 1"
          :books="tabData(tabId).volumes"
        />
      </div>
      <div class="search" v-if="!customBook">
        <baseInputIconSearch
          class="w-100"
          :placeholder="$t('freeSearchInput.placeHolderInBook')"
          @input="setTextSearch"
          :value="searchInBookTxt"
          @search="search"
          @clear="$store.state.freeSearch.searchInBookTxt = ''"
          :nofocus="true"
        />
      </div>
    </div>
    <!-- not delete this div!!! -->
    <div class=""></div>
    <pagesArrows
      v-if="numPages > 0 && !txtBook"
      :numPages="numPages"
      :page="page"
      :tabId="tabId"
    />
    <customText
      class="dir-l"
      @font-family="changeF"
      @font-size="changeS"
      v-if="txtBook"
    />
  </div>
</template>
<script>
import { doFreeSearch, setSearchType } from "@/services/freeSearch.js";
import pagesArrows from "@/components/book/pagesArrows.vue";
import customText from "@/components/book/customText.vue";
import bookVolumes from "@/components/book/bookVolumes.vue";

export default {
  props: ["hasTabs", "numPages", "page", "tabId", "book"],
  components: {
    pagesArrows,
    customText,
    bookVolumes,
  },
  computed: {
    customBook() {
      return this.otzarBook.type(this.book) == "custom";
    },
    txtBook() {
      return this.$store.state.tabs[this.tabId].txtBook;
    },
    searchInBookTxt() {
      return this.$store.state.freeSearch.searchInBookTxt;
    },
  },
  methods: {
    setTextSearch(e) {
      this.$store.state.freeSearch.searchInBookTxt = e.target.value;
    },

    search() {
      this.$store.state.freeSearch.inBook = true;
      if (this.searchInBookTxt != "") {
        this.$emit("search", {
          show: true,
          txt: this.searchInBookTxt,
        });
        setSearchType("classic");
        doFreeSearch(
          this.searchInBookTxt.replace(/[\u0591-\u05C7]/g, ""), //remove niqud,
          [this.book],
          0,
          true,
          this.tabId
        ).then(() => {
          this.$emit("search", {
            show: false,
            txt: this.searchInBookTxt,
          });
        });
      }
    },
    changeF(font) {
      this.$emit("font-family", font);
    },
    changeS(size) {
      this.$emit("font-size", size);
    },
    tabData(tabId) {
      return this.$store.state.tabs[tabId];
    },
  },
};
</script>
<style scoped lang="scss">
.combo-vol {
  // position: absolute;
}
.search {
  width: 350px;
}
</style>
