import store from "@/store/store";
let func = null;
let moment = require("moment");
let pastYear = moment().subtract(1, "years");
let plus = 10;
let flag = true;
export default function(Vue) {
  Vue.directive("zindex", {
    // When the bound element is inserted into the DOM...
    inserted: function(el, binds) {
      let left = el.offsetLeft;
      let top = el.offsetTop;
      let secs = moment().diff(pastYear, "seconds");
      // add last zindex
      el.style.zIndex = secs;
      if (!binds.modifiers.fixed) {
        //neg the plus
        plus = ~plus;

        el.style.left = plus + left + "px";
        el.style.top = plus + top + "px";
      }

      //   alert(8);
      func = () => {
        let secs = moment().diff(pastYear, "seconds");
        el.style.zIndex = secs;
      };
      //add eent lis
      el.addEventListener("click", func);
    },
    update: (el, binds) => {
      el.addEventListener("click", func);
      if (el.style.display != "none" && binds.modifiers.trigerupdate) {
        func = () => {
          if (flag) {
            let left = el.offsetLeft;
            let top = el.offsetTop;
            let secs = moment().diff(pastYear, "seconds");
            // add last zindex
            el.style.zIndex = secs;
            // if (!binds.modifiers.fixed) {
            //   //neg the plus
            //   plus = ~plus;

            //   el.style.left = plus + left + "px";
            //   el.style.top = plus + top + "px";
            // }
          }
          flag = false;
        };
      }
    },

    unbind: function(el) {
      flag = true;
      if (func) el.removeEventListener("click", func);
    },
  });
}
