import { Axios } from "@/services/_axios";
import store from "../store/store";
export default async function licenseCheck({ to, from, next }) {
  //check del
  const delBooks = await Axios.get("/api/general/delBooks");
  if (delBooks.data.length === 0) {
    const next = {
      name: "lic_error",
      params: {
        err: "no delbooks file",
        code: 100,
      },
    };

    store.state.fatalError = true;
    return Promise.resolve({ error: 0, next });
  } else return Promise.resolve({ error: 0, next });
}
