export const namespaced = true;
import {
  addQuickButton,
  getUserQuickButtons,
} from "@/services/userFoldersData";
import Vue from "vue";
export const state = {
  buttons: [],
};

export const mutations = {
  ADD(state, btn) {
    state.buttons.push(btn);
  },
  SET(state, data) {
    state.buttons = data;
  },
  DELETE(state, id) {
    state.buttons = state.buttons.filter((btn) => btn.id !== id);
  },
};
export const actions = {
  add({ commit }, btn) {
    return new Promise(function(resolve, reject) {
      addQuickButton(btn).then((button) => {
        if (button) {
          commit("ADD", button);
          resolve(button.id);
        } else reject(-1);
      });
    });
  },
  remove({ commit }, id) {
    commit("DELETE", id);
  },
  setData({ commit }, data) {
    commit("SET", data);
  },

  setQuickButtonsData({ dispatch }) {
    return new Promise(function(resolve, reject) {
      getUserQuickButtons()
        .then((r) => {
          dispatch("setData", r);
          resolve(true);
        })
        .catch((error) => {
          console.error(error);
          reject(false);
        });
    });
  },
};

export const getters = {
  getButtons(state) {
    return state.buttons;
  },
};
