<template>
  <div class="div-input">
    <svg class="icon-search">
      <use v-bind="{ href: '#icons_' + nameIcon }" />
    </svg>
    <div class="relative w-100">
      <input
        class="input-search"
        ref="input"
        type="text"
        :value="textSearch"
        @input="doSearch"
        @keydown="scrollList"
        v-bind:placeholder="$t('searchInput.placeHolder')"
        v-select.focus
      />
      <div
        class=" otz-icon icon-otzar no-bg icon-clear"
        v-if="getTextSearchInput.length > 0"
        @click="clearTxt"
      >
        F
      </div>
      <div class="icon-adv-search icon-circle" @click="openAdvSearch">
        ...
      </div>
    </div>
    <BaseCleanPopup
      v-keyboard="[
        {
          keys: ['Escape'],
          cbs: [
            () => {
              showAdvWin = false;
            },
          ],
        },
      ]"
      v-body.focuselement="bookname"
      :h="37"
      :winh="310"
      :winw="410"
      v-zindex
      v-if="showAdvWin"
      @close="showAdvWin = false"
    >
      <template v-slot:header>
        <div class="flex-center-vh">
          {{ $t("advancSearchBook.text2") }}
        </div>
      </template>
      <advanvcSearch @close="showAdvWin = false" />
    </BaseCleanPopup>
  </div>
</template>

<script>
// import store from "@/store/store";

import { mapActions, mapState, mapGetters } from "vuex";
import advanvcSearch from "@/components/listBookArea/searchBarContainer/advanvcSearch.vue";

let timeout, timeoutSetActive;
export default {
  components: {
    advanvcSearch,
  },
  data() {
    return {
      // txtSearch: "",
      nameIcon: "icon-input-search",
      nameopen: "open-list",
      showAdvWin: false,
      bookname: "bookname",
    };
  },
  computed: {
    ...mapState("bookList", ["textSearch", "setFocusInInput"]),
    ...mapGetters("bookList", ["getTextSearchInput"]),
  },
  methods: {
    ...mapActions("bookList", ["setSearchTo", "setLastPinTo", "setDoNotFocus"]),
    ...mapActions("booklistComp", ["setFirstActive"]),
    openAdvSearch() {
      this.showAdvWin = true;
    },
    doSearch(event) {
      clearTimeout(timeout);
      clearTimeout(timeoutSetActive);
      timeout = setTimeout(() => {
        this.setDoNotFocus(true);
        this.setSearchTo(event.target.value);
        // this.setLastPinTo(0);
        //this.setPinChecks(false);
      }, 300);
      timeoutSetActive = setTimeout(() => {
        //this.setFirstActive(true);
      }, 800);
    },
    scrollList(event) {
      switch (event.code) {
        case "ArrowDown":
        case "Numpad2":
          this.setFirstActive(new Date().getMilliseconds());
          break;

        case "Tab":
          event.preventDefault();
          event.stopPropagation();
          if (event.shiftKey) {
            this.$store.dispatch("tabsManager/goToNextTab");
            break;
          }

          let bookElement = document.getElementById(
            `the-book${this.$store.state.tabsManager.currentTabId}`
          );
          if (bookElement) bookElement.focus();
          break;
        case "F2":
          event.preventDefault();
          event.stopPropagation();
          this.$store.state.bookList.setFocusInInput++;
          break;
        case "F3":
          event.preventDefault();
          event.stopPropagation();
          this.$store.state.freeSearchBookList.setFocusInInput++;
          break;
      }
    },
    clearTxt() {
      this.setSearchTo("");
      this.$refs.input.focus();
    },
  },
  watch: {
    setFocusInInput: function() {
      if (this.$refs.input) this.$refs.input.focus();
    },
  },
};
</script>

<style lang="scss" scoped>
$bg-color1: #fff;
.div-input {
  padding: 12px 12px 12px;
  position: relative;
  .icon-adv-search {
    position: absolute;
    left: 5px;
    width: 20.3px;
    height: 20.3px;
    border-radius: 50%;
    font-size: 16px;
    font-weight: 900;
    top: 5px;
    line-height: 14px;
    background-color: var(--bg-color2);
    text-align: center;
    cursor: pointer;
    color: var(--text-color2);
  }
}
.icon-search {
  height: 22.5px;
  width: 24px;
  position: absolute;
  right: 11px;
  top: 18.5px;
  position: absolute;
  z-index: 1;
}
input {
  height: 30.54px;
  border-radius: 15px;
  width: 100%;
  background-color: $bg-color1;
  border: 1px solid rgba(0, 0, 0, 0.2);
  font-size: 16px;
  font-weight: bold;
  color: #000;
  padding: 0 30px 0 22px;
  outline: 0;
  &.input-search {
    text-align: right;
    direction: rtl;
  }
}
input:focus {
  border: 1px solid var(--text-color10);

  // border: 1px solid rgba(0, 0, 0, 0.5);
}
input::placeholder {
  // font-size: 16px;
  font-weight: normal;
  color: rgba(0, 0, 0, 0.4);
}
.icon-clear {
  position: absolute;
  top: 0;
  height: 100%;
  left: 32px;
  line-height: 31px;
  font-size: 10px;
}
</style>
