    };
  },
  computed: {
    handlerStyle() {
      return "top:" + "";
    },
    nameIcon() {
      if (this.txt) return "arrow-general";
    },
  },
  methods: {
    parentClick() {
      this.$emit("click");
    },
  },

  watch: {
    left: function() {
      this.$refs.handler.style.left = this.left + "%";
    },
  },
};
</script>

