<template>
  <!-- <div class="item page-nav-item" @click="goToPage()" >
    <span   >{{ letter }}</span>
  </div> -->
  <div
    :active="active"
    class="item page-nav-item"
    @click="goToPage()"
    :position="position"
  >
    <span class="text">{{ letter }}</span>
  </div>
</template>

<script>
export default {
  props: ["id", "name", "letter", "position", "active"],
  computed: {
    title() {
      return this.letter;
    },
  },
  methods: {
    async goToPage() {
      this.$emit("click", this.id);
    },
  },
};
</script>

<style lang="scss" scoped>
.item {
  text-align: right;
}
</style>
