<template>
  <div class="contact-main-block w-100 relative">
    <HeaderMenu
      :title="title"
      :routeBackName="routeBackName"
      :class="{ 'mrg-b-30': !$store.state.hasInternet }"
    />
    <div v-if="$store.state.hasInternet">
      <div class="waiting" v-if="waiting">
        <div class="text-color text-larg">{{ this.$t("support.waiting") }}</div>
        <spinnerCircle />
      </div>
      <div class="contact-support-team" v-if="!waiting">
        <div class="contact-support">
          <div class="mrg-b-6 bold">
            {{ this.$t("support.contactSupport") }}
          </div>
          <div>02-5866078 | otzar@otzar.biz</div>
          <button
            v-remove.online
            @click="openErrorsLog"
            class="center btn btn-w-larg"
          >
            {{ $t("support.openLogFile") }}
          </button>
        </div>
        <!-- <div v-remove.online>
          <div>מסור מספר זה לתמיכה:</div>
          <div class="code-support bold">{{ code }}</div>
        </div> -->
        <div v-remove.offline>
          <form :action="'mailto:otzar@otzar.biz'">
            <input
              type="hidden"
              name="Subject"
              value="פניה בענין האתר המקוון"
            />
            <input type="hidden" name="Body" value="" />
            <button @click="sendMail" class="center btn btn-w-larg">
              כתוב לנו
            </button>
            <!-- <input
              type="submit"
              class="center btn btn-w-larg"
              value="כתוב לנו"
            /> -->
          </form>
        </div>
      </div>
    </div>
    <div class="contact-support" v-else>
      <div class="mrg-b-6 bold">{{ this.$t("support.contactSupport") }}</div>
      <div>02-5866078 | otzar@otzar.biz</div>
      <button
        v-remove.online
        @click="openErrorsLog"
        class="center btn btn-w-larg"
      >
        {{ $t("support.openLogFile") }}
      </button>
    </div>
  </div>
</template>

<script>
// import menuService from "../../../../services/menu";
// import * as userSendEmail from "@/services/userSendEmail.js";
export default {
  sockets: {
    connect: function() {},
    tvcode: function(code) {
      this.waiting = false;
      this.code = code.trim();
    },
  },
  data() {
    return {
      title: this.$t("mainMenu.support"),
      routeBackName: "main",
      waiting: globalThis.SERVER_MODE == "offline",
      code: "",
    };
  },
  methods: {
    sendMail() {
      const body = "";
      const subject = "פניה בענין האתר המקוון";

      window.location.replace(
        `mailto:otzar@otzar.biz?body=${body}&subject=${subject} wants to contact you`
      );
    },
    async openErrorsLog() {
      if (!(await globalThis.OPEN_ERROR_LOG())) {
        const notification = {
          type: "error",
          message: this.$t("support.noFileMsg"),
          timeout: 2000,
        };
        this.$notify(notification);
      }
    },
  },
  created() {
    //disable tv for now
    this.waiting = false;
    //if we had internet then try to get the tv info
    if (this.$store.state.hasInternet) {
      //run tv and wait for code
      // if (globalThis.SERVER_MODE == "offline")
      //   this.$socket.client.emit("getTvCode");
    }
  },
};
</script>

