<template>
  <div class="contact-main-block w-100 relative">
    <HeaderMenu
      :title="title"
      :routeBackName="routeBackName"
      :class="{ 'mrg-b-30': !$store.state.hasInternet }"
    />
    <div>
      <div class="contact-support-team">
        <div v-if="mainEditionDesc != ''" class="contact-support">
          <div class="mrg-b-6 bold">
            {{ this.$t("myLicense.OH_license") }}
          </div>
          <div>{{ $t("myLicense.edition") }}:</div>
          <div>{{ mainEditionDesc }}</div>
          <div>{{ $t("myLicense.start") }}:</div>
          <div>{{ periodStartStr }}</div>
          <div>{{ $t("myLicense.end") }}:</div>
          <div>{{ periodEndStr }}</div>
          <div v-if="packages.size > 0">{{ $t("myLicense.packages") }}:</div>
          <div v-for="(p, index) in packages" :key="index">
            {{ getPackageDesc(p) }}
          </div>
        </div>
        <div v-else>
          אין לך מנוי פעיל

          <div v-if="tickets.length > 0">
            <div v-for="ticket in tickets" :key="ticket.id">
              יש לך כרטיסיית צפייה נותרו לך {{ ticket.daysLeft }} מתוך
              {{ ticket.days }}
              <button @click="doActualizeDay(ticket.id)">מימוש יום</button>
            </div>
          </div>
        </div>

        <div></div>
        <div v-if="prints > 0">
          <div class="mrg-b-6 bold">
            {{ $t("myLicense.printLicense") }}
          </div>
          <div>{{ $t("myLicense.amount") }}:</div>
          <div>
            {{ printsLeft }} / {{ prints }} {{ $t("myLicense.prints") }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import menuService from "../../../../services/menu";
// import * as userSendEmail from "@/services/userSendEmail.js";
import { permissionsEnum } from "@/services/license.js";
import { getUserLicenseInfo, actualizeDay } from "@/services/online/license.js";
import moment from "moment";
export default {
  data() {
    return {
      title:
        this.$t("general.hello") +
        " " +
        this.$store.state.user.fullname +
        " > " +
        this.$t("myProfile.myLicense"),
      routeBackName: "myProfile",
      mainEdition: "",
      packages: new Set(),
      printCode: "",
      prints: 0,
      printsLeft: 0,
      periodStart: "",
      periodEnd: "",
      tickets: [],
    };
  },
  computed: {
    mainEditionDesc() {
      let editionData = permissionsEnum[this.mainEdition];
      return editionData ? editionData.description : "";
    },
    periodStartStr() {
      return moment(this.periodStart).format("DD.MM.YYYY / hh:mm");
    },
    periodEndStr() {
      return moment(this.periodEnd).format("DD.MM.YYYY / hh:mm");
    },
  },
  methods: {
    async doActualizeDay(saleId) {
      if (await actualizeDay(saleId)) {
        getUserLicenseInfo().then((data) => {
          if (!data) return;
          this.parseLicenseData(data);
        });
      } else alert("תקלה");
    },
    getPackageDesc(p) {
      return permissionsEnum[p] ? permissionsEnum[p].description : "";
    },
    parseLicenseData(data) {
      //print data
      this.printCode = data.printCode;
      this.prints = data.prints;
      this.printsLeft = data.prints - data.printsUsed;

      //main edition and packages
      data.permissions.forEach((permission) => {
        if ((permission.protectionCode & 0b10) > 0) {
          this.mainEdition = "GENERAL";
          this.periodStart = permission.validFrom;
          this.periodEnd = permission.validTo;
        } else if ((permission.protectionCode & 0b1000) > 0) {
          this.mainEdition = "CHABAD";
          this.periodStart = permission.validFrom;
          this.periodEnd = permission.validTo;
        } else if ((permission.protectionCode & 0b100) > 0) {
          this.mainEdition = "SCHOOL";
          this.periodStart = permission.validFrom;
          this.periodEnd = permission.validTo;
        }

        //now packages
        if ((permission.protectionCode & 0b10000) > 0)
          this.packages.add("KOOK");
        if ((permission.protectionCode & 0b100000) > 0)
          this.packages.add("MACHON");
        if ((permission.protectionCode & 0b1000000) > 0)
          this.packages.add("SHLOMO");
        if ((permission.protectionCode & 0b10000000) > 0)
          this.packages.add("SHALOM");
        if ((permission.protectionCode & 0b100000000) > 0)
          this.packages.add("OFEK");
        if ((permission.protectionCode & 0b1000000000) > 0)
          this.packages.add("OZ");
        if ((permission.protectionCode & 0b10000000000000) > 0)
          this.packages.add("AHARON");
        if (
          (permission.protectionCode & 0b1000) > 0 &&
          this.mainEdition != "CHABAD"
        )
          this.packages.add("CHABAD");
      });

      this.tickets = data.tickets;
    },
  },
  created() {
    getUserLicenseInfo().then((data) => {
      console.log(data);
      if (!data) return;
      this.parseLicenseData(data);
    });
  },
};
</script>

