<template>
  <div class="main-setting-menu">
    <HeaderMenu :title="$t('about.name')" :routeBackName="routeBackName" />
    <div class="about">
      <div class="item" @click="openDevOnClicks">
        <div>{{ clientId }}</div>
      </div>
      <div class="item" @click="openDevOnClicks">
        <div>{{ getLicType }}</div>
        <div>{{ getVersion }}</div>
        <div>{{ getUpdateVersion }}</div>
      </div>

      <div class="item" v-if="this.packageData != 'NA'">
        <div class="title">
          {{ $t("about.edition") }}
        </div>
        <p cl v-for="(item, index) in getEdition" :key="index">
          {{ item }}
        </p>
      </div>

      <div class="item" v-if="this.packageData != 'NA'">
        <div class="title">
          {{ $t("about.package") }}
        </div>
        <span v-if="getPackages.length == 0"> {{ $t("about.nopackage") }}</span>
        <p cl v-for="(item, index) in getPackages" :key="index">
          {{ item }}
        </p>
      </div>

      <!--  <div class="item" v-if="this.packageData != 'NA'">
        <div class="title">
          חבילות חסרות
        </div>

        <p cl v-for="(item, index) in missingPackages" :key="index">
          {{ item.desc }}
          <span
            style="margin-right:20px;padding:5px;border:1px solid"
            @click="buyPackage(item.desc, item.licCode)"
            >רכוש</span
          >
        </p>
      </div>
      <iframe ref="store"></iframe> -->
      <div class="item" v-if="(this.packageData != 'NA' && mefLicense) || true">
        <div class="title">
          {{ $t("about.verMeforshei") }} {{ mefotzarLicence + 1 }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getAppInfo } from "@/services/offline/license.js";
import init from "@/config/init.js";
import errorsMgr from "@/helper/errorCodes";
import { Axios } from "@/services/_axios";

export default {
  data() {
    return {
      routeBackName: "help",
      licenseType: 0,
      expireDate: "",
      counter: 0,
      version: 0,
      updateVer: 0,
      mefotzarLicence: 0,
      packageData: 0,
      packages: [],
      missingPackages: [],
    };
  },
  computed: {
    clientId() {
      return (
        this.$t("about.userId") +
        ": " +
        (this.$store.state.user.stickerId || "מידע לא זמין")
      );
    },
    getLicType() {
      return typeof this.licenseType === "string"
        ? this.licenseType
        : this.licenseType == 0
        ? this.$t("about.notFindInfo")
        : this.licenseType == 1
        ? this.$t("about.permanentLicense")
        : this.$t("about.temporaryLicense") + " " + this.expireDate;
    },
    getVersion() {
      return this.version == 0
        ? ""
        : this.$t("about.version") + "  " + this.version;
    },
    getUpdateVersion() {
      return this.updateVer == 0
        ? ""
        : this.$t("about.updateversion") + "  " + this.updateVer;
    },
    mefLicense() {
      let licensePackages = init.licensePackages;

      let pack = this.packageData;
      if (pack == 0) return false;

      return (pack & licensePackages.MEF_OTZAR) == licensePackages.MEF_OTZAR;
    },
    getPackages() {
      let licensePackages = init.licensePackages;
      let tempArr = [];
      this.missingPackages = [];
      let pack = this.packageData;
      //if (pack == 0) return [];

      this.isPackageExists(
        pack,
        licensePackages.ENC,
        "אנציקלופדיה תלמודית",
        tempArr
      );

      this.isPackageExists(pack, licensePackages.AHARON, "זכרון אהרן", tempArr);

      this.isPackageExists(pack, licensePackages.CHABAD, 'חב"ד', tempArr);

      this.isPackageExists(pack, licensePackages.KOOK, "מוסד הרב קוק", tempArr);

      this.isPackageExists(
        pack,
        licensePackages.MACHON,
        "מכון ירושלים",
        tempArr
      );

      this.isPackageExists(
        pack,
        licensePackages.OTZAR_MEFHARSHI_HATLMOD,
        "אוצר מפרשי התלמוד",
        tempArr
      );

      this.isPackageExists(pack, licensePackages.SHLOMO, "חכמת שלמה", tempArr);
      this.isPackageExists(pack, licensePackages.SHALOM, "אהבת שלום", tempArr);
      this.isPackageExists(pack, licensePackages.OZ, "עוז והדר", tempArr);
      this.isPackageExists(pack, licensePackages.OFEK, "מכון אופק", tempArr);

      this.packages = tempArr;
      return this.packages;
    },
    getEdition() {
      let licensePackages = init.licensePackages;
      let tempArr = [];

      let pack = this.packageData;
      if (pack == 0) return [];

      if ((pack & licensePackages.ALL) == licensePackages.ALL)
        tempArr.push(this.$t("about.licensePackagesFull"));
      else {
        tempArr.push("בני תורה");
      }

      // this.packages = tempArr;
      return tempArr;
    },
  },
  methods: {
    /*  buyPackage(packageName, packageCode) {
      Axios.get("/api/store/plans")
        .then((response) => {
          this.$refs.store.src = response.data;
        })
        .catch(function(error) {
          return Promise.reject(error);
        });
    }, */
    getInfo(name) {},
    isPackageExists(pack, packCode, packDesc, tempArr) {
      if ((pack & packCode) == packCode) tempArr.push(packDesc);
      else this.missingPackages.push({ licCode: packCode, desc: packDesc });
    },
    openDevOnClicks() {
      this.counter++;
      if (this.counter == 7) {
        if (globalThis._elwin) globalThis._elwin.webContents.openDevTools();
        this.counter = 0;
      }
    },
  },
  created() {
    getAppInfo()
      .then((res) => {
        if (res) {
          if (typeof res === "number") {
            this.licenseType =
              (errorsMgr.errorCodes[res - 1] || "UNKNOWN") +
              " " +
              this.$t("errors.code") +
              " " +
              res;
            this.expireDate = "";
            this.version = "";
            this.packageData = "NA";
            return;
          }
          this.updateVer = res.updatesVer;
          this.licenseType = res.LicType;
          this.expireDate = res.Day + "/" + res.Month + "/" + res.Year;
          this.version = res.otzarVer;
          this.packageData = res.packages;
          this.mefotzarLicence = res.mefotzarLicence;
        }
      })
      .catch((err) => {
        console.error(err);
      });
  },
};
</script>

<style lang="scss" scoped>
.about {
  // padding: 20px;
}
.main-setting-menu {
  color: var(--custom-color1);
  .title {
    color: #7f7f7f;
    font-weight: bold;
    font-size: 13px;
  }
  .item {
    border-bottom: 1px solid #ececec;
    padding: 20px;
  }
}
</style>
