<template>
  <popupUserExtensions
    @close="$emit('close')"
    :title="$t('userDefaultFolders.markers')"
    name="marker"
    :list="geMarkers"
    :status="status"
    :listCheckedIds="listCheckedIds"
    icon="5"
    :total="totalRows"
    :limit="limit"
    :offset="offset"
    @inc="getRows"
    @search="doSearch"
  />
</template>

<script>
import popupUserExtensions from "@/components/popupUserExtensions/popupUserExtensions.vue";
import { mapGetters, mapActions } from "vuex";
import persPopup from "@/mixing/mixPersonalAddPopup";
export default {
  mixins: [persPopup],
  components: {
    popupUserExtensions,
  },
  data() {
    return { limit: 15 };
  },
  //menuPersonalExtentions
  computed: {
    ...mapGetters("menuPersonalExtentions", [
      "getUserMarkersList",
      "getListCheckedIds",
    ]),
    x() {
      let wWindow = window.winSizes.innerWidth;
      let left = wWindow / 2 - 500;
      return left;
    },
    y() {
      let hWindow = window.winSizes.innerHeight;
      let top = hWindow / 2 - 296;
      return top;
    },
    geMarkers() {
      return this.getUserMarkersList;
    },
    listCheckedIds() {
      return this.getListCheckedIds;
    },
    totalRows() {
      if (this.getUserMarkersList.length == 0) return 0;
      return this.getUserMarkersList[0].totalRows;
    },
    status() {
      let comments = this.getUserMarkersList;

      if (comments.length == 0) return "";
      let commentsStatus =
        this.totalRows +
        " " +
        this.$t("personalExtensions.markerIn") +
        " " +
        comments[0].totalBooks +
        " " +
        this.$t("general.books") +
        " ";
      return commentsStatus;
    },
  },
  methods: {
    ...mapActions("userPersonalExtensions", ["loadUserMarkersList"]),
    doSearch(txt) {
      this.setOffset(0);
      this.loadUserMarkersList({
        offset: this.offset,
        limit: this.limit,
        txt,
      });
    },
    getNext() {
      this.incOffset(1);
      this.getRows();
    },
    getPrev() {
      this.incOffset(-1);
      this.getRows();
    },
    getRows(val) {
      if (val) this.incOffset(val);
      else this.setOffset(0);
      this.loadUserMarkersList({ offset: this.offset, limit: this.limit });
    },
  },
  created() {
    this.getRows();
  },
};
</script>

<style lang="scss" scoped>
.comments-pop {
  height: 100%;
  width: 100%;
  background-color: antiquewhite;
  position: absolute;
  z-index: 4;
}
</style>
