<template>
  <div class="tools-container">
    <div
      v-if="!txtBook"
      class="item item-fit-w"
      v-tooltip="{
        content: this.$t('toolbox.fitHeight'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div
        :class="['font-icon-bar font-icon', { active: fit == 'height' }]"
        @click="fitToHeight"
      >
        d
      </div>
    </div>
    <div
      v-if="!txtBook"
      class="item"
      v-tooltip="{
        content: this.$t('toolbox.fitWidth'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div
        :class="['font-icon-bar font-icon width', { active: fit == 'width' }]"
        @click="fitToWidth"
      >
        d
      </div>
    </div>
    <div
      class="item"
      v-if="!isWindow(tabId) && !this.$store.state.bookOnlyMode"
      v-tooltip="{
        content: this.$t('toolbox.fullScreen'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div
        :class="['font-icon-bar font-icon', { active: fullScreen }]"
        @click="setFullScreen"
      >
        {{ getFullScreenIcon }}
      </div>
    </div>

    <div tabindex="0" v-if="!txtBook" class="item" @keyup="doZoom">
      <baseDropDown
        :class="['left zoom', { active: zoomValue != 100 }]"
        :selfClose="false"
      >
        <template #button class="list-dropdown">
          <div
            class="font-icon-bar font-icon"
            v-tooltip="{
              content: $t('toolbox.zoom'),
              placement: 'right',
              classes: 'machon-tooltip',
            }"
          >
            f
          </div>
        </template>
        <template #list class="list-dropdown disable">
          <div class="flex flex-align-center flex-bet">
            <div
              class="present mrg-l-12 flex flex-align-center"
              style="line-height: 33px;"
            >
              {{ zoomValue }}%
            </div>
            <div class="flex">
              <div
                class="zoom-act zoom-in icon-circle mrg-l-6"
                :class="{ disable: zoomValue >= maxZoom }"
                @click="zoomInOut(1)"
              >
                +
              </div>
              <div
                class="zoom-act zoom-out icon-circle mrg-l-6"
                :class="{ disable: zoomValue <= minZoom }"
                @click="zoomInOut(-1)"
              >
                -
              </div>
              <baseButton @click="initZoom">{{
                $t("toolbox.reset")
              }}</baseButton>
            </div>
          </div>

          <!-- <vue-slider v-model="value" /> -->
        </template>
      </baseDropDown>
    </div>
    <div class="seperator"></div>
    <!-- *****2***** -->
    <div
      v-dev
      v-version.up="20"
      class="item mm-icon"
      @click="setFindMmWin(!showFindMmWin)"
    >
      <div
        class="font-icon-bar font-icon"
        v-tooltip="{
          content: 'חפש מראה מקום',
          placement: 'right',
          classes: 'machon-tooltip',
        }"
      >
        m
      </div>
    </div>
    <!--     <div v-if="!customBook" class="item">
      <baseDropDown :class="['search']" :selfClose="false">
        <template #button class="list-dropdown">
          <div
            class="font-icon-bar font-icon"
            ref="search"
            @click="search"
            v-tooltip="{
              content: $t('toolbox.searchInBook'),
              placement: 'right',
              classes: 'machon-tooltip',
            }"
          >
            m
          </div>
        </template>
        <template #list="{ clicked }" class="list-dropdown disable">
          <div class="w-100">
            <baseInputIconSearch
              class="w-100"
              :placeholder="$t('bookSelectArea.searchInBook')"
              @input="setSearchTxt"
              @search="
                search();
                clicked();
              "
            />
          </div>

         
        </template>
      </baseDropDown>
    </div> -->
    <div
      v-if="!txtBook && !customBook"
      class="item"
      v-tooltip="{
        content: this.$t('toolbox.ocr'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div
        :class="['font-icon-bar font-icon', { active: OCREnabled }]"
        @click="toggleOcr"
      >
        b
      </div>
    </div>
    <div
      class="item"
      v-tooltip="{
        content: this.$t('toolbox.print'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div
        v-disable
        :class="[
          'font-icon-bar font-icon',
          { active: printBookVisibility },
          { disable: !bookAllowed },
        ]"
        @click="print"
      >
        h
      </div>
    </div>
    <div class="seperator"></div>
    <!-- ***3*** -->
    <div
      v-if="!txtBook"
      class="item"
      v-tooltip="{
        content: this.$t('toolbox.userExtens'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div
        v-kiosk
        :class="['font-icon-bar font-icon', { active: showAdds }]"
        @click="showUserAdditions"
      >
        c
      </div>
    </div>
    <div
      class="item"
      v-tooltip="{
        content: this.$t('toolbox.link'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div class="font-icon-bar font-icon" @click="link">i</div>
    </div>
    <div class="seperator"></div>
    <div
      v-if="!$store.state.bookOnlyMode"
      class="item fav"
      :class="{ active: getFavActive }"
      v-tooltip="{
        content: this.getFavoritesTooltip,
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div class="font-icon-bar font-icon" v-kiosk @click="addToFavorites">
        {{ getFavoritesIcon }}
      </div>
    </div>
    <!-- <div class="item">
      <div class="font-icon-bar font-icon" @click="search">m</div>
    </div> -->

    <!-- <div class="item">
      <div class="font-icon-bar font-icon" @click="search">m</div>
    </div> -->

    <!-- <div class="item">
      <div class="font-icon-bar font-icon" @click="search">m</div>
    </div> -->

    <!-- <div class="item">
      <div class="font-icon-bar font-icon" @click="search">m</div>
    </div> -->
    <div
      v-if="!txtBook && !customPDFBook && !customBook"
      class="item mm-icon"
      @click="toggleMM"
      v-tooltip="{
        content: mmToolTip,
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div class="font-icon-bar font-icon" :class="{ active: showMm }" v-kiosk>
        {{ getIconMM }}
      </div>
    </div>
    <!-- <div class="item">
      <div class="font-icon-bar font-icon" @click="search">m</div>
    </div> -->

    <div
      v-if="hasFsResults"
      class="item"
      @click="removeFsMarks()"
      v-tooltip="{
        content: showHideFs,
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div
        :class="['font-icon-bar font-icon hide-results', { active: !drawFs }]"
      >
        <div v-if="!drawFs">
          <div class="mef-icon">l</div>
        </div>
        <div v-if="drawFs">
          <div class="mef-icon">l</div>
        </div>
      </div>
    </div>

    <div class="seperator" v-if="!$store.state.bookOnlyMode"></div>

    <div
      v-if="!$store.state.bookOnlyMode"
      class="item"
      @click="addBook()"
      v-tooltip="{
        content: this.$t('toolbox.openBook'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div class="font-icon-bar font-icon">j</div>
    </div>
    <!-- <div
      v-if="txtBook"
      class="item"
      @click="removeFsMarks()"
      v-tooltip="{
        content: this.$t('toolbox.removeFs'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <baseDropDown :class="['search']" :selfClose="false">
        <template #button class="list-dropdown">
          <div class="font-icon-bar font-icon">m</div>
        </template>
        <template #list class="list-dropdown disable">
          <div class="w-100">
            <ComboboxInput
              class="fit-height "
              :items="listFonts"
              @input="chooseFonts($event)"
              :labelStart="1"
              :labal="listFonts[0]"
              :labelKey="1"
            />


          </div>
        </template>
      </baseDropDown>
    </div> -->
    <div
      v-if="!$store.state.bookOnlyMode"
      class="item"
      v-tooltip="{
        content: this.$t('toolbox.detailesBook'),
        placement: 'right',
        classes: 'machon-tooltip',
      }"
    >
      <div
        :class="['font-icon-bar font-icon', { active: bookDetailsVisibility }]"
        @click.stop="bookDetails"
      >
        a
      </div>
    </div>
  </div>
</template>
<script>
import { doFreeSearch, setSearchType } from "@/services/freeSearch.js";
import { allowedToPrint, startPrint } from "@/services/print.js";
import { systemFonts } from "@/services/general.js";
import { mapActions, mapGetters, mapState } from "vuex";
import VueSlider from "vue-slider-component/dist-css/vue-slider-component.umd.min.js";
import "vue-slider-component/dist-css/vue-slider-component.css";
import init from "@/config/init.js";
// import theme
import "vue-slider-component/theme/default.css";
import * as tabModule from "@/store/modules/_tab.js";

export default {
  components: {
    VueSlider,
  },
  data() {
    return {
      showPrintBox: false,
      zoomValue: 100,
      sliderOptions: {
        min: 50,
        max: 150,
      },
      searchTxt: "",
      closeFS: true,
      showMM: false,
      maxZoom: 250,
      minZoom: 40,
      zoomKey: "",
      lastZoomKey: "",
      searchTimer: null,
    };
  },
  props: [
    "book",
    "page",
    "showDetails",
    "showPrint",
    "tabId",
    "showAdds",
    "drawFs",
  ],
  watch: {
    openFs: function(newV, oldV) {
      if (newV) {
        this.$refs.search.click();
        this.$store.dispatch(`tabs/${this.tabId}/openFs`, false, {
          root: true,
        });
      }
    },
    /** @todo mistake: params order */
    zoomValue: function(oldVal, val) {
      this.zoom(val);
    },
    // setShowMm(val) {
    //   this.setMm({ key: "show", value: val });
    //   this.$emit("drawMM", val);
    // },
    // viewPlacesRegularly: function(val, oldVal) {
    //   this.showMM = val;
    //   this.$emit("drawMM", this.showMM);
    // },
    // book: function(val, oval) {
    //   this.showMM = this.viewPlacesRegularly;
    //   this.$emit("drawMM", this.showMM);
    // },
  },

  computed: {
    ...mapState("bookPopups", [
      "printBookVisibility",
      "bookDetailsVisibility",
      "OCREnabled",
      "personalExtVisibility",
    ]),
    ...mapGetters("userSetting", ["showMm"]),
    // ...mapState("userSetting", ["viewPlacesRegularly"]),
    ...mapGetters("tabsManager", ["fullScreen", "isWindow"]),
    ...mapGetters("findMm", ["showFindMmWin"]),
    openFs() {
      return this.$store.state.tabs[this.tabId].openFs;
    },
    toggleMmStyle() {
      if (this.showMM) return this.$t("toolbox.hideMM");
      return this.$t("toolbox.showMM");
    },
    txtBook() {
      return this.$store.state.tabs[this.tabId].txtBook;
    },
    hasFsResults() {
      return this.$store.getters[`tabs/${this.tabId}/hasFsResults`];
    },
    customBook() {
      return this.otzarBook.type(this.book) == "custom";
    },
    customPDFBook() {
      return this.otzarBook.type(this.book) == "customPDF";
    },

    bookInFavorites() {
      let x = this.$store.state.userFavoriteBooks.favoriteChange;
      return this.$store.getters[`tabs/${this.tabId}/bookInFavorites`];
    },
    getFavoritesIcon() {
      let x = this.$store.state.userFavoriteBooks.favoriteChange;
      return !this.bookInFavorites ? "g" : "O";
    },
    getFavoritesTooltip() {
      let x = this.$store.state.userFavoriteBooks.favoriteChange;
      let tooltip;
      if (this.bookInFavorites) tooltip = this.$t("toolbox.removefavorites");
      else tooltip = this.$t("toolbox.favorites");
      return tooltip;
    },
    getFavActive() {
      if (this.$store.state.userFavoriteBooks.favoriteChange) {
        return true;
      }
      return false;
    },
    mmToolTip() {
      return this.showMm ? this.$t("toolbox.hideMM") : this.$t("toolbox.mm");
    },
    getFullScreenIcon() {
      return this.fullScreen ? "e" : "e";
    },
    fit() {
      return this.$store.state.tabs[this.tabId].bookFit;
    },
    bookAllowed() {
      return allowedToPrint(this.book.toString());
    },
    listFonts() {
      let fonts = systemFonts().data;
      fonts.forEach((font, i) => {
        let a = font;
        if (a) {
          a = a.split("\\");
          a = a[a.length - 1].split(".");
          let b = {};
          b.label = a[0];
          b.name = a[0];
          b.key = 0;
          fonts[i] = b;
        }
      });

      return fonts;
    },
    showHideFs() {
      if (this.drawFs) return this.$t("toolbox.removeFs");
      return this.$t("toolbox.showFs");
    },
    getIconMM() {
      if (!this.showMm) return "k";
      return "z";
    },
  },
  methods: {
    ...mapActions("createBook", ["setAction"]),
    ...mapActions("tabsManager", ["toggleFullScreen"]),
    ...mapActions("userSetting", ["setMm"]),
    ...mapActions("findMm", ["setFindMmWin"]),
    ...mapActions("userFavoriteBooks", [
      "addFavoriteBook",
      "removeFavoriteBook",
    ]),
    ...mapActions("bookPopups", [
      "togglePrintBookVisible",
      "toggleBookDetailsVisible",
      "setOCREnable",
      "togglePersonalExtVisible",
    ]),
    doZoom(event) {
      this.lastZoomKey = new Date().getTime();
      this.zoomKey += event.key;
      this.searchTimer = clearTimeout();
      this.searchTimer = setTimeout(this.doZoomPage, 500);
    },
    doZoomPage() {
      try {
        let found;
        let searchtxt = this.zoomKey;
        //check if it a valid  number
        if (
          !isNaN(searchtxt) &
          (parseInt(searchtxt) <= 250) &
          (parseInt(searchtxt) >= 40)
        ) {
          let zoom = parseInt(searchtxt);
          if (zoom > this.maxZoom) zoom = this.maxZoom;
          else if (zoom < this.minZoom) zoom = this.minZoom;
          this.zoomValue = zoom;
        }

        this.searchTimer = setTimeout(this.resetPageSearch, 1000);
      } catch (ex) {
        //console.error(ex);
      }
    },
    resetPageSearch() {
      if (new Date().getTime() - this.lastZoomKey > 1000) this.zoomKey = "";
    },
    removeFsMarks() {
      this.$emit("drawFs", !this.drawFs);
    },
    toggleMM() {
      this.setMm({ key: "show", value: !this.showMm });
    },
    addBook() {
      let currentTab = this.$store.state.tabs[this.tabId];

      currentTab = JSON.parse(JSON.stringify(currentTab));
      let tab = currentTab;
      tab.id = "win_" + new Date().getTime();
      tab.winWidth = document.body.clientWidth * 0.9;
      tab.winHeight = document.body.clientHeight * 0.9;
      let newTab = Object.assign({}, tabModule);
      newTab.state = function() {
        return tab;
      };
      this.$store.registerModule(["tabs", tab.id], newTab);
      this.$store.state.tabsManager.tabs.push(tab.id);
      //close details window
      this.$store.dispatch("bookPopups/closeBookDetails");
      // this.openbooks.push(this.currentTabId);
    },
    setFullScreen() {
      // const bookOnly = this.$store.state.bookOnlyMode;

      // if (bookOnly) {
      //   this.openFullscreen();
      // } else {

      let currScroll = 0;
      let bookScroller = document.getElementById("the-book" + this.tabId);
      if (bookScroller)
        currScroll = bookScroller.scrollTop / bookScroller.scrollHeight;
      this.toggleFullScreen(currScroll);
      // }
    },
    setSearchTxt(e) {
      this.searchTxt = e.target.value;
    },
    initZoom() {
      this.zoomValue = 100;
    },
    openFullscreen() {
      const elem = document.body;

      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.webkitRequestFullscreen) {
        /* Safari */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) {
        /* IE11 */
        elem.msRequestFullscreen();
      }
    },
    zoomInOut(dir) {
      let zoom = this.zoomValue + 10 * dir;
      if (zoom > this.maxZoom) zoom = this.maxZoom;
      else if (zoom < this.minZoom) zoom = this.minZoom;
      this.zoomValue = zoom;
    },
    bookDetails() {
      this.$emit("book-details", !this.showDetails);
      // this.toggleBookDetailsVisible();
    },
    triggerResize() {
      this.$store.dispatch(`tabs/${this.tabId}/triggerResize`, null, {
        root: true,
      });
    },
    fitToHeight() {
      this.zoomValue = 100;
      this.$store
        .dispatch(`tabs/${this.tabId}/setZoom`, this.zoomValue, {
          root: true,
        })
        .then(() => {
          this.$store
            .dispatch(`tabs/${this.tabId}/setBookFit`, "height", {
              root: true,
            })
            .then(() => {
              this.triggerResize();
            });
        });
    },
    fitToWidth() {
      this.$store.dispatch(`tabs/${this.tabId}/setBookFit`, "width", {
        root: true,
      });
      this.triggerResize();
    },
    zoom() {
      this.$store
        .dispatch(`tabs/${this.tabId}/setBookFit`, "width", {
          root: true,
        })
        .then(() => {
          this.$store.dispatch(`tabs/${this.tabId}/setZoom`, this.zoomValue, {
            root: true,
          });
          setTimeout(this.triggerResize(), 0);
        });
    },
    showUserAdditions() {
      this.$emit("show-add-win");
    },
    addToFavorites() {
      let books = [this.book];

      if (this.bookInFavorites) this.removeFavoriteBook({ books });
      else this.addFavoriteBook({ books });
    },
    print() {
      if (this.showPrint) this.$emit("print", { show: !this.showPrint });
      else if (startPrint(this.tabId) === true)
        this.$emit("print", { show: !this.showPrint });

      return;
    },
    search() {
      this.closeFS = true;
      this.$store.state.freeSearch.inBook = true;
      if (this.searchTxt != "") {
        this.$emit("search", {
          show: true,
          txt: this.searchTxt.trim(),
        });
        setSearchType("classic");
        doFreeSearch(
          this.searchTxt.trim().replace(/[\u0591-\u05C7]/g, ""), //remove niqud,
          [this.book],
          0,
          true,
          this.tabId
        ).then(() => {
          this.$emit("search", {
            show: false,
            txt: this.searchTxt.trim(),
          });
        });
        this.searchTxt = "";
      }
    },
    link() {
      let link;
      let fsData = this.$store.state.tabs[this.tabId].drawData?.fs;
      if (!fsData) fsData = { fs: 0, start: 0, end: 0 };
      link = `${location.protocol}//${window.location.host}/#/book/${
        this.book
      }/p/${this.page}/t/${this.tabId.slice(4)}1234/fs/${fsData.fs}/start/${
        fsData.start
      }/end/${fsData.end}/c`;
      navigator.clipboard.writeText(link).then(() => {
        const notification = {
          type: "success",
          message: this.$t("link.message"),
          timeout: 7000,
          setting: {
            grid: "icon",
            nameIcon: "copy",
            position: "center",
          },
        };
        this.$notify(notification);
      });
    },
    toggleOcr() {
      /*  if (this.OCREnabled == true) {
        this.$emit("ocr", { show: false });
      } else  */
      this.setOCREnable(!this.OCREnabled);
    },
  },
  mounted() {
    // this.$emit("drawMM", this.showMm);
  },
};
</script>
<style lang="scss">
.vue-slider {
  top: 17px;
}
.vue-slider-dot-handle,
.vue-slider-process {
  background-color: var(--custom-color1);
}
.vue-slider-dot-tooltip {
  visibility: visible;
  top: 5px;
}
.vue-slider-dot-tooltip-inner-top::after {
  content: "%";
  top: 0;
  transform: translate(0);
  border: 0;
  font-size: 12px;
  height: 100%;
  width: 10px;
  line-height: 26px;
  right: -5px;
}
.vue-slider-dot-tooltip-inner {
  background-color: transparent;
  color: #000;
  font-size: 12px;
  font-weight: 600;
}

.zoom-act {
  font-size: 22px;
  text-align: center;
  line-height: 21px;
}
.item-fit-w {
  margin-top: -2px;
  margin-bottom: -2px;
}
.toolbox-icon {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.bottom-border {
  border-bottom: #e5e5e5 1px solid;
}
.tools-container .item {
  height: 36px;
  width: 38px;
  line-height: 40px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tools-container .font-icon-bar {
  border-radius: 3px;
  font-size: 35px;
  text-align: center;
  height: 33px;
  width: 33px;
  line-height: 33px;
  color: #626262;
  &:hover:not(.active),
  &:not(.disable):hover {
    color: #000;
  }
  &.active {
    background-color: #0000001f;
    //  border: 1px solid 0000001f;
  }
  &.width {
    transform: rotate(90deg);
    // height: 29px;
    // width: 33px;
  }
}
.font-icon-bar:hover {
  // color: darkgray;
  background-color: #00000014;
  cursor: pointer;
}
.fav.active .font-icon-bar {
  color: var(--custom-color1);
}
.item {
  position: relative;

  .zoom {
    .list-dropdown {
      left: 100% !important;
      top: 0;
      height: 100%;
      width: 200px;
      box-shadow: 0 2px 3px 1px #0000005c;
      background-color: #f7f7f7;
      padding: 10px 10px 10px 5px;
      height: 33px;
      padding: 0px 10px;
      .flex-align-center {
        height: 100%;
      }
    }
  }
  &.width {
    // padding: 5px 2px 5px 5px;
    // padding: 2px 2px 5px 2px;
  }
}
.search {
  .list-dropdown {
    top: 0;
    height: 100%;
    display: flex;
    align-items: center;
    width: 300px;
    left: 100%;
  }
}
.disable {
  opacity: 0.3;
  cursor: default;
}
.icon-hide-results {
  position: absolute;
  top: 17px;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.hide-results {
  .mef-icon {
    font-size: 61px;
  }
}
.mm-icon .font-icon-bar.active {
  color: var(--custom-color1) !important;
