<template>
  <div ref="bookAreaContainer" class="book">
    <template v-if="getCurrentBook > 0">
      <BaseCleanPopup
        v-body
        v-zindex
        :h="40"
        :winh="300"
        ref="ocrwin"
        v-if="OCRResultsVisible && showEditor"
        @resize="reRenderImage()"
        :showfn="reRenderImage"
        @close="
          toggleOCR({ show: false });
          showEditor = false;
        "
        :noneOverlay="true"
      >
        <template v-slot:header>
          <span class="flex flex-align-center h-100">
            {{ $t("bookSelectArea.header") }}
          </span>
        </template>
        <div class="mrg-6 ocr-win-container">
          <!-- <div class="header drag">
          <div style="width:200px">{{ $t("bookSelectArea.header") }}</div>
          <div class="darg-circle"></div>
          <div class="close-popup otz-icon" @click="toggleOCR({ show: false })">F</div>
        </div>-->

          <div v-if="supportsOcr" class="inner-results ocr-content">
            <div class="title-ocr-win">{{ $t("general.text") }} (OCR):</div>
            <textarea
              width="200px"
              class="mrg-l-6"
              height="200px"
              v-model="OCR"
              ref="textArea"
              v-focus
            ></textarea>
            <div class="img-ocr" ref="canvasContainer">
              <div class="title-ocr-win">
                תמונה:
              </div>
              <canvas ref="canvas"></canvas>
            </div>
          </div>
          <div class="buttons flex-bet padd-12">
            <div v-if="supportsOcr">
              <div class="btn btn-outline" @click="copyTxt">
                {{ $t("bookSelectArea.btnCopyTxt") }}
              </div>
            </div>
            <div class="flex-bet">
              <div v-kiosk.remove>
                <BaseButton @click="createMarker">
                  {{ $t("bookSelectArea.btnCrtMarker") }}
                </BaseButton>
              </div>
              <div v-kiosk.remove>
                <BaseButton @click="copyImage" v-kiosk>
                  {{ $t("bookSelectArea.btnCopyImg") }}
                </BaseButton>
              </div>
              <BaseButton @click="copyImageToCB">
                {{ $t("bookSelectArea.btnCopyImgCB") }}
              </BaseButton>
            </div>
          </div>
        </div>
      </BaseCleanPopup>

      <!-- </vue-draggable-resizable> -->
      <!-- צייונים, עמודים וציונים אישיים -->

      <Split
        class="flex-g-1 padd-t-3 book-gutter-container"
        :class="{ 'tsiyunim-close': !openDock }"
        @onDragEnd="onDragEnd"
      >
        <SplitArea
          :class="{ 'full-screen': !openDock }"
          :size="defaultSplitterBook"
          :minSize="200"
        >
          <freeSearchProgress
            class="search-in-book"
            v-if="isSearching"
            :percent="percent"
            :search="fsTxtSearch"
            :seconds="seconds"
            @stop="stopFreeSearch()"
            :message="$t('freeSearchResults.reloadResults')"
          />
          <div class="bookBox">
            <booksHeader
              class="header-book-area"
              :book="getCurrentBook"
              :numPages="getPages.length"
              :page="getCurrentPage"
              :tabId="tabId"
              @font-family="changeFontFamily"
              @font-size="changeFontSize"
              @search="setSearching"
            />

            <div
              class="book-area wide-scroll"
              ref="bookArea"
              @click="openEditorPosition($event)"
              :class="dataClass"
            >
              <div class="close-open-dockable" v-if="!$_mobile">
                <div
                  class="otz-icon icon-open-dockable"
                  v-show="!openDock"
                  @click="toggleDockable"
                >
                  <span class="inner-icon">
                    N
                  </span>
                </div>
                <div
                  class="otz-icon icon-close-dockable"
                  v-show="openDock"
                  @click="toggleDockable"
                >
                  7
                </div>
              </div>
              <txt-book-area
                v-if="txtBook"
                :tabId="tabId"
                :tabData="getTabData"
                :drawFs="drawFs"
                :fontFamily1="fontF"
                :fontSize1="fontS"
                @print="showPrintWin"
              />
              <bookArea
                @addexten="showEditor = true"
                v-if="!txtBook"
                :tabId="tabId"
                :tabData="getTabData"
                @toggleocr="toggleOCR"
                :mm="drawMM"
                :drawFs="drawFs"
                @print="showPrintWin"
                @login="$emit('login')"
              />
            </div>
          </div>
        </SplitArea>
        <component
          :is="SpliterOrTemplate"
          class="open-dockable border-b-light"
          :class="{ close: !openDock, 'mobile-book-info': $_mobile }"
          :size="defaultSplitterBookInfo"
          :minSize="150"
        >
          <!-- <SplitArea
        v-if="!$_mobile"
        class="open-dockable border-b-light"
        :class="{ close: !openDock }"
        :size="defaultSplitterBookInfo"
        :minSize="130"
      > -->
          <!-- pages and fs results of text book -->

          <div
            class="open-dockable"
            :class="{ 'mobile-book-info-w': $_mobile }"
            v-if="txtBook"
          >
            <div
              class="close-open-dockable"
              :class="{ 'mobile-close-dock-container': $_mobile }"
              v-if="$_mobile"
            >
              <div
                class="otz-icon icon-open-dockable"
                :class="{ 'mobile-button-location': $_mobile }"
                v-show="!openDockMode"
                @click="toggleDockable"
              >
                N
              </div>
              <div
                class="otz-icon icon-close-dockable"
                :class="{ 'mobile-button-location': $_mobile }"
                v-show="openDockMode"
                @click="toggleDockable"
              >
                7
              </div>
            </div>
            <BaseDockabaleWindow
              v-show="openDockMobile"
              @click="changeDock"
              dockState="dock"
            >
              <template #content class="list-content">
                <!--  fs results -->

                <bookNavigatingList
                  v-if="getFsResults.length > 0"
                  class="flex-first"
                  dockState="full"
                  showCloseIcon="false"
                  :hasContent="getFsResults.length > 0"
                  :search="false"
                  :open="true"
                  :position="3"
                  @click="setPartOpen"
                >
                  <template #header class="list-header">
                    <div>
                      {{ $t("fsresults.header") }}
                    </div>

                    <div class="icon-circle otz-icon arrow-toggle">o</div>
                  </template>
                  <template #search class="list-header"></template>
                  <template #listContent class="list-container">
                    <div
                      class="flex-wrap flex-center text-book-list"
                      style="width: fit-content;
                  max-width: 100%;"
                    >
                      <fsResultsItem
                        class="item-text-book item"
                        @click="
                          goToFSPage({ page: fs.id, id: fs.id });
                          $store.dispatch(`tabs/${tabId}/setPage`, fs.id);
                        "
                        v-for="fs in getFsResults"
                        :key="fs.id + fs.parentTitle"
                        :id="fs.id"
                        :title="fs.parentTitle + ' ' + fs.subTitle"
                        :active="fs.id == currFsPage"
                        :class="{ 'nav-active': fs.id == currFsPage }"
                      />
                      <!-- <div>
                      <div class="btn">
                        הסתר תוצאות
                      </div>
                    </div> -->
                    </div>
                  </template>
                </bookNavigatingList>

                <!-- pages -->

                <bookNavigatingList
                  class="flex-first"
                  dockState="full"
                  showCloseIcon="false"
                  :hasContent="getPages.length > 0"
                  :search="true"
                  :open="true"
                  :position="0"
                  :scrollto="getCurrentPage"
                  :book="getCurrentBook"
                >
                  <template #header class="list-header"> </template>
                  <template #search class="list-header">
                    <div>
                      <input
                        v-bind:placeholder="$t('pages.placeholder')"
                        @input="searchInList('pages')"
                        ref="inputPages"
                        class="nav-inputs"
                      />
                      <div
                        v-show="searchTxt['pages'] != ''"
                        class="otz-icon no-bg clear-input"
                        @click="clear($refs.inputPages, 'pages')"
                      >
                        F
                      </div>
                    </div>
                  </template>

                  <template #listContent class="list-container">
                    <emptyListMessage
                      :title="$t('general.noFindResults')"
                      v-if="
                        filteredLists2.mainPages.length == 0 &&
                          getPages.length > 0
                      "
                    />
                    <pageNavigatorItem
                      v-for="page in filteredLists2.mainPages"
                      :class="{
                        'nav-active': page.parentId == currentParent,
                        'nav-visited': isPageVisited(page.position),
                      }"
                      :fakeReact="visitedPages"
                      @click="
                        goToPage({ page: page.id });
                        currentParent = page.parentId;
                        $store.dispatch(`tabs/${tabId}/setPage`, page.id);
                      "
                      :key="page.id + page.parentTitle"
                      :id="page.id"
                      :name="page.parentTitle"
                      :position="page.id"
                      :letter="page.parentTitle"
                    />
                  </template>
                </bookNavigatingList>

                <!-- sub pages -->

                <bookNavigatingList
                  class="flex-first"
                  dockState="full"
                  showCloseIcon="false"
                  :hasContent="getPages.length > 0"
                  :search="true"
                  :open="true"
                  :position="0"
                  :scrollto="getCurrentPage"
                  :book="getCurrentBook"
                >
                  <template #header class="list-header"> </template>
                  <template #search class="list-header">
                    <div>
                      <input
                        v-bind:placeholder="$t('pages.placeholder')"
                        @input="searchInList('tsiyunim')"
                        ref="inputPages"
                        class="nav-inputs"
                      />
                      <div
                        v-show="searchTxt['pages'] != ''"
                        class="otz-icon no-bg clear-input"
                        @click="clear($refs.inputPages, 'tsiyunim')"
                      >
                        F
                      </div>
                    </div>
                  </template>

                  <template #listContent class="list-container">
                    <!--  <emptyListMessage
                    :title="$t('general.noFindResults')"
                    v-if="
                      filteredLists2.subPages.length == 0 && getPages.length > 0
                    "
                  /> -->
                    <pageNavigatorItem
                      :class="{
                        'nav-active': p.id == getCurrentPage,
                        'nav-visited': isPageVisited(p.position),
                      }"
                      :fakeReact="visitedPages"
                      @click="
                        goToPage({ page: p.id });
                        $store.dispatch(`tabs/${tabId}/setPage`, p.id);
                      "
                      v-for="p in filteredLists2.subPages"
                      :key="p.id + p.parentTitle"
                      :id="p.id"
                      :name="p.subTitle"
                      :position="p.id"
                      :letter="p.subTitle"
                    />
                  </template>
                </bookNavigatingList>
              </template>
            </BaseDockabaleWindow>
          </div>

          <div
            class="open-dockable"
            :class="{ 'mobile-book-info-w': $_mobile }"
            v-if="!txtBook"
          >
            <div
              class="close-open-dockable"
              :class="{ 'mobile-close-dock-container': $_mobile }"
              v-if="$_mobile"
            >
              <div
                class="otz-icon icon-open-dockable"
                :class="{ 'mobile-button-location': $_mobile }"
                v-show="!openDockMode"
                @click="toggleDockable"
              >
                N
              </div>
              <div
                :class="{ 'mobile-button-location': $_mobile }"
                class="otz-icon icon-close-dockable"
                v-show="openDockMode"
                @click="toggleDockable"
              >
                7
              </div>
            </div>
            <BaseDockabaleWindow
              v-show="openDockMobile"
              @click="changeDock"
              dockState="dock"
            >
              <template #content class="list-content">
                <bookNavigatingList
                  v-if="getFsResults.length > 0"
                  class="flex-first"
                  dockState="full"
                  showCloseIcon="false"
                  :hasContent="getFsResults.length > 0"
                  :search="false"
                  :open="openStatus(3)"
                  :position="3"
                  @click="setPartOpen"
                  @setActive="goToActiveFSResult"
                  :currentItem="currentFSItem"
                  :itemsCount="getFsResults.length"
                >
                  <template #header class="list-header">
                    <div>
                      {{ $t("fsresults.header") }}
                      {{
                        $store.state.tabs[tabId].fsTotal != 0
                          ? "{" + $store.state.tabs[tabId].fsTotal + "}"
                          : ""
                      }}
                    </div>

                    <div class="icon-circle otz-icon arrow-toggle">o</div>
                  </template>
                  <template #search class="list-header"></template>
                  <template #listContent class="list-container">
                    <div class="h-100 flex-column flex-center">
                      <div
                        class="flex-wrap flex-center flex-g-1 align-content-start"
                        style="width: fit-content;
    max-width: 100%; height: calc(100% - 60px);
    overflow: auto;"
                      >
                        <fsResultsItem
                          @click="
                            goToFSPage({ page: fs.position, id: fs.id });
                            currentFSItem = index;
                          "
                          v-for="(fs, index) in getFsResults"
                          :key="fs.id + fs.position"
                          :id="fs.position"
                          :title="fs.position"
                          :active="fs.id == currFsPage"
                        />
                      </div>
                      <!--  <div>
                      <div class="btn mrg-h-6" style="margin: auto;">
                        הסתר תוצאות
                      </div>
                    </div> -->
                    </div>
                  </template>
                </bookNavigatingList>

                <bookNavigatingList
                  v-if="hasTabs"
                  class="flex-first"
                  dockState="full"
                  showCloseIcon="false"
                  :hasContent="getPages.length > 0"
                  :search="true"
                  :open="openStatus(0)"
                  :position="0"
                  :scrollto="getCurrentPage"
                  :book="getCurrentBook"
                  @click="setPartOpen"
                  @setActive="goToActivePage"
                  :currentItem="currentPageItem"
                  :itemsCount="filteredLists.pages.length"
                  :listFocus="pagesListFocus"
                >
                  <template #header class="list-header">
                    <div>{{ $t("pages.header") }}</div>
                    <div class="icon-circle otz-icon arrow-toggle">o</div>
                  </template>
                  <template #search class="list-header">
                    <div>
                      <input
                        v-bind:placeholder="$t('pages.placeholder')"
                        @input="searchInList('pages')"
                        ref="inputPages"
                        class="nav-inputs"
                        @keydown="keyDown"
                      />
                      <div
                        v-show="searchTxt['pages'] != ''"
                        class="otz-icon no-bg clear-input"
                        @click="clear($refs.inputPages, 'pages')"
                      >
                        F
                      </div>
                    </div>
                  </template>

                  <template #listContent class="list-container">
                    <RecycleScroller
                      :buffer="80"
                      style="height:100%;direction:ltr"
                      :items="filteredLists.pages"
                      :item-size="28"
                      v-slot="{ item, index, active }"
                      ref="pagesScroller"
                      @visible="scrollToPage"
                    >
                      <pageNavigatorItem
                        :class="{
                          'nav-active': item.position == getCurrentPage,
                          'nav-visited': isPageVisited(item.position),
                        }"
                        :fakeReact="visitedPages"
                        @click="
                          goToPage({ page: item.position });
                          currentPageItem = index;
                        "
                        :id="item.id"
                        :name="item.name"
                        :position="item.position"
                        :letter="pageLetter(item)"
                        :active="active"
                      />
                    </RecycleScroller>
                    <emptyListMessage
                      :title="$t('general.noFindResults')"
                      v-if="
                        filteredLists.pages.length == 0 && getPages.length > 0
                      "
                    />
                  </template>
                </bookNavigatingList>
                <bookNavigatingList
                  v-if="hasTabs"
                  :hasContent="getTsiyunim.length > 0"
                  :search="true"
                  class="flex-second"
                  :open="openStatus(1)"
                  :position="1"
                  @click="setPartOpen"
                  :scrollto="getCurrentPage"
                  :book="getCurrentBook"
                  :listFocus="TsiyunListFocus"
                  @setActive="goToActiveTsiyun"
                  :currentItem="currentTsiyunItem"
                  :itemsCount="filteredLists.tsiyunim.length"
                >
                  <template #header class="list-header">
                    <div>{{ $t("tsiyunim.header") }}</div>

                    <div class="icon-circle otz-icon arrow-toggle">o</div>
                  </template>
                  <template #search class="list-header">
                    <!-- <input
                    :placeholder="$t('tsiyunim.placeholder')"
                    @input="searchInList('tsiyunim')"
                  /> -->

                    <div>
                      <input
                        :placeholder="$t('tsiyunim.placeholder')"
                        @input="searchInList('tsiyunim')"
                        ref="inputTsiyunim"
                        class="nav-inputs"
                        @keydown="tsiyunKeyDown"
                      />
                      <div
                        v-show="searchTxt['tsiyunim'] != ''"
                        class="otz-icon no-bg clear-input"
                        @click="clear($refs.inputTsiyunim, 'tsiyunim')"
                      >
                        F
                      </div>
                    </div>
                  </template>

                  <template #listContent="data" class="list-container">
                    <RecycleScroller
                      :buffer="80"
                      style="height:100%;direction:ltr"
                      :items="
                        [...tsiyunimType2, ...tsiyunimType1].map((t, i) => {
                          return { idx: i, ...t };
                        })
                      "
                      :item-size="28"
                      v-slot="{ item, index, active }"
                      keyField="idx"
                      ref="tsiyunimScroller"
                      @visible="scrollToTsiyun"
                    >
                      <tsiyunItem
                        v-if="item.type == 2"
                        :class="{
                          'nav-active': item.page == getCurrentPage,
                          'nav-visited': isPageVisited(item.page),
                        }"
                        :fakeReact="visitedPages"
                        :mm="true"
                        @click="
                          goToTsiyun(item.page);
                          currentTsiyunItem = index;
                        "
                        @open-mm="
                          openMareMakom(
                            item.catBook,
                            item.catPage,
                            item.CpageName
                          )
                        "
                        :page="item.page"
                        :id="item.id"
                        :title="item.title"
                        :isUser="false"
                        :currentPage="getCurrentPage"
                        :tabId="tabId"
                        :active="active"
                      />

                      <tsiyunItem
                        v-else
                        :class="{
                          'nav-active':
                            item.page == getCurrentPage ||
                            data.active == item.page,
                          'nav-visited': isPageVisited(item.page),
                        }"
                        :fakeReact="visitedPages"
                        @click="
                          goToTsiyun(item.page);
                          currentTsiyunItem = index;
                        "
                        :page="item.page"
                        :id="item.id"
                        :title="item.title"
                        :isUser="false"
                        :currentPage="getCurrentPage"
                        :tabId="tabId"
                        :active="active"
                      />
                    </RecycleScroller>

                    <!--  <tsiyunItem
                      :class="{ 'nav-active': tsiyun.page == getCurrentPage }"
                      :mm="true"
                      @click="goToTsiyun(tsiyun.page)"
                      @open-mm="
                        openMareMakom(
                          tsiyun.catBook,
                          tsiyun.catPage,
                          tsiyun.CpageName
                        )
                      "
                      v-for="(tsiyun, i) in tsiyunimType2"
                      :key="tsiyun.catPage + tsiyun.title + i"
                      :page="tsiyun.page"
                      :id="tsiyun.id"
                      :title="tsiyun.title"
                      :isUser="false"
                      :currentPage="getCurrentPage"
                      :tabId="tabId"
                    /> -->
                    <!--   <div
                      class="seperator"
                      v-if="
                        tsiyunimType1.length > 0 && tsiyunimType2.length > 0
                      "
                    ></div>
                    <tsiyunItem
                      :class="{
                        'nav-active':
                          tsiyun.page == getCurrentPage ||
                          data.active == tsiyun.page,
                      }"
                      @click="
                        goToTsiyun(tsiyun.page);
                        currentTsiyunItem = index;
                      "
                      v-for="(tsiyun, index) in tsiyunimType1"
                      :key="tsiyun.page + tsiyun.title + index"
                      :page="tsiyun.page"
                      :id="tsiyun.id"
                      :title="tsiyun.title"
                      :isUser="false"
                      :currentPage="getCurrentPage"
                      :tabId="tabId"
                    /> -->
                  </template>
                </bookNavigatingList>

                <bookNavigatingList
                  :hasContent="true"
                  :search="true"
                  class="flex-third"
                  :open="openStatus(2)"
                  :position="2"
                  @click="setPartOpen"
                  :scrollto="getCurrentPage"
                  :itemsCount="getUserTsiyuns.length"
                >
                  <template #header class="list-header">
                    <userTziyunsHeader
                      v-kiosk
                      :title="$t('tsiyunim.myheader')"
                      :tabId="tabId"
                      v-if="!$_mobile"
                    />

                    <div class="icon-circle otz-icon arrow-toggle">o</div>
                  </template>
                  <template #search class="list-header">
                    <div v-show="getUserTsiyuns.length > 0">
                      <input
                        v-bind:placeholder="$t('tsiyunim.myplaceholder')"
                        @input="searchInList('myTsiyunim')"
                        ref="inputMyTz"
                        class="nav-inputs"
                      />
                      <div
                        v-show="searchTxt['myTsiyunim'] != ''"
                        class="otz-icon no-bg clear-input"
                        @click="clear($refs.inputMyTz, 'myTsiyunim')"
                      >
                        F
                      </div>
                    </div>
                  </template>

                  <template
                    #listContent
                    class="list-container"
                    v-if="!$_mobile"
                  >
                    <userTziyunsInnerEmpty
                      v-kiosk
                      v-if="getUserTsiyuns.length == 0"
                      :tabId="tabId"
                    />
                    <emptyListMessage
                      :title="$t('general.noFindResults')"
                      v-if="
                        filteredLists.userTsiyunim.length == 0 &&
                          getUserTsiyuns.length > 0
                      "
                    />
                    <tsiyunItem
                      :class="{
                        'nav-active': tsiyun.page == getCurrentPage,
                        'nav-visited': isPageVisited(tsiyun.page),
                      }"
                      :fakeReact="visitedPages"
                      @click="goToItemPage(tsiyun)"
                      v-for="tsiyun in filteredLists.userTsiyunim"
                      :key="tsiyun.id + tsiyun.title"
                      :page="tsiyun.page"
                      :id="tsiyun.id"
                      :title="tsiyun.title"
                      :isUser="true"
                      :currentPage="getCurrentPage"
                      :tabId="tabId"
                    />
                  </template>
                </bookNavigatingList>
              </template>
            </BaseDockabaleWindow>
          </div>

          <!-- תוספות אישיות: מפתחות, הערות,הדגשות -->
          <!-- </SplitArea> -->
        </component>
      </Split>

      <vue-draggable-resizable
        :x="40"
        :y="37"
        v-zindex.fixed
        :parent="true"
        :resizable="false"
        v-if="showUserAdditionsWin && !txtBook"
        :class-name="dock[1]"
        drag-handle=".drag"
        class="extensions-dock personal-ex-dock"
        :class="{ pinned: hasPin }"
      >
        <BaseDockabaleWindow
          @click="changeDock"
          @close="closeUserExtens"
          @pin="togglePin"
          dockState="dock"
          class="personal-extensions-dock"
          :title="true"
        >
          <template #header class="list-header flex drag">
            <div>{{ $t("personalExtensions.titleHader") }}</div>

            <div
              class="icon-del icon-close icon-circle otz-icon"
              @click="togglePersonalExtVisible"
            >
              F
            </div>
          </template>

          <template #content class="list-content">
            <bookNavigatingList
              class="flex-first comment-dock extens-dock"
              dockState="full"
              :hasContent="getPages.length > 0"
              :search="true"
              :open="extensOpenIndex == 1"
              :position="0"
              @click="setPartOpen1(1)"
            >
              <template #header="{ clicked }" class="list-header">
                <!-- <div>{{ $t("personalExtensions.commentTitle") }}</div> -->
                <userExtensionsHeader
                  :title="$t('personalExtensions.commentTitle')"
                  type="comment"
                  @add-exten="addExtenstions"
                  @click="clicked()"
                />

                <div class="icon-circle otz-icon arrow-toggle">o</div>
              </template>
              <template #search class="list-header">
                <!-- <input
                v-bind:placeholder="$t('comment.placeholder')"
                @input="searchInList('comment')"
              /> -->
                <div>
                  <input
                    v-bind:placeholder="$t('comment.placeholder')"
                    @input="searchInList('comment')"
                    ref="inputComment"
                    class="nav-inputs"
                  />
                  <div
                    v-show="searchTxt['comment'] != ''"
                    class="otz-icon no-bg clear-input"
                    @click="clear($refs.inputComment, 'comment')"
                  >
                    F
                  </div>
                </div>
              </template>

              <template #listContent class="list-container">
                <emptyListMessage
                  :title="$t('general.noFindResults')"
                  v-if="
                    filteredLists.comment.length == 0 &&
                      userCommentsList.length > 0
                  "
                />
                <emptyListMessage
                  type="comment"
                  @add-exten="addExtenstions"
                  :title="$t('personalExtensions.noComments')"
                  v-if="userCommentsList.length == 0"
                />
                <!-- <pageNavigatorItem
                v-for="comment in filteredLists.comment"
                :key="comment.id"
                :item="comment"
                @click="goToPage({ page: comment.pageId })"
              />-->
                <!--  <DynamicScroller
                  :items="filteredLists.comment"
                  :min-item-size="70"
                  style="height:100%;direction:ltr"
                >
                  <template v-slot="{ item, index, active }">
                    <DynamicScrollerItem
                      :item="item"
                      :active="active"
                      :size-dependencies="[item.data]"
                      :data-index="index"
                    >
                      <extensPageItem
                        :tabId="tabId"
                        :item="item"
                        type="comment"
                        @click="goToItemPage(item)"
                      />
                    </DynamicScrollerItem>
                  </template>
                </DynamicScroller> -->
                <extensPageItem
                  :tabId="tabId"
                  v-for="comment in filteredLists.comment"
                  :key="comment.id + comment.data"
                  :item="comment"
                  type="comment"
                  @click="goToItemPage(comment)"
                />
              </template>
            </bookNavigatingList>
            <!-- מפתחות -->
            <bookNavigatingList
              class="flex-first key-dock extens-dock"
              dockState="full"
              :hasContent="getPages.length > 0"
              :search="true"
              :open="extensOpenIndex == 2"
              :position="2"
              @click="setPartOpen1(2)"
            >
              <template #header class="list-header">
                <!-- <div>{{ $t("personalExtensions.commentTitle") }}</div> -->
                <userExtensionsHeader
                  :title="$t('personalExtensions.keyTitle')"
                  type="key"
                  @add-exten="addExtenstions"
                />

                <div class="icon-circle otz-icon arrow-toggle">o</div>
              </template>
              <template #search class="list-header">
                <!-- <input
                v-bind:placeholder="$t('key.placeholder')"
                @input="searchInList('key')"
              /> -->
                <div>
                  <input
                    v-bind:placeholder="$t('key.placeholder')"
                    @input="searchInList('key')"
                    ref="inputKey"
                    class="nav-inputs"
                  />
                  <div
                    v-show="searchTxt['key'] != ''"
                    class="otz-icon no-bg clear-input"
                    @click="clear($refs.inputKey, 'key')"
                  >
                    F
                  </div>
                </div>
              </template>

              <template #listContent class="list-container">
                <emptyListMessage
                  :title="$t('general.noFindResults')"
                  v-if="filteredLists.key.length == 0 && userKeyList.length > 0"
                />
                <emptyListMessage
                  type="key"
                  @add-exten="addExtenstions"
                  :title="$t('personalExtensions.noKeys')"
                  v-if="userKeyList.length == 0"
                />

                <extensPageItem
                  :tabId="tabId"
                  v-for="key in filteredLists.key"
                  :key="key.id + key.key + key.value"
                  :item="key"
                  type="key"
                  @click="goToItemPage(key)"
                />
              </template>
            </bookNavigatingList>
            <!-- הדגשות -->
            <bookNavigatingList
              class="flex-first marker-dock extens-dock"
              dockState="full"
              :hasContent="getPages.length > 0"
              :search="false"
              :open="extensOpenIndex == 3"
              :position="3"
              @click="setPartOpen1(3)"
            >
              <template #header class="list-header">
                <!-- <div>{{ $t("personalExtensions.commentTitle") }}</div> -->
                <userExtensionsHeader
                  :title="$t('personalExtensions.markerTitle')"
                  type="marker"
                  @add-exten="addExtenstions"
                />

                <div class="icon-circle otz-icon arrow-toggle">o</div>
              </template>
              <!-- <template #search class="list-header">
              <input
                v-bind:placeholder="$t('marker.placeholder')"
                @input="searchInList('key')"
              />
            </template>-->
              <template #listContent class="list-container">
                <!-- <emptyListMessage
                :title="$t('general.noFindResults')"
                v-if="filteredLists.key.length == 0 && userKeyList.length > 0"
              />-->
                <emptyListMessage
                  :title="$t('personalExtensions.noMarkers')"
                  type="marker"
                  @add-exten="addExtenstions"
                  v-if="userMarkersList.length == 0"
                />

                <extensPageItem
                  :tabId="tabId"
                  v-for="marker in filteredLists.marker"
                  :key="marker.id + marker.pageId"
                  :item="marker"
                  type="marker"
                  @click="goToItemPage(marker)"
                />
              </template>
            </bookNavigatingList>

            <!-- קישורים פנימיים -->

            <bookNavigatingList
              v-if="!$store.state.bookOnlyMode"
              class="flex-first marker-dock extens-dock"
              dockState="full"
              :hasContent="getPages.length > 0"
              :search="false"
              :open="extensOpenIndex == 4"
              :position="4"
              @click="setPartOpen1(4)"
            >
              <template #header class="list-header">
                <userExtensionsHeader
                  :title="$t('personalExtensions.LinksTitle')"
                  type="link"
                  @add-exten="addExtenstions"
                />
                <div class="icon-circle otz-icon arrow-toggle">o</div>
              </template>

              <template #listContent class="list-container">
                <emptyListMessage
                  type="link"
                  @add-exten="addExtenstions"
                  :title="$t('personalExtensions.noLinks')"
                  v-if="filteredLists.links && filteredLists.links.length == 0"
                />

                <extensPageItem
                  :tabId="tabId"
                  v-for="link in uniqueFilteredLinks"
                  :key="link.id + '' + link.bookId"
                  :item="link"
                  type="link"
                  @click="(item) => goToItemPage(item, 'link')"
                />
              </template>
            </bookNavigatingList>
          </template>
        </BaseDockabaleWindow>
      </vue-draggable-resizable>
      <div class="tool-box" v-mobile.hide>
        <toolBoxContainer
          @drawMM="setDrawMM"
          :book="getCurrentBook"
          :page="getCurrentPage"
          :showDetails="showDetails"
          :showAdds="showUserAdditionsWin"
          :drawFs="drawFs"
          @book-details="showDetailsWin"
          @print="showPrintWin"
          @ocr="toggleOCR"
          @search="setSearching"
          :tabId="tabId"
          @show-add-win="showAddWindow"
          @drawFs="setDrawFs"
        />
      </div>

      <BaseCleanPopup
        v-body
        :winh="hPrint"
        :winw="440"
        v-if="showPrint"
        v-zindex
        :h="40"
        @close="closePrintWin()"
        :noneOverlay="true"
      >
        <template v-slot:header>
          <span class="flex flex-align-center h-100">{{
            $t("bookPrint.header")
          }}</span>
        </template>
        <printBook
          :pages="getPages"
          :bookid="getCurrentBook"
          @print="showPrintWin"
          @loading="fitSizePrintWin"
          :page="getCurrentPage"
          :book="getCurrentBook"
          drag-handle=".drag"
          class="print-book"
          :tabId="tabId"
          @close="closePrintWin()"
          :drawFs="drawFs"
          :from="printFrom"
          :to="printTo"
        />
      </BaseCleanPopup>

      <BaseCleanPopup
        v-body
        v-if="openEditor && type != 'marker' && showEditor"
        v-zindex
        @close="closeExtens"
        :h="37"
        :mini_title="miniTitle"
        :mini="true"
        :winh="hUserExtens"
        :winw="wUserExtens"
        :noneOverlay="true"
      >
        <template v-slot:header>
          <div class="flex-center-vh">
            {{
              type == "comment"
                ? $t("comments.comment")
                : type == "link"
                ? $t("links.link")
                : $t("keys.key")
            }}
          </div>
        </template>

        <add-user-exten
          :tabId="tabId"
          v-if="type == 'comment'"
          @close="
            closeEditor();
            showEditor = false;
          "
          :x="getOffset.xPx"
          :y="getOffset.yPx"
          :book="getCurrentBook"
          :page="getPageEditor"
        />
        <add-user-key
          :tabId="tabId"
          @close="
            closeEditor();
            showEditor = false;
          "
          :x="getOffset.xPx"
          :y="getOffset.yPx"
          :book="getCurrentBook"
          :page="getPageEditor"
          v-else-if="type == 'key'"
        />
        <add-user-link
          :tabId="tabId"
          :firstonly="addingLink"
          @close="
            closeExtens();
            showEditor = false;
          "
          :x="getOffset.xPx"
          :y="getOffset.yPx"
          :book="getCurrentBook"
          :page="getPageEditor"
          v-else-if="type == 'link'"
          @gotoitempage="(item) => goToItemPage(item, 'link')"
        />
      </BaseCleanPopup>

      <openBookButton
        v-if="hasTabs & !isBookPage"
        class="open-book-btn"
        :book="getCurrentBook"
        :page="getCurrentPage"
      />
    </template>
    <newTab v-else> </newTab>
  </div>
</template>
<script>
import { mapGetters, mapState, mapActions } from "vuex";
import newTab from "./newTab.vue";
import bookArea from "@/components/book/bookArea.vue";
import booksHeader from "@/components/book/booksHeader.vue";
import printBook from "@/components/book/printBook.vue";
import bookDetails from "@/components/book/bookDetails.vue";
import tsiyunItem from "@/components/book/tsiyunItem.vue";
import toolBoxContainer from "@/components/book/toolBoxContainer.vue";
import pageNavigatorItem from "@/components/book/pageNavigatorItem.vue";
import fsResultsItem from "@/components/book/fsResultsItem.vue";
const addUserExten = () => import("./addUserExten.vue");
import addUserKey from "./addUserKey.vue";
import addUserLink from "./addUserLink.vue";
import bookNavigatingList from "@/components/book/bookNavigatingList.vue";
import openBookButton from "@/components/book/openBookButton.vue";
import extensPageItem from "@/components/book/extensPageItem.vue";
import userTziyunsInnerEmpty from "@/components/book/userTziyunsInnerEmpty.vue";
import emptyListMessage from "@/components/book/emptyListMessage.vue";
import userTziyunsHeader from "@/components/book/userTziyunsHeader.vue";
import userExtensionsHeader from "@/components/book/userExtensionsHeader.vue";
import freeSearchProgress from "@/components/listBookArea/freeSearchBarContainer/freeSearchProgress.vue";
import txtBookArea from "@/components/book/txtBookArea.vue";
import { getOCR } from "@/services/bookToolBoxMethods.js";
import config from "@/config/general";
import VueSplit from "vue-split-panel";
import BaseTemplate from "@/components/BaseComponents/BaseTemplate.vue";
import mixBook from "@/mixing/mixBook.js";
import { getImageObject } from "@/services/bookData.js";

export default {
  mixins: [mixBook],
  props: ["tabId"],
  data() {
    return {
      showEditor: false,
      openDockMobile: true,
      currentPageItem: 0,
      currentTsiyunItem: 0,
      currentFSItem: 0,
      pagesListFocus: false,
      TsiyunListFocus: false,
      tabData: {},
      searchTxt: {
        pages: "",
        tsiyunim: "",
        myTsiyunim: "",
        comment: "",
        key: "",
        marker: "",
      },
      showUserAdditionsWin: false,
      openDock: true,
      dock: ["dock", "float"],
      activeItem: [0, 0, 0, 0],
      navigatePartsOpen: [true, true, false, true],
      extensOpenIndex: 1,
      offsetEditor: {
        x: 0,
        y: 0,
      },
      currFsPage: 0,
      showDetails: false,
      showPrint: false,
      OCR: "",
      OCRRect: {},
      markerRect: {},
      isSearching: false,
      seconds: 0,
      percent: 0,
      fsTxtSearch: "",
      drawMM: false,
      tempOCRImg: null,
      tempCanvas: null,
      tempCanvasCtx: null,
      lastCrop: null,
      drawFs: true,
      currentParent: 0,
      fontF: "",
      fontS: 4,
      hasPin: false,
      hPrint: 440,
      printFrom: -1,
      printTo: -1,
    };
  },
  components: {
    openBookButton,
    bookArea,
    tsiyunItem,
    toolBoxContainer,
    pageNavigatorItem,
    extensPageItem,
    booksHeader,
    bookNavigatingList,
    userTziyunsHeader,
    printBook,
    bookDetails,
    userTziyunsInnerEmpty,
    emptyListMessage,
    userExtensionsHeader,
    fsResultsItem,
    addUserExten,
    addUserKey,
    addUserLink,
    freeSearchProgress,
    txtBookArea,
    newTab,
  },
  watch: {
    searching: function(val) {
      this.isSearching = val && this.inBook;
    },
    finishedFs: function(val) {
      if (val) {
        this.setSearching(false);
        this.$store.state.tabs[this.tabId].fsFinished = false;
      }
    },
    getCurrentBook: function() {
      this.$store.state.tabs[this.tabId].fsTotal = 0;
      this.drawFs = true;
      this.$store.state.tabsManager.scrollToFsResult = true;
      Object.keys(this.searchTxt).forEach((key) => (this.searchTxt[key] = ""));
      this.clearAllInputs();
      this.$store.dispatch(`tabs/${this.tabId}/setCustomTitle`, "");
    },
    getCurrentDrawData: function() {
      this.drawFs = true;
      this.$store.state.tabsManager.scrollToFsResult = true;
    },
    scrollTo: function(newV) {
      if (this.txtBook) this.currFsPage = newV;
    },
    getCurrentPage: function(val) {
      this.scrollToPage();
      this.scrollToTsiyun();
    },
  },
  computed: {
    ...mapState("freeSearch", ["searching", "inBook"]),
    ...mapGetters("createBook", ["getDrawOcrAction"]),
    ...mapState("bookPopups", ["OCREnabled"]),
    ...mapState("personalAdditionsTabs", ["addingLink"]),
    ...mapGetters("personalAdditionsTabs", ["getPersonalAdditions"]),
    ...mapGetters("tabsManager", ["isWindow"]),
    ocrMode() {
      return this.userSettings.settings.book_selection_default &&
        this.userSettings.settings.book_selection_default == 1
        ? true
        : false;
    },
    visitedPages() {
      return this.$store.state.books.visitedPages[this.getCurrentBook] || [];
    },
    finishedFs() {
      return this.$store.state.tabs[this.tabId].fsFinished;
    },
    scrollTo() {
      return this.$store.state.tabs[this.tabId].scrollTo;
    },
    openDockMode() {
      if (this.$_mobile) return this.openDockMobile;
      return this.openDock;
    },
    SpliterOrTemplate() {
      if (this.$_mobile) {
        return BaseTemplate;
      }
      return VueSplit.SplitArea;
    },
    txtBook() {
      return this.$store.state.tabs[this.tabId].txtBook;
    },
    supportsOcr() {
      return this.otzarBook.type(this.getTabData.book) != "custom";
    },
    getTabData() {
      return this.$store.getters["tabs/" + this.tabId + "/getTabData"];
    },
    getFsResults() {
      let react = this.$store.state.tabs[this.tabId].fsChange;
      return this.getTabData.pages
        ? this.getTabData.pages.filter((p) => p.words)
        : [];
    },
    getPages() {
      return this.getTabData.pages || [];
    },
    getCurrentPage() {
      return this.$store.state.tabs[this.tabId].page;
    },
    getCurrentBook() {
      return this.$store.state.tabs[this.tabId].book.toString();
    },
    getCurrentDrawData() {
      return this.$store.state.tabs[this.tabId].drawData;
    },
    getTsiyunim() {
      return this.getTabData.tsiyunim || [];
    },
    getUserTsiyuns() {
      return this.getTabData.usertsiyuns || [];
    },
    ...mapState("bookPopups", [
      "printBookVisibility",
      "bookDetailsVisibility",
      "bookDetailsFromListBook",
      "personalExtVisibility",
    ]),
    ...mapGetters("tabsManager", [
      "getTabs",
      "getAllTabs",
      "getMaxShown",
      "getActivePage",
      "getActiveTsiyun",
      "getActiveUserTsiyun",
    ]),
    ...mapGetters("bookPopups", ["getSelectBookDetails"]),
    ...mapGetters("bookPersonalExtensions", [
      "getUserCommentsList",
      "getUserKeysList",
      "getUserMarkersList",
      "getUserLinksList",
      "getOpenEditor",
      "getAddExtensType",
      "crtExtentionsMode",
      "getOffset",
      "getPageEditor",
    ]),

    OCRResultsVisible() {
      return this.$store.state.bookPopups.OCRResultsVisible;
    },
    x() {
      let wWindow = this.$refs.bookAreaContainer.clientWidth;
      let left = wWindow / 2 - 220;
      return left;
    },
    y() {
      // return 5;
      let hHeight = this.$refs.bookAreaContainer.clientHeight;
      let top = hHeight / 2 - 250;
      return top;
    },
    openEditor() {
      return (
        this.$store.getters["personalAdditionsTabs/getOpenEditor"] || false
      );
      // return this.getOpenEditor;
    },
    type() {
      return this.getAddExtensType;
    },
    addExtensType() {
      return this.getAddExtensType;
    },
    dataClass() {
      if (this.crtExtentionsMode) return this.type + "-cursor";
      return "";
    },
    personalAdditions() {
      let adds = this.getPersonalAdditions[this.getTabData.book];
      return adds || { comments: [], keys: [], markers: [], links: [] };
    },
    userCommentsList() {
      return this.personalAdditions.comments || [];
      return this.getUserCommentsList.filter(
        (c) => c.bookId == this.getCurrentBook
      );
    },
    userKeyList() {
      return this.personalAdditions.keys || [];
      return this.getUserKeysList.filter(
        (k) => k.bookId == this.getCurrentBook
      );
    },
    userMarkersList() {
      return this.personalAdditions.markers || [];
      return this.getUserMarkersList.filter(
        (m) => m.bookId == this.getCurrentBook
      );
    },
    userLinksList() {
      return this.personalAdditions.links || [];
      return this.getUserLinksList.filter(
        (l) => l.bookId == this.getCurrentBook
      );
    },
    miniTitle() {
      return (
        this.$t(this.addExtensType + "s." + this.addExtensType) +
        " " +
        this.$t("general.new")
      );
    },
    uniqueFilteredLinks() {
      let unq = [];
      this.filteredLists.links.forEach((l) => {
        if (unq.filter((ul) => ul.linkTo == l.id)[0]) return;
        unq.push(l);
      });
      return unq;
    },
    tsiyunimType1() {
      try {
        return this.filteredLists.tsiyunim.filter((t) => t.type == 1);
      } catch (ex) {
        return [];
      }
    },
    tsiyunimType2() {
      try {
        return this.filteredLists.tsiyunim.filter((t) => t.type == 2);
      } catch (ex) {
        return [];
      }
    },
    filteredLists2() {
      let search = this.searchTxt;
      let mainPages = this.getPages.filter((page) => {
        try {
          return page.parentTitle.includes(search.pages) && page.subId == 0;
        } catch (ex) {
          console.error(ex);
          return false;
        }
      });
      let subPages = this.getPages.filter((page) => {
        try {
          return (
            page.subTitle.includes(search.tsiyunim) &&
            page.parentId == this.currentParent
          );
        } catch (ex) {
          return false;
        }
      });

      let ret = { mainPages, subPages };
      return ret;
    },

    filteredLists() {
      let search = this.searchTxt;
      let pages = this.getPages.filter((page) => {
        if (search.pages === "") return page;
        try {
          if (
            !page.pagedata ||
            page.pagedata[0].letter == "" ||
            page.pagedata[0].letter == null
          )
            return page.position.toString().includes(search.pages);
          else return page.pagedata[0].letter.includes(search.pages);
        } catch (ex) {
          console.error(ex.message, ex);
          return false;
        }
      });

      let tsiyunim = this.getTsiyunim.filter((page) => {
        try {
          return page.title.includes(search.tsiyunim);
        } catch (ex) {
          console.error(ex.message, ex);
          return false;
        }
      });

      let userTsiyunim = this.getUserTsiyuns.filter((page) =>
        page.title.includes(search.myTsiyunim)
      );

      let comment = this.userCommentsList.filter((page) =>
        page.data.includes(search.comment)
      );
      let key = this.userKeyList.filter((page) => {
        return page.value.includes(search.key);
      });
      let marker = this.userMarkersList;
      let links = this.userLinksList;
      let ret = { pages, tsiyunim, userTsiyunim, comment, key, marker, links };
      return ret;
    },
    getBookTabs() {
      let tabs = this.getTabs;
      return tabs;
    },
    hasTabs() {
      return this.getAllTabs.length > 0;
    },
    hasTsiyunim() {
      let tsiyunim = this.getTsiyunim;
      return tsiyunim.length > 0;
    },
    isBookPage() {
      return this.$route.name == "bookOnly";
    },
    wUserExtens() {
      // return this.type == "comment" ? 380 : 240;

      switch (this.type) {
        case "comment":
          return 380;
          break;
        case "key":
          return 240;
          break;
        case "link":
          return 380;
          break;
      }
    },
    hUserExtens() {
      // return this.type == "comment" ? 330 : 240;
      switch (this.type) {
        case "comment":
          return 330;
          break;
        case "key":
          return 240;
          break;
        case "link":
          return 280;
          break;
      }
    },
    defaultSplitterBook() {
      if (this.$_mobile) return 100;
      return 100 - this.defaultSplitterBookInfo;
    },
    defaultSplitterBookInfo() {
      //why different  value for window?
      /*  if (this.isWindow(this.tabId)) {
        return 25;
      } */
      let wWindow = window.winSizes.innerWidth;
      let wList;

      return (wList = (250 / wWindow) * 100);
    },
  },
  methods: {
    isPageVisited(page) {
      return this.visitedPages.includes(page);
    },
    scrollToPage() {
      if (!this.$refs.pagesScroller) return;
      if (!isInViewport(this.$refs.pagesScroller.$el)) return;
      const pageElem = this.$el.querySelectorAll(
        '.page-nav-item[position="' + this.getCurrentPage + '"][active=true]'
      );

      if (pageElem.length > 0)
        pageElem[0].scrollIntoViewIfNeeded({ behavior: "smooth" });
      else this.$refs.pagesScroller.scrollToItem(this.getCurrentPage - 1);
    },
    scrollToTsiyun() {
      if (!this.$refs.tsiyunimScroller) return;
      if (!isInViewport(this.$refs.tsiyunimScroller.$el)) return;
      const pageElem = this.$el.querySelectorAll(
        '.tsiyun-item[position="' +
          this.getCurrentPage +
          '"][user="1"][active=true]'
      );

      if (pageElem.length > 0)
        pageElem[0].scrollIntoViewIfNeeded({ behavior: "smooth" });
      else {
        let inx = this.filteredLists.tsiyunim.findIndex(
          (element) => element.page == this.getCurrentPage
        );
        if (inx) this.$refs.tsiyunimScroller.scrollToItem(inx);
      }
    },
    keyDown(event) {
      switch (event.code) {
        case "ArrowDown":
          this.inputDown();

          break;
      }
    },
    tsiyunKeyDown(event) {
      switch (event.code) {
        case "ArrowDown":
          this.inputDownTsiyunim();

          break;
      }
    },
    ...mapActions("bookPopups", [
      "setOCREnable",
      "closePersonalExt",
      "togglePersonalExtVisible",
    ]),
    ...mapActions("tabsManager", [
      "setCurrentScrollTo",
      "setActiveTsiyun",
      "setActivePage",
      "setActiveUserTsiyun",
      "setResizeToggle",
      "toggleSplitter",
    ]),
    ...mapActions("bookPersonalExtensions", [
      // "closeEditorWin",
      "startCreateExtentionMode",
      "stopCreatModeAllExtantions",
      "setAddEtensType",
      "closeExtens",
    ]),
    ...mapActions("personalAdditionsTabs", ["closeEditorWin"]),
    ...mapActions("createBook", ["setOcrOutput"]),
    printPage() {
      alert("P");
    },
    clearAllInputs() {
      let inputs = document.getElementsByClassName("nav-inputs");
      for (let i = 0; i < inputs.length; i++) {
        inputs[i].value = "";
      }
    },
    inputDown() {
      this.currentPageItem = 0;
      this.goToActivePage(this.currentPageItem);
      this.pagesListFocus = !this.pagesListFocus;
    },
    inputDownTsiyunim() {
      this.currentTsiyunItem = 0;
      this.goToActiveTsiyun(this.currentTsiyunItem);
      this.TsiyunListFocus = !this.TsiyunListFocus;
    },

    setDrawFs(value) {
      this.drawFs = value;
    },
    showAddWindow(value) {
      value == undefined
        ? (this.showUserAdditionsWin = !this.showUserAdditionsWin)
        : (this.showUserAdditionsWin = value);
    },
    showMarkersList() {
      this.showUserAdditionsWin = true;
      setTimeout(() => (this.extensOpenIndex = 3), 0);
    },
    setDrawMM(draw) {
      this.drawMM = draw;
    },

    setSearching({ show, txt }) {
      if (show) {
        this.fsTxtSearch = txt;
        this.interval = setInterval(() => {
          this.seconds += 0.1;
          this.percent += 5;
          if (this.percent > 100) {
            this.percent = 0;
          }
        }, 100);
      } else {
        this.seconds = 0;
        this.percent = 0;
        clearInterval(this.interval);
      }
      this.isSearching = show;
    },
    stopFreeSearch() {
      this.setSearching(false);
    },
    reRenderImage() {
      this.$refs.ocrwin.$el.click();
      if (!this.lastCrop) return;
      setTimeout(() => {
        this.drawImgCrop(
          this.lastCrop.img,
          this.lastCrop.cWidth,
          this.lastCrop.cHeight,
          this.lastCrop.startX,
          this.lastCrop.startY,
          this.lastCrop.imgRatio
        );
      }, 0);
    },
    toggleOCR({
      show,
      img,
      x,
      y,
      width,
      height,
      pageData,
      cWidth,
      cHeight,
      startX,
      startY,
      imgRatio,
      markerRect,
      ocrRectInPx,
    }) {
      if (!show) {
        this.tempCanvasCtx.fillStyle = "white";
        this.tempCanvasCtx.fillRect(
          0,
          0,
          this.tempCanvas.width,
          this.tempCanvas.height
        );
      }
      this.lastCrop = {
        img,
        cWidth,
        cHeight,
        startX,
        startY,
        imgRatio,
      };
      this.showEditor = true;
      this.OCR = "";
      this.OCRRect = { x, y, width, height, pageData };
      this.markerRect = markerRect;
      if (!this.getDrawOcrAction)
        this.$store.state.bookPopups.OCRResultsVisible = show;
      this.setOCREnable(this.ocrMode);
      if (show) {
        //set rect of drawed part
        this.$store.dispatch(`tabs/${this.tabId}/setRects`, {
          page: this.OCRRect.pageData.position,
          rect: { x, y, width, height },
        });

        if (!this.supportsOcr) return;
        getOCR({
          bookId: this.getCurrentBook,
          pageId: this.OCRRect.pageData.id,
          x: ocrRectInPx.x,
          y: ocrRectInPx.y,
          width: ocrRectInPx.width,
          height: ocrRectInPx.height,
          startWord: pageData.pagedata[0].firstWord,
          numWord: pageData.pagedata[0].numWords,
        }).then((ocr) => {
          if (Number.isInteger(ocr.data)) {
            this.OCR =
              this.$t("general.noFindResults") +
              ", " +
              this.$t("errors.code") +
              ": " +
              ocr.data;
          } else if (ocr.data === "NA")
            //book is not allowed to OCR
            this.OCR = this.$t("ocr.notPossibleConvertOcr");
          else {
            try {
              this.OCR = ocr.data.join(" ");
            } catch (ex) {
              console.error("no ocr recieved: ", ocr);
              this.OCR = this.$t("general.noFindResults");
            }
            if (this.OCR.trim() == "")
              this.OCR = this.$t("general.noFindResults");
          }
          let croppedImg;
          setTimeout(async () => {
            croppedImg = await this.drawImgCrop(
              img,
              cWidth,
              cHeight,
              startX,
              startY,
              imgRatio
            );

            //ocr for user book
            if (this.getDrawOcrAction) {
              let payload = {
                rect: { x, y, width, height },
                ocr: this.OCR,
                img: croppedImg,
              };

              this.setOcrOutput(payload);
              this.$store.dispatch("createBook/setOcrAction", false);
              //     this.$store.state.bookPopups.OCRResultsVisible = false;
            }
          }, 500);
        });
      }
    },
    async drawImgCrop(img, cWidth, cHeight, startX, startY, imgRatio) {
      try {
        let bookId = this.getCurrentBook;
        let pageId = this.OCRRect.pageData.name;
        //get image in origin size
        let origImage = await getImageObject(bookId, pageId);
        let origRatio = (origImage.width / img.width) * imgRatio;

        let canvas = this.$refs.canvas;
        if (canvas != undefined) {
          let canvasCtx = canvas.getContext("2d");
          let width;
          let height;
          let canvasWidth = this.$refs.canvasContainer.clientWidth;
          let canvasHeight = this.$refs.canvasContainer.clientHeight;
          canvas.width = canvasWidth;
          canvas.height = canvasHeight;

          if (cWidth > cHeight) {
            width = canvasWidth;
            height = cHeight * (canvasWidth / cWidth);
          } else {
            height = canvasHeight;
            width = cWidth * (canvasHeight / cHeight);
          }

          canvasCtx.clearRect(0, 0, canvasWidth, canvasHeight);

          canvasCtx.drawImage(
            img,
            startX * imgRatio,
            startY * imgRatio,
            cWidth * imgRatio,
            cHeight * imgRatio,
            0,
            0,
            width,
            height
          );
        }
        //this.tempCanvas.setAttribute("dir", "rtl");

        this.tempCanvas.width = cWidth * origRatio;
        this.tempCanvas.height = cHeight * origRatio + 50;
        this.tempCanvasCtx = this.tempCanvas.getContext("2d");
        this.tempCanvasCtx.clearRect(
          0,
          0,
          this.tempCanvas.width,
          this.tempCanvas.height
        );
        this.tempCanvasCtx.fillStyle = "white";
        this.tempCanvasCtx.fillRect(
          0,
          0,
          this.tempCanvas.width,
          this.tempCanvas.height
        );
        this.tempCanvasCtx.drawImage(
          origImage.picture,
          startX * origRatio,
          startY * origRatio,
          cWidth * origRatio,
          cHeight * origRatio,
          0,
          0,
          cWidth * origRatio,
          cHeight * origRatio
        );
        try {
          let footer =
            this.$store.getters[`tabs/${this.tabId}/getBookNameAndAuthor`] +
            " / " +
            this.$t("general.page") +
            " " +
            (this.OCRRect.pageData
              ? this.OCRRect.pageData.letter || this.OCRRect.pageData.position
              : this.OCRRect.pageData.position);

          let footer2 = this.$t(
            `bookPrint.${this.appMode.online() ? "printByWeb" : "printByOtzar"}`
          );
          this.tempCanvasCtx.textAlign = "right";
          this.tempCanvasCtx.fillStyle = "black";
          this.tempCanvasCtx.font = "12px David";
          this.tempCanvasCtx.fillText(
            footer,
            cWidth * origRatio - 10,
            cHeight * origRatio + 25
          );
          this.tempCanvasCtx.fillText(
            footer2,
            cWidth * origRatio - 10,
            cHeight * origRatio + 45
          );
        } catch (ex) {}
        let dataUrl = this.tempCanvas.toDataURL();

        return dataUrl;
      } catch (ex) {
        console.error(ex);
      }
    },
    copyImageToCB() {
      this.tempCanvas.toBlob(function(blob) {
        const item = new ClipboardItem({ "image/png": blob });
        navigator.clipboard.write([item]);
      });
      const notification = {
        type: "success",
        message: this.$t("bookSelectArea.copyImgMsg"),
        timeout: 4000,
      };
      this.$notify(notification);
    },
    copyImage() {
      this.tempCanvas.toBlob((blob) => {
        let anchor = document.createElement("a");
        anchor.download =
          this.getCurrentBook + "_" + this.OCRRect.pageData.position + ".jpg";
        anchor.href = URL.createObjectURL(blob);
        anchor.click();
        URL.revokeObjectURL(anchor.href);
      }, "image/jpg");
    },
    copyTxt() {
      var copyText = this.$refs.textArea;
      copyText.select();
      document.execCommand("copy");
      const notification = {
        type: "success",
        message: this.$t("bookSelectArea.copyTxtMsg"),
        timeout: 4000,
      };
      this.$notify(notification);
    },
    createMarker() {
      let payload = {
        bookId: this.getCurrentBook,
        pageId: this.OCRRect.pageData.id,
        x: this.markerRect.x,
        y: this.markerRect.y,
        width: this.markerRect.width,
        height: this.markerRect.height,
      };
      //this.$store.dispatch("userPersonalExtensions/addUserMarker", payload);
      this.$store.dispatch(`personalAdditionsTabs/addMarker`, {
        bookId: this.getCurrentBook,
        userMarker: payload,
      });
      //  this.closeResults();
      const notification = {
        type: "success",
        message: this.$t("personalExtensions.succeedMarker"),

        timeout: 2000,
        setting: {
          grid: "icon",
          nameIcon: "marker-small",
          position: "center",
        },
      };
      this.$notify(notification);
      this.showMarkersList();
    },
    onDragEnd(size) {
      this.setResizeToggle();
      this.toggleSplitter();
    },
    toggleDockable() {
      if (this.$_mobile) {
        this.openDockMobile = !this.openDockMobile;
      } else {
        this.openDock = !this.openDock;
        this.toggleSplitter();
      }
    },
    addExtenstions(type) {
      this.closeExtens(); //reset all before start

      this.setOCREnable(type == "marker" || this.ocrMode);

      this.setAddEtensType(type);

      this.startCreateExtentionMode();
    },
    openEditorPosition(e) {
      try {
        if (this.crtExtentionsMode) {
          this.offsetEditor.x = e.offsetX;
          this.offsetEditor.y = e.offsetY;
          if (!this.addingLink || this.addExtensType != "link")
            this.stopCreatModeAllExtantions();
        }
      } catch (ex) {
        console.error(ex);
      }
    },
    closeEditor() {
      this.closeEditorWin();
    },
    pageLetter(page) {
      return page.pagedata &&
        page.pagedata.length > 0 &&
        page.pagedata[0].letter != "" &&
        page.pagedata[0].letter != null
        ? page.pagedata[0].letter.replace(/יה/g, "טו").replace(/יו/g, "טז")
        : page.position;
    },
    goToFSPage(data) {
      this.$store.state.tabsManager.scrollToFsResult = true;
      this.currFsPage = data.id;

      this.goToPage(data);
    },
    goToPage(pageIndex) {
      this.go(pageIndex.page, this.tabId); // + 1);
    },

    goToActiveTsiyun(active) {
      this.currentTsiyunItem = active;
      let page = [...this.tsiyunimType2, ...this.tsiyunimType1][
        this.currentTsiyunItem
      ]; // this.filteredLists.tsiyunim[this.currentTsiyunItem];
      if (page) page = page.page;
      this.goToPage({
        page,
      });
    },
    goToActiveFSResult(active) {
      this.currentFSItem = active;
      let page = this.getFsResults[this.currentFSItem];
      this.goToFSPage({ page: page.position, id: page.id });
    },
    goToActivePage(active) {
      this.currentPageItem = active;
      let page = this.filteredLists.pages[this.currentPageItem];
      if (page) page = page.position;
      this.goToPage({
        page,
      });
    },

    goToTsiyun(tsiyunPage) {
      //let page = this.getPages.find(page => page.id == tsiyunPage.page);
      if (tsiyunPage == null) tsiyunPage = 1;
      this.goToPage({ page: tsiyunPage }); //{ page: page.position });
    },
    goToItemPage(item, type = null) {
      if (type === "link" && item.bookId != this.getCurrentBook) {
        return this.openInNewWin(item.bookId, item.position);
      }
      let page;

      //stupid fix
      if (item.pageId)
        page = this.getPages.find((page) => page.id == item.pageId);
      else page = this.getPages.find((page) => page.position == item.page);

      if (!page) return;
      this.go(page.position, this.tabId);
    },
    searchInList(index) {
      this.searchTxt[index] = event.target.value;
    },
    searchInListClear(index) {
      this.searchTxt[index] = "";
    },
    changeDock() {
      this.dock = this.dock == "dock" ? "float" : "dock";
    },
    setPartOpen(index, open) {
      if (!open && this.navigatePartsOpen.filter((elem) => elem).length <= 1)
        open = true;
      this.navigatePartsOpen.splice(index, 1, open);
    },
    setPartOpen1(index) {
      this.extensOpenIndex = index;
    },
    openStatus(index) {
      return this.navigatePartsOpen[index];
    },
    showDetailsWin() {
      // this.showDetails = show;
      this.$store.dispatch(
        "bookPopups/showBookDetailsVisible",
        this.getCurrentBook
      );
    },
    closeDetails() {
      this.showDetails = false;
    },
    showPrintWin(payLoad) {
      this.fitSizePrintWin(false);
      this.printFrom = payLoad.from || -1;
      this.printTo = payLoad.to || -1;

      this.showPrint = payLoad.show;
    },
    closePrintWin() {
      this.showPrint = false;
    },
    clear(input, index) {
      input.value = "";
      this.searchInListClear(index);
    },
    openMareMakom(catBook, catPage, CpageName) {
      if (!catPage || !catBook) return;
      let page = CpageName != undefined ? "PID_" + CpageName : "P" + catPage;
      this.openInNewWin(catBook, page, null, null);
    },
    changeFontFamily(font) {
      this.fontF = font;
    },
    changeFontSize(size) {
      this.fontS = size;
    },
    togglePin() {
      this.hasPin = !this.hasPin;
      this.$store.dispatch(`tabs/${this.tabId}/triggerResize`, null, {
        root: true,
      });
    },
    /*  down(event) {
      event.stopPropagation();

      event.preventDefault();

      let part = 0;
      let list = [];
      switch (part) {
        case 0:
          list = this.filteredLists.pages;
          break;
        case 1:
          list = this.filteredLists.tsiyunim;
          break;
        case 2:
          list = this.filteredLists.usertsiyuim;
          break;
        case 3:
          list = this.getFsResults;
          break;
      }
      if (this.activeItem[part] < list.length) {
        this.activeItem[part]++;
        let page = list[this.activeItem[part]];
        this.goToPage({ page: page.position });
      }
    },
    up(event) {
      event.stopPropagation();

      event.preventDefault();
      let part = 0;
      if (this.activeItem[part] > 1) this.activeItem[part]--;
    }, */
    closeUserExtens() {
      this.closeExtens();
      this.showUserAdditionsWin = false;
    },
    fitSizePrintWin(flag) {
      if (flag) {
        this.hPrint = 220;
      } else this.hPrint = 440;
    },
  },
  mounted() {
    this.tempCanvas = document.createElement("canvas");
    this.tempCanvasCtx = this.tempCanvas.getContext("2d");
    if (this.$_mobile) {
      this.openDockMobile = !this.openDock;
      // this.toggleSplitter();
      // this.defaultSplitterBook = 100;
      // this.defaultSplitterBookInfo = 0;
    }
  },
};

function isInViewport(element) {
  const rect = element.getBoundingClientRect();
  return (
    rect.top >= 0 &&
    rect.left >= 0 &&
    rect.bottom <=
      (window.innerHeight || document.documentElement.clientHeight) &&
    rect.right <= (window.innerWidth || document.documentElement.clientWidth)
  );
}
