// }

.arrow-icon {
  width: 17px;
  height: 100%;
  margin-right: 3px;

  &:hover:not(.disable) {
    background-color: transparent !important;
    color: #000;
  }
}
.text {
  height: 18px;
  line-height: 20px;
}
</style>
