let SERVER_MODE = "offline";

//only for development
globalThis.OTZAR_DEV_MODE = false;

//not recommend on development
globalThis.SERVER_MODE = SERVER_MODE;
globalThis.SERVER_MODE_USE_REAL_DOMAIN =
  globalThis.SERVER_MODE == "online" ? true : false;
//not recommend on development
globalThis.SET_DEFAULT = false;
//should we load the books
globalThis.DEV_LOAD_BOOKS = true;
//should we compress the books data
globalThis.DEV_COMPRESS = false;
//should we compress the hebrew code too (if yes then localstorage is used)
globalThis.DEV_COMPRESS_HEBREW = false;
//should we load system folders
globalThis.DEV_LOAD_SYSTEM_FOLDERS = true;
//should we create a index for books (its take about 3~ secs)
globalThis.DEV_CREATE_SEARCH_BOOKS_INDEX = true;

//dont change it unless you know what you doing
globalThis.DEV_NEW_VER = 1;

//compress mode (its run from exe file)
globalThis.DEV_COMPILE_MODE = false;

//these two go together
globalThis.DEV_CONNECT_USER_TO_FREE_AUTO = false;
globalThis.DEV_CONNECT_USER_TO_FREE_AUTO_LICENSE_CODE = 0b1;

globalThis.DEV_ALLOWED_PAGES_BASIC = 150;
globalThis.DEV_ALLOWED_PAGES_PACKAGE = 40;

//not recommend on development
globalThis.DEV_FOR_TEST_OR_REAL_PROD = true;

//change this to force cache deleting
globalThis.BOOKS_CACHE_VER = 1;

//change this to apply user subscriptions on online
globalThis.DEV_ONLINE_SUBS_ON = true;

if (globalThis.SET_DEFAULT) {
  setTo("DEV_COMPRESS", true);
  setTo("DEV_COMPRESS_HEBREW", true);
  setTo("DEV_LOAD_BOOKS", true);
  setTo("DEV_LOAD_SYSTEM_FOLDERS", true);
  setTo("DEV_CREATE_SEARCH_BOOKS_INDEX", true);
}

function setTo(settings, defaultSetting) {
  if (globalThis[settings] != defaultSetting) {
    globalThis[settings] = defaultSetting;
    // console.info(`*${settings} IGNORE BECAUSE SET_DEFAULT IS TRUE*`);
  }
}

//check for node or browser

//check if we need to clean all prev data
if (typeof window === "undefined") {
  //NODE ENV
  if (globalThis.DEV_VER < globalThis.DEV_NEW_VER) {
    const fs = require("fs");
    const path = require("path");
    const { promisify } = require("util");
    const exists = promisify(fs.exists);
    const directory = __dirname + "\\server\\data\\cache";
    exists(directory + "\\ver_" + globalThis.DEV_NEW_VER).then((ret) => {
      if (ret) return;
      if (globalThis.DEV_DEBUG)
        console.warn("****YOU CACHE IS NOT UPDATE. WE CLEAN IT NOW :-)****");
      if (!ret) {
        fs.readdir(directory, (err, files) => {
          if (err) throw err;

          for (const file of files) {
            fs.unlink(path.join(directory, file), (err) => {
              if (err) throw err;
            });
          }
        });
        fs.writeFile(
          directory + "\\ver_" + globalThis.DEV_NEW_VER,
          "",
          {},
          () => {}
        );
      }
    });
  }
} else {
  //BROWSER ENV
  // let dev_ver = localStorage.getItem("dev_ver");
  // if (!dev_ver || dev_ver < globalThis.DEV_NEW_VER) {
  //   const storageVer = localStorage.getItem("storageVersion");
  //   console.info("clear local storage");
  //   if (storageVer) localStorage.setItem("storageVersion", storageVer);
  //   localStorage.setItem("dev_ver", globalThis.DEV_NEW_VER);
  // }
}

//some warnings to show
if (!globalThis.DEV_COMPRESS_HEBREW && globalThis.DEV_DEBUG)
  console.warn("*NOT USING LOCALSTORAGE FOR BOOKS AND WORDALT*");

if (!globalThis.DEV_LOAD_SYSTEM_FOLDERS && globalThis.DEV_DEBUG)
  console.warn("*SYSTEM FOLDERS NOT LOADED*");

//logging errors
var path = null;
var moment = null;
var os = null;
var fsp = null;

if (!globalThis.LOG_ERROR)
  globalThis.LOG_ERROR = async (errorDesc) => {
    try {
      if (!path) path = require("path");
      if (!moment) moment = require("moment");
      if (!os) os = require("os");
      if (!fsp) fsp = require("./server/helpers/fsp");

      let logsDir = path.join(os.tmpdir(), "logs");
      if (!(await fsp.exists(logsDir))) await fsp.mkdir(logsDir);
      try {
        fsp.appendFile(
          path.join(logsDir, "out.log"),
          `[${moment().format(
            "DD/MM/YYYY h:mm"
          )} (server-side) ] : ${errorDesc}${os.EOL}`
        );
      } catch (ex) {}
    } catch (ex) {
      console.log(ex);
    }
  };

if (!globalThis.LOG_EXCEPTION)
  globalThis.LOG_EXCEPTION = async (exception) => {
    console.log("I am in global", exception);
    if (!path) path = require("path");
    if (!moment) moment = require("moment");
    if (!os) os = require("os");
    if (!fsp) fsp = require("./server/helpers/fsp");
    /*  let logsDir = path.join(globalThis.DEV_CURRENT_DRIVE, "logs");
  if (local) logsDir = path.join(globalThis.DEV_APP_FOLDER, "logs"); */

    let logsDir = path.join(os.tmpdir(), "logs");
    if (!(await fsp.exists(logsDir))) await fsp.mkdir(logsDir);

    try {
      fsp.appendFile(
        path.join(logsDir, "errors.log"),
        `[${moment().format(
          "DD/MM/YYYY h:mm"
        )} (server-side) ] : ${exception.stack || exception || "nothing"}${
          os.EOL
        }`
      );
    } catch (ex) {}
  };
// module.exports = { SERVER_MODE: SERVER_MODE };
