<template>
  <div class="main-setting-menu">
    <HeaderMenu
      :arrow="false"
      :title="$t('general.hello') + ' ' + $store.state.user.fullname + '!'"
    />
    <ul class="list-main-setting list-menu">
      <li class="item">
        <RowItem
          v-disable
          @click="showMyLicense"
          :title="$t('myProfile.myLicense')"
          :HasIconItem="false"
          :HasIconArrow="true"
          :HasIconDisplay="false"
          iconItem="8"
          :HasIconCheckbox="false"
          type="checkbox"
        />
        <router-view />
      </li>
      <li class="row-in-setting item">
        <RowItem
          v-disable
          @click="showHiddenList()"
          :title="$t('myProfile.personalArea')"
          :HasIconItem="false"
          :HasIconArrow="false"
          :HasIconDisplay="false"
          :HasIconCheckbox="false"
        />
        <router-view />
      </li>

      <li class="item">
        <RowItem
          v-disable
          @click="resetHideBooks()"
          @change="resetHideBooks"
          :title="$t('myProfile.passwordChange')"
          :HasIconItem="false"
          :HasIconArrow="true"
          :HasIconDisplay="false"
          :HasIconCheckbox="false"
        />
        <router-view />
      </li>
      <li class="item">
        <RowItem
          v-disable
          @click="log_out()"
          :title="$t('myProfile.logout')"
          :HasIconItem="false"
          :HasIconArrow="false"
          :HasIconDisplay="false"
          :HasIconCheckbox="false"
        />
        <router-view />
      </li>
    </ul>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { books } from "@/store/modules/books";
import { logoutUser } from "@/services/login";
import menuService from "@/services/menu";
export default {
  data() {
    return {
      routeBackName: "tools",
      commentsIcon: "fa fa-comment-o",
      keysIcon: "fa fa-key",
      markersIcon: "fa fa-paint-brush",
      linksIcon: "fa fa-link",
    };
  },
  computed: {
    hideMode() {
      const mode = this.userSettings.settings.enableHidden != "0";
      return mode;
    },
  },
  methods: {
    ...mapActions("user", ["logout"]),
    showHiddenList() {},
    showMyLicense() {
      menuService.openMyLicense();
    },
    async resetHideBooks() {},
    log_out() {
      logoutUser().then(async (data) => {
        this.logout(data);

        this.$notify({
          type: "success",
          message: this.$t("login.logoutSuccess"),
          timeout: 2000,
        });
        await this.$socket.client.emit("logout");
        //reload window
        window.location.reload();
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.main-setting-menu {
  width: -webkit-fill-available;
  z-index: 3;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.list-main-setting {
  display: flex;
  flex-direction: column;
  // margin: 0;
  // padding: 0;
  list-style-type: none;
}
</style>
