<template>
  <div @click="click()">
    <svg class="icon" :width="width" :height="height" :viewBox="viewBox">
      <use v-bind="{ href: '#icons-folder_icon-folder-' + nameIcon }" />
    </svg>
  </div>
</template>

<script>
export default {
  props: {
    nameIcon: {
      type: String,
      default: "normal-symbol",
    },
    viewBox: {
      type: [String],
    },
    width: {
      type: [Number, String],
      default: 66,
    },
    height: {
      type: [Number, String],
      default: 54,
    },
    iconColor: {
      type: String,
      default: "currentColor",
    },
  },
  methods: {
    click() {
      this.$emit("click");
    },
  },
};
</script>

<style lang="scss" scoped></style>
