async function showExpireMsg(data) {
  let act = await Vue.$msg({
    title: Vue.$t("general.attention"),
    content: Vue.$t("driverErrors.licWarning") + data,
    btns: [Vue.$t("general.confirm")],
  });
}

function readyUpdateBooksAndInxTask() {
  store.dispatch("bookListRefresh/setStatus", "ready", { root: true });
}
function startUpdateBooksAndInxTask() {
  store.dispatch("bookListRefresh/setStatus", "preparing", { root: true });
}
function goToErrorPage({ err, code }) {
  router.replace({
    name: "lic_error",
    params: { err, code },
  });
}
function goToForceClose({ err, code }) {
  router.replace({
    name: "kickout",
  });
}
function goToNoServer({ err, code }) {
  router.replace({
    name: "noserver",
  });
}
function reloadPage() {
  location.href = window.location.protocol + "\\\\" + window.location.host;
  // router.replace({
  //   name: "network",
  // });
}

function goToNoPlace({ err, code }) {
  router.replace({
    name: "no_place",
  });
}
