export const permissionsEnum = {
  GENERAL: { value: 1, description: "אוצר החכמה הבסיסי" },
  CHABAD: { value: 3, description: 'מהדורת חב"ד' },
  SCHOOL: { value: 2, description: 'מהדורת בי"ס' },
  OZ: { value: 9, description: "מכון עוז והדר" },
  KOOK: { value: 4, description: "מכון הרב קוק" },
  SHALOM: { value: 7, description: "מכון אהבת שלום" },
  MACHON: { value: 5, description: "מכון ירושלים" },
  SHLOMO: { value: 6, description: "מכון חכמת שלמה" },
  OFEK: { value: 8, description: "מכון אופק" },
  AHARON: { value: 13, description: "מכון זכרון אהרון" },
  FREE: { value: 11, description: "ספרים חינמיים" },
};

//is user allowed book
export function getBookPermission(bookId) {
  const bookOnly = store.state.bookOnlyMode;
  let bk;

  if (!bookOnly) {
    bk = store.getters["books/getBookbyId"](bookId);
    if (bk) {
      const bookPermissions = bk.permissions;
      //get permission descriptions
      let bookPermissionsData = [];
      Object.keys(permissionsEnum).forEach((p) => {
        let currPer = permissionsEnum[p].value;
        currPer = 1 << currPer;
        if (currPer & bookPermissions)
          bookPermissionsData.push(permissionsEnum[p]);
      });
      return { bookPermissions, bookPermissionsData };
    }
  }
  return { bookPermissions: 0, bookPermissionsData: [] };
}
