<template>
  <!-- <div class="extensPopups"> -->
  <!-- <vue-draggable-resizable
    :x="x"
    :y="y"
    :parent="true"
    :resizable="false"
    :w="1000"
    :h="593"
    drag-handle=".drag"
  > -->
  <!-- <BaseCleanPopup :w="1000" :h="593"> -->
  <popupUserExtensions
    @close="$emit('close')"
    :title="$t('personalExtensions.commentTitle')"
    name="comment"
    :list="geComments"
    :status="status"
    :listCheckedIds="listCheckedIds"
    icon="5"
    :total="totalRows"
    :limit="limit"
    :offset="offset"
    @inc="getRows"
    @search="doSearch"
  />
  <!-- </BaseCleanPopup> -->
  <!-- </vue-draggable-resizable> -->
  <!-- </div> -->
  <!-- <div v-for="comment in geComments" :key="comment.id">
      {{ comment.created }}
      {{ comment.data }}
  </div>-->
</template>

<script>
import popupUserExtensions from "@/components/popupUserExtensions/popupUserExtensions.vue";
import { mapGetters, mapActions } from "vuex";
import persPopup from "@/mixing/mixPersonalAddPopup";
export default {
  mixins: [persPopup],
  components: {
    popupUserExtensions,
  },
  data() {
    return { searchValue: "" };
  },
  //menuPersonalExtentions
  computed: {
    ...mapGetters("menuPersonalExtentions", [
      "getUserCommentsList",
      "getListCheckedIds",
    ]),
    x() {
      let wWindow = window.winSizes.innerWidth;
      let left = wWindow / 2 - 500;
      return left;
    },
    y() {
      let hWindow = window.winSizes.innerHeight;
      let top = hWindow / 2 - 296;
      return top;
    },
    geComments() {
      return this.getUserCommentsList;
    },
    listCheckedIds() {
      return this.getListCheckedIds;
    },
    totalRows() {
      if (this.getUserCommentsList.length == 0) return 0;
      return this.getUserCommentsList[0].totalRows;
    },
    status() {
      let comments = this.getUserCommentsList;

      if (comments.length == 0) return "";
      let commentsStatus =
        this.totalRows +
        " " +
        this.$t("personalExtensions.commentIn") +
        " " +
        comments[0].totalBooks +
        " " +
        this.$t("general.books") +
        " ";
      return commentsStatus;
    },
  },
  methods: {
    ...mapActions("userPersonalExtensions", ["loadUserCommentsList"]),
    doSearch(txt) {
      this.searchValue = txt;
      /* this.setOffset(0);
      this.loadUserCommentsList({
        offset: this.offset,
        limit: this.limit,
        txt,
      }); */
    },
    getNext() {
      this.incOffset(1);
      this.getRows();
    },
    getPrev() {
      this.incOffset(-1);
      this.getRows();
    },
    getRows(val) {
      if (val) this.incOffset(val);
      else this.setOffset(0);
      this.loadUserCommentsList({
        offset: this.offset,
        limit: this.limit,
        txt: this.searchValue,
      });
    },
  },
  created() {
    this.getRows();
  },
};
</script>

<style lang="scss" scoped>
.comments-pop {
  height: 100%;
  width: 100%;
  background-color: antiquewhite;
  position: absolute;
  z-index: 4;
}
</style>
