let func = null;
let directives = [
  {
    name: "focus",
    actions: {
      inserted: function(el, binds) {
        if (binds.value != true) el.focus();
      },
    },
  },
  {
    name: "select",
    actions: {
      inserted: function(el, binds) {
        el.select();

        if (binds.modifiers.focus) {
          func = () => {
            el.select();
          };

          el.addEventListener("focus", func);
        }
      },

      unbind: function(el, binds) {
        if (binds.modifiers.focus)
          if (func) el.removeEventListener("focus", func);
      },
    },
  },
];

export default directives;
