import VueInst from "@/main_app.js";
import store from "@/store/store";

function msg(Vue, msg) {
  const notification = {
    type: "error",
    message: msg,

    timeout: 2000,
    setting: {
      grid: "icon",
      nameIcon: "not-allowed",
      position: "center",
    },
  };
  Vue.notify(notification);
}

export default function(Vue) {
  Vue.directive("usertype", {
    inserted: function(el, binding, vnode) {
      if (
        globalThis.SERVER_MODE === "online" &&
        store.state.user.userType === "GUEST" &&
        binding.modifiers.guest
      ) {
        /** @type {HTMLElement} */
        let old_element = el;
        let new_element = old_element.cloneNode(true);
        old_element.parentNode.replaceChild(new_element, old_element);
        new_element.onclick = () =>
          msg(Vue, VueInst.$t("optionNotAvailableForGuest"));
        new_element.style.cursor = "default";
        new_element.style.color = "lightgrey";
      }
    },
    updated: function(el, binding, vnode) {
      if (
        globalThis.SERVER_MODE === "online" &&
        store.state.user.userType === "GUEST" &&
        binding.modifiers.guest
      ) {
        /** @type {HTMLElement} */
        let old_element = el;
        let new_element = old_element.cloneNode(true);
        old_element.parentNode.replaceChild(new_element, old_element);
        new_element.onclick = () =>
          msg(Vue, VueInst.$t("optionNotAvailableForGuest"));

        new_element.style.cursor = "default";
        new_element.style.color = "lightgrey";
      }
    },
  });
}
