import store from "@/store/store";
export default function auth({ to, from }) {
  if (!store.state.user.username) {
    
    //if free user so let log him

    let originalRef = from.refer ? from.refer : to;
    let routeData = { name: "login", params: { refer: originalRef } };
    return Promise.resolve({ error: 0, next: routeData });
  } else return Promise.resolve({ error: 0, next: null });
}
