    userBookDetails: "api/user/bookdetails",
    userReset: "api/user/factory-reset",
    recentBooks: "/recentbooks",
    favorites: "/favorites",
    users: "api/user",
    settings: "/settings",
    login: "/login",
    logout: "/logout",
    tsiyuns: "/tsiyuns",
    altWords: "/altWords",
    book: "/book",
    userLists: "api/userlists",
    quickbutton: "/quickbutton",
    desktops: "/desktops",
    freeuser: "/freeuser",
    recent: "/recent",
    history: "api/history",
    historyType: "/type",
    personalComments: "/comments",
    personalKeys: "/maftechot",
    personalLinks: "/links",
    personalMarks: "/marks",
    sendEmail: "api/mail",
