export let mobile = false;
if (
  /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
    navigator.userAgent
  )
) {
  mobile = true;
}

let plug = {};
window._mobile = mobile;
plug.install = function(Vue) {
  Vue._mobile = mobile;

  Vue.prototype.$_mobile = mobile;
};

export default plug;
