<template>
  <div>
    <div
      class="word-break align-center-div"
      style="text-align: center; padding: 15px;"
      v-if="!loading && warnPopupsNotAllowed"
    >
      <BaseIcon
        class="icon-header align-center"
        style="top: 0px;"
        width="40"
        height="40"
        nameIcon="not-allowed"
        pathIcon="icons-ganeral"
      />
      <h3>{{ $t("bookPrint.popupWarnTitle") }}</h3>
      <p>{{ $t("bookPrint.popupWarnTxt1") }}</p>
      <p>{{ $t("bookPrint.popupWarnTxt2") }}</p>
      <baseButton class="center btn" @click="warnPopupsNotAllowed = false"
        >הבנתי</baseButton
      >
    </div>
    <div v-else :class="{ loading: loading }">
      <div class="content-print content rtl" v-if="!loading">
        <div class="title mrg-b-9">{{ $t("bookPrint.printRang") }}</div>
        <div class="flex-row line-print">
          <baseCheckBox
            class="check-item check-larg"
            @change="choosePrintOnlyCurrent"
            size="larg"
            :checked="onlyCurrent"
          />
          <span>{{ $t("bookPrint.currentPage") }}</span>
        </div>
        <div class="flex-row line-print">
          <!-- <baseRadioButton class size="larg" name="print-win" @change="choosePrintRange" /> -->
          <baseCheckBox
            class="check-item check-larg"
            @change="choosePrintRange"
            size="larg"
            :checked="!onlyCurrent"
          />
          <span>{{ $t("bookPrint.from") }}</span>
          <input
            ref="from"
            type="text"
            v-model="fromPage"
            @focus="setPrintRange"
            class="a-center"
            @input="checkPages"
          />
          <span>{{ $t("bookPrint.to") }}</span>
          <input
            ref="true"
            type="text"
            class="a-center"
            v-model="toPage"
            @focus="setPrintRange"
            @input="checkPages"
          />
        </div>
        <div class="note1" v-if="false">{{ $t("bookPrint.note1") }}</div>

        <div class="line-print mrg-b-30 mrg-t-12">
          <div class="title mrg-b-9">{{ $t("bookPrint.titleTopPage") }}</div>
          <baseInput
            class="b-border full-w no-padd"
            :placeholder="$t('bookPrint.topTitlePlaceHolder')"
            @input="getHeader($event)"
            :text="initHeader"
          />
        </div>
        <div class="line-print">
          <div class="title mrg-b-9">{{ $t("bookPrint.comment") }}</div>
          <div class="flex-row rtl flex-align-center">
            <baseToggleSwitch
              @toggle="printComments($event.target.checked)"
              @click.self.stop
              :checked="wComments"
              class="left-icon"
            />
            <span>{{ $t("bookPrint.contentComment") }}</span>
          </div>
        </div>

        <div class="font-13 bold text-color mrg-t-12">{{ errorMessage }}</div>
        <div class="flex-column rtl flex-align-center" v-if="showPrintCodePart">
          <span>
            {{
              $t("bookPrint.allowedPrintsMsg") +
                " " +
                userPrintInfo.allowedPrints
            }}
          </span>
          <span>
            {{ $t("bookPrint.usePrintCodeMsg") }}
          </span>
          <div v-if="userPrintInfo.code">
            <span>
              {{ $t("bookPrint.printCodeMsg") + " " + userPrintInfo.code }}
            </span>

            <span>
              {{ $t("bookPrint.pagesLeft") + " " + userPrintInfo.pages }}
            </span>
          </div>
          <div class="flex-row rtl flex-align-center" v-else>
            <baseInput
              class="b-border full-w no-padd"
              :placeholder="$t('bookPrint.inputPrintCode')"
              @input="setPrintCode($event)"
              :text="printCode"
            />
            <div class="btn btn-outline" @click="getPagesLeft">
              {{ $t("bookPrint.showPagesLeft") }}
            </div>
            <span> {{ $t("general.or") }}:</span>
            <div class="btn btn-outline" @click="goToStore">
              {{ $t("bookPrint.purchasePrintCode") }}
            </div>
            <div
              class="flex-column rtl flex-align-center"
              v-if="printCode != ''"
            >
              כרטיסיית הדפסה {{ printCode }}
              <span v-if="printCodePages != -1">
                יתרה {{ printCodePages }}
              </span>
            </div>
          </div>
        </div>
      </div>
      <div class="btn-print">
        <div class="btn btn-outline" @click="closeWindow" v-if="!loading">
          {{ $t("bookPrint.cancel") }}
        </div>

        <div
          class="btn btn-outline"
          :class="{ disable: !allowedPDF }"
          @click="printPDF"
          v-if="!loading"
        >
          {{ $t("general.exportPdf") }}
        </div>

        <div
          id="printToPrinterBtn"
          class="btn"
          :class="{ disable1: !allowedPrint }"
          @click="printToPrinter"
          v-if="!loading"
        >
          {{ $t("bookPrint.btnTxt") }}
        </div>
        <div class="btn btn-outline" @click="cancelPrint" v-if="loading">
          {{ $t("bookPrint.cancel") }}
        </div>
        <!-- <BaseButton @click="cancelPrint" v-if="loading">{{
        $t("bookPrint.cancel")
      }}</BaseButton> -->
      </div>
      <div
        class="title mrg-b-9 a-center content-print content loading-print"
        v-if="loading"
      >
        {{
          $t("bookPrint.export") +
            (onlyCurrent || $store.state.progressWindow.currentPage === 0
              ? ""
              : " " +
                $store.state.progressWindow.currentPage +
                " " +
                $t("general.ofResults") +
                " " +
                $store.state.progressWindow.numPages)
        }}
      </div>
      <bounce-loader
        :loading="loading"
        :color="color"
        :size="size"
      ></bounce-loader>
    </div>
  </div>
</template>
<script>
import {
  printBook,
  stopPrinting,
  sendPrintSocket,
  getUserPrintInfo,
  getPrintCodeLeft,
} from "@/services/print.js";
import BounceLoader from "vue-spinner/src/BounceLoader.vue";
import { mapGetters, mapActions, mapState } from "vuex";
import { getBookName } from "@/services/bookData.js";
import { fetchBookByID } from "@/services/loadBooksDataFromServer.js";

const maxPagesToPDF = 20;
const maxToPDFOnline = 1; //TODO
const maxToOnline = 1; //TODO
export default {
  components: {
    BounceLoader,
  },
  props: ["pages", "show", "page", "book", "tabId", "drawFs", "from", "to"],
  data() {
    return {
      fromPage: this.page,
      toPage: this.page,
      onlyCurrent: true,
      loading: false,
      color: "var(--custom-color1)",
      size: "50px",
      header: "",
      wComments: false,
      errorMessage: "",
      allowedPDF: true,
      warnPopupsNotAllowed: false,
      userPrintInfo: {},
      printCode: "",
      printCodePages: -1,
    };
  },
  sockets: {
    finishedPrintBook: async function(data) {
      if (!data) {
        this.loading = false;
        this.$emit("loading", false);
        if (this.appMode.online())
          this.errorMessage = this.$t("bookPrint.printOverflow");
        return;
      }
      let res = await printBook(data);
      this.loading = false;
      this.$emit("loading", false);
      if (res == "popupsNotAllowed") {
        this.warnPopupsNotAllowed = true;
      } else {
        this.closeWindow();
      }
    },
  },
  computed: {
    ...mapGetters("books", ["getBookbyId"]),
    showPrintCodePart() {
      return this.appMode.online() && !globalThis.DEV_CONNECT_USER_TO_FREE_AUTO;
    },
    maxToPDF() {
      return globalThis.SERVER_MODE == "offline"
        ? maxPagesToPDF
        : maxToPDFOnline;
    },
    allowedPrint() {
      if (
        globalThis.SERVER_MODE == "online" &&
        this.toPage - this.fromPage >= maxToOnline
      ) {
        return false;
      }
      return true;
    },
    initHeader() {
      return this.userSettings.settings.userConfig.printHeader || "";
    },
    getFromPage() {
      return this.page;
    },
  },
  methods: {
    printComments(check) {
      this.wComments = check;
    },
    getHeader($event) {
      this.header = $event.target.value;
    },
    setPrintCode($event) {
      this.printCodePages = -1;
      this.printCode = $event.target.value;
    },
    getPagesLeft() {
      //get pages left of print code
      if (!this.printCode) return;
      getPrintCodeLeft(this.printCode).then(
        (data) => (this.printCodePages = data.pages)
      );
    },
    goToStore() {
      window.open("https://www.otzar.org/wotzar/pcionlinestore.aspx");
    },
    closeWindow() {
      this.$emit("print", { show: false });
      //this.togglePrintBookVisible();
    },
    inputFrom(event) {
      let from = parseInt(event.target.value);
      if (!from) return;
      if (!isNaN(from)) this.fromPage = from;
      else alert("enter a number");
    },
    inputTo(event) {
      let to = parseInt(event.target.value);
      if (!to) return;
      if (!isNaN(to)) this.toPage = to;
      else alert("enter a number");
    },
    validateNumber() {},
    cancelPrint() {
      this.loading = false;
      this.$emit("loading", false);
      stopPrinting();
      this.$emit("close");
    },
    async getBookFromServer(bookId) {
      let bookDetails = await fetchBookByID(bookId);

      if (bookDetails) {
        return {
          bookId: bookDetails.id,
          ...bookDetails,
          mainAuthorName:
            bookDetails.authors.length > 0 ? bookDetails.authors[0].name : "",
        };
      } else return false;
    },
    async printToPrinter() {
      if (!this.allowedPrint) return this.checkPages();
      this.warnPopupsNotAllowed = false;
      const bookOnlyMode = this.$store.state.bookOnlyMode;
      this.errorMessage = "";
      let book = this.book;
      let fPage = this.fromPage;
      let tPage = this.toPage;
      if (this.onlyCurrent) {
        fPage = this.page;
        tPage = this.page;
      } else {
        fPage = this.fromPage;
        tPage = this.toPage;
      }

      if (fPage > this.pages.length) fPage = this.pages.length;
      if (tPage > this.pages.length) tPage = this.pages.length;

      let bookb = null;
      if (bookOnlyMode) {
        bookb = await this.getBookFromServer(book);
      } else bookb = this.getBookbyId(book);
      if (!bookb) return;
      let Name = getBookName(bookb.name, bookb.volume, true);
      let author = bookb.mainAuthorName;
      if (author != "") Name += " / " + author;
      this.loading = true;
      this.$emit("loading", true);
      await sendPrintSocket(
        //await printBook(
        book,
        fPage,
        tPage,
        this.pages,
        Name,
        this.header,
        this.wComments,
        this.tabId,
        false,
        this.$socket.client,
        this.drawFs,
        this.printCode
      );
    },
    checkPages() {
      if (globalThis.SERVER_MODE == "offline")
        if (Math.abs(this.toPage - this.fromPage) >= this.maxToPDF)
          this.allowedPDF = false;
        else {
          this.allowedPDF = true;
          this.errorMessage = "";
        }

      if (globalThis.SERVER_MODE == "online")
        if (this.toPage - this.fromPage >= maxToOnline) {
          this.allowedPDF = false;
          this.errorMessage = this.$t("bookPrint.maxToOnline").replace(
            "%NUM%",
            maxToOnline
          );
        } else {
          this.errorMessage = "";
          this.allowedPDF = true;
        }
    },
    async printPDF() {
      if (!this.allowedPDF) return;
      const bookOnlyMode = this.$store.state.bookOnlyMode;
      this.errorMessage = "";
      let book = this.book;
      let fPage = this.fromPage;
      let tPage = this.toPage;
      if (this.onlyCurrent) {
        fPage = this.page;
        tPage = this.page;
      } else {
        fPage = this.fromPage;
        tPage = this.toPage;
      }

      if (fPage > this.pages.length) fPage = this.pages.length;
      if (tPage > this.pages.length) tPage = this.pages.length;

      let bookb = null;
      if (bookOnlyMode) {
        bookb = await this.getBookFromServer(book);
      } else bookb = this.getBookbyId(book);
      if (!bookb) return;
      if (this.appMode.offline() && Math.abs(tPage - fPage) >= this.maxToPDF) {
        /* this.$notify({
          type: "error",
          message: this.$t("bookPrint.PDFLimited").replace("%NUM%", this.maxToPDF),
          timeout: 4000,
        }); */
        this.errorMessage = this.$t("bookPrint.PDFLimited").replace(
          "%NUM%",
          this.maxToPDF
        );
        return;
        tPage = fPage + this.maxToPDF - 1;
      }
      let Name = getBookName(bookb.name, bookb.volume, true);
      let author = bookb.mainAuthorName;
      if (author != "") Name += " / " + author;
      this.loading = true;
      this.$emit("loading", true);
      await sendPrintSocket(
        // await printBook(
        book,
        fPage,
        tPage,
        this.pages,
        Name,
        this.header,
        this.wComments,
        this.tabId,
        true,
        this.$socket.client,
        this.drawFs,
        this.printCode
      );
    },
    choosePrintOnlyCurrent(e) {
      if (e.target.checked) this.allowedPDF = true;
      this.onlyCurrent = e.target.checked;
    },
    choosePrintRange(e) {
      if (e.target.checked) this.checkPages();
      this.onlyCurrent = !e.target.checked;
    },
    setPrintRange() {
      this.onlyCurrent = false;
    },
  },
  mounted() {
    this.allowedPDF = true;
    this.errorMessage = "";

    if (this.from > 0 && this.to > 0) {
      this.fromPage = this.from;
      this.toPage = this.to;
      this.checkPages();
      this.onlyCurrent = false;
    } else {
      this.fromPage = this.page;
      this.toPage = this.page;
    }
    this.header = this.userSettings.settings.userConfig.printHeader;

    //get print info of user for online only
    if (this.appMode.online()) {
      getUserPrintInfo(this.book).then((data) => (this.userPrintInfo = data));
    }
  },
};
</script>
<style lang="scss">
$bg-color2: #f7f7f7;
$bg-color5: #f4f4f4;
