  font-size: 12px;
  font-weight: bold;
  // margin-bottom: 10px;
}
.full-w input {
  width: 100% !important;
}
.print-book {
  z-index: 9999 !important;
}
.print-book-container {
  // .header {
  //   width: 100%;
  //   height: 18px;
  //   line-height: 18px;
  //   position: relative;
  //   padding: 0 9px;
  //   color: #b2b2b2;
  //   font-weight: bold;
  //   margin-bottom: 15px;
  //   // background-color: #f1f1f1;
  // }
  // .header span {
  //   // padding-top: 20px;
  //   // padding-right: 20px;
  //   font-size: 12px;
  // }
}

.icon-del {
  // height: 6px;
  // position: absolute;
  // top: 0;
  // left: 8px;
  // width: 6px;
  svg {
    height: 6px;
    stroke: rgba($color: #000000, $alpha: 0.3);
    width: 6px;
  }
}
.btn-print {
  display: flex;
  justify-content: center;
  background-color: $bg-color5;
  padding: 8px 0;
  height: 41px;
  position: absolute;
  bottom: 0;
  width: 100%;
}
.print-book-container .title {
  // padding: 0 25.5px;
  color: #b2b2b2;
  font-weight: 600;
  margin-top: 20px;
}
.line-print {
  margin-top: 40px;
}

.content-print {
  padding: 33px 15px 0 15px;
  input {
    padding: 0;
