    align-items: center;
    .check-larg label {
      top: 0;
      transform: translate(0);
    }
  }
}
.line-print input {
  width: 31px;
  height: 15px;
  // text-align: center;
  // background: $bg-color2;
  border: 1px solid #dedede;
  margin: 0 3px;
  border: 0;
  border-bottom: 1px solid #b2b2b2;
  border-radius: 0;
