    fill: var(--custom-color1);
    stroke: var(--custom-color1);
    color: var(--custom-color1);
  }
}
.line-print {
  .switch {
    margin: 7px 0 0 5px;
  }
}
.disable1 {
  background-color: var(--custom-color1) !important;
  opacity: 0.3;
